import Grid from "@material-ui/core/Grid";
import HomeHeader from "../../components/HomeHeader/HomeHeader";
import classes from "./Administration.module.css";
import { Button, Container } from "@material-ui/core";
import Divider from "../../components/Divider/Divider";
import Footer from "../../components/Footer/Footer";
import BoxWithTitleAndWhiteBack from "../../components/BoxWithTitle2/BoxWithTitleAndWhiteBack";
import { Link } from 'react-router-dom'
import { useEffect } from "react";



function Administration() {
  useEffect(()=>{
    window.scrollTo(0,0);
},[])
  return (
    <>
      <HomeHeader  />
      
      
      <div className={classes.checkAccount}>
        <Container>
         
          <Divider padding="30px" />
          <BoxWithTitleAndWhiteBack title="">
          <Grid container spacing={1}>
            
            <Grid item xs={12} md={6} sm={6}>
            <Link to="/AdminAcceptStudent"><button className={classes.link}><span>Accept Student</span></button></Link>
            </Grid>
            <Grid item xs={12} md={6} sm={6}>
            <Link to="/AdminAcceptEnrolment"><button className={classes.link}><span>Accept Enrolment</span></button></Link>
            </Grid>
            <Grid item xs={12} md={6} sm={6}>
            <Link to="/SubscribeSemester"><button className={classes.link}><span>Subscribe Semester</span></button></Link>
            </Grid>
           
          </Grid>
          </BoxWithTitleAndWhiteBack>
          <Divider padding="20px" />
        </Container>
      </div>
      <Footer />
    </>
  );
}

export default Administration;
