import * as actionTypes from '../actions/actions';



const initialState = {
    dueAmount: '',
    majorName: '',
    campus: '',
    credits: '',
    gpa: '',
    probation:'',
    semester: '',
    passedCrd: ''
    
}




const StudentPortalR =  (state = initialState, action) => {

    switch (action.type) {
        case actionTypes.StudentPortal.GET_DUE_AMOUNT :
            return { ...state,dueAmount: action.adata.amount +' '+action.adata.currency  }
        
        case actionTypes.StudentPortal.GET_MAJOR :
            return { ...state,majorName: action.mdata}

        case actionTypes.StudentPortal.GET_CAMPUS :
            return { ...state,campus: action.cdata.name} 
            
        case actionTypes.StudentPortal.GET_SEMESTER :
            return { ...state,semester: action.sdata}    
            
        case actionTypes.StudentPortal.GET_CREDITS :
            return { ...state,credits: action.cdata.remainingCrd, passedCrd: action.cdata.passedCrd}     

        case actionTypes.StudentPortal.GET_GPA_SP :
            return { ...state,gpa: action.gdata.gpa} 

        case actionTypes.StudentPortal.GET_PROBATION :
            return { ...state,probation: action.pdata} 
        default:
            return state
    }
}

export default StudentPortalR;