import React from 'react'
import classes from './ReceiptRow.module.css'
const ReceiptRow = ({Date, Ref, CrDr, Amount}) => {
    return (
        <div className={classes.ReceiptRow}>
            <div>
                {Date}
            </div>
            <div>
                {Ref}
            </div>
            <div>
                {CrDr}
            </div>
            <div>
                {Amount}
            </div>
        </div>
    )
}

export default ReceiptRow
