
import * as actionTypes from '../actions/actions';



const initialState = {
    email: '',
    password: '',
    loading: false,
    group: '',
    userId: '',
    status: '',
    error: '',
    errorShow: false,
    application: '',
    applicant: '',
    major: '',
    name: ''
    
}




const LoginR =  (state = initialState, action) => {

    
    
    switch (action.type) {
        case actionTypes.Login.LOGIN :
            return { ...state, [action.name] : action.value  }
        
        case actionTypes.Login.LOGIN_START :
            return { ...state,loading: true}

        case actionTypes.Login.LOGIN_END :
            return { ...state,userId: action.userData.userId,group: action.userData.group,status: action.userData.status,errorShow: false,name: action.userData.name} 
            
        case actionTypes.Login.LOGIN_FAIL :
            return { ...state,loading: false,error: action.error,errorShow: true}                    

        case actionTypes.Login.LOGIN_GET_APP_DATA :
            return { ...state,applicant: action.dataApp.applicant,application: action.dataApp.application,major: action.dataApp.majors !== null ? action.dataApp.majors[0]:"",loading: false} 
            
        case actionTypes.Login.LOGIN_GET_APP_DATA_ERROR :
            return { ...state,loading: false,error: action.error,errorShow: true} 
        default:
            return state
    }
}

export default LoginR;
