import * as actionTypes from '../actions/actions';

const initialState = {
    citizenshipData: [],
    maritalStatusData: [],
    primaryLanguagesData: [],
    firstname: '',
    middlename: '',
    lastname: '',
    dob: '',
    citizenship: '',
    phone: '',
    primarylanguage: '',
    landline: '',
    email: '',
    caddress: '',
    maritalstatus: '',
    gender: '',
    passportnumber: '',
    passportfile: '',
    userId: '',
    status:'',
    error: '',
    loading: false,
    
}

const CreateNUidR =  (state = initialState, action) => {
    switch (action.type) {

        case actionTypes.CreateNUid.GET_USER_INFO :
            return { ...state, [action.name] : action.value  }

        case actionTypes.CreateNUid.CREATENUID_START :
            return { ...state,loading: true}
    
        case actionTypes.CreateNUid.CREATENUID_END :
            return { ...state,loading: false,userId: action.userData.userId,status: action.userData.status} 
                
        case actionTypes.CreateNUid.CREATENUID_FAIL :
            return { ...state,loading: false,error: action.error}
            
        case actionTypes.CreateNUid.CREATENUID_CITIZENSHIPDATA :
            return { ...state,loading: false,citizenshipData: action.ctshdata}  
            
        case actionTypes.CreateNUid.CREATENUID_PRIMARYLANGUAGEDATA :
            return { ...state,loading: false,primaryLanguagesData: action.ctshdata} 

        case actionTypes.CreateNUid.CREATENUID_MARITALSTATUSDATA :
            return { ...state,loading: false,maritalStatusData: action.msdata}   
        default:
            return state
    }
}

export default CreateNUidR;
