 import PaymentStudentInfo from '../paymentStudentInfo/PaymentStudentInfo';
 import CardImageSrc from '../../../assets/images/payment card-1-01.png';
 import PaymentForm from '../PaymentForm/PaymentForm';
 import * as paymentAction from '../../../store/actionsCreators/PaymentAction';
import classes from './ContainerMain.module.css';
import { connect } from 'react-redux';
import React, {useEffect} from 'react';
import { Backdrop, makeStyles } from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
    backdrop: {
      zIndex: theme.zIndex.drawer + 1,
      color: '#fff',
    },
  }));

const ContainerMain = ({submitPayment,amount,studentInfo,nameOnCard,cardNumber,expiryDate,cvv,loading,error,getPaymentInfo,getData,result}) => {

    const classesMakeStyle = useStyles();

    const [openBackdrop, setOpenBackdrop] = React.useState(false);

    useEffect(() => {
        if(result !== ''){
        setOpenBackdrop(true);
        }
      }, [result]);
  
      const handleClose = () =>{
        setOpenBackdrop(false);
    }

    const handleOnchange = (e) =>{
        getPaymentInfo(e.target.name, e.target.value);
    }


    useEffect(() => {
        getData(localStorage.getItem('userId'),localStorage.getItem('major'));
    },[])


    const submitForm = (e) =>{
        submitPayment(nameOnCard,localStorage.getItem('userId'),localStorage.getItem('major'),cardNumber,expiryDate,cvv,amount)
    }

    return (
        <div className={classes.container}>
            <Backdrop className={classesMakeStyle.backdrop} open={openBackdrop} onClick={handleClose}>
                <div style={{ color: '#fff' }}>
                    {
                        
                        <div className={classes.textBoxApplication}>
                            {
                             result === "Success" ? <p>Payment successfully done.</p>:<p>Somthing wrong</p>
                            }
                         </div>

                    }
                </div>    
        </Backdrop>
            <div className={classes.CardImage}>
                    <img src={CardImageSrc} alt="" />
                </div>
            <div className={classes.paymentInfo}>
            <PaymentStudentInfo personalInfo={ studentInfo }/>
            <PaymentForm nameOnCard={ nameOnCard } cardNumber={ cardNumber } expiryDate={ expiryDate } cvv={ cvv } amount={amount} handleOnchange={ handleOnchange} submitForm={submitForm} loading={loading}/>
            </div>
        </div>
    );
}

const mapStateToProps = (state) =>{
    return{
        studentInfo: state.PaymentR.studentInfo,
        nameOnCard: state.PaymentR.nameOnCard,
        cardNumber: state.PaymentR.cardNumber,
        expiryDate: state.PaymentR.expiryDate,
        amount: state.PaymentR.amount,
        cvv: state.PaymentR.cvv,
        loading: state.PaymentR.loading,
        error: state.PaymentR.error,
        result: state.PaymentR.result

    };
}

const mapDispatchToProps = (dispatch) =>{
    return{
        getPaymentInfo : (name, value) => dispatch(paymentAction.getPaymentInfo(name, value)), 
        submitPayment: (nameOnCard,userId,major,cardNumber,expiryDate,cvv,amount)=> dispatch(paymentAction.submitPayment(nameOnCard,userId,major,cardNumber,expiryDate,cvv,amount)),
        getData: (userid,major)=> dispatch(paymentAction.getData(userid,major))
    };
}
 
export default connect(mapStateToProps, mapDispatchToProps)(ContainerMain);