import GreyBoxithTitle from '../../../components/GreyBoxWithTitle/GreyBoxWithTitle'
import classes from './WelcomeBox.module.css'


const WelcomeBox = ({WelcomeMessage}) => {
    return ( 
        <div>
             <GreyBoxithTitle Title='Welcome,' /> 
                <div className={classes.welcomeText}>
                   <p> {WelcomeMessage}</p>
                </div>
        </div>

    );}

export default WelcomeBox