import React from "react";
import CoursesToDropRow from "./CoursesToDropRow/CoursesToDropRow";
import classes from "./CoursesToDropTable.module.css";

const CoursesToDropTable = ({courses,dropList}) => {

  // const CoursesToDrop = [
  //   {
  //     id: "34213",
  //     name: "course-1",
  //   },
  //   {
  //     id: "34213",
  //     name: "course-1",
  //   },
  //   {
  //     id: "34213",
  //     name: "course-1",
  //   },
  // ]; 

  const CoursesToDropList = courses.map((receipt, index) => {
    return (
      <CoursesToDropRow
      CourseId={receipt.code}
      CourseTitle={receipt.title}
      checkValue={receipt.enrolmentid}
      listToDrop={dropList}
        key={index}
      />
    );
  });

  return (
    <>
      <div className={classes.CoursesToDrop}>
        <div className={classes.Title}>
          <div>Course Id</div>
          <div>Course Title</div>
          <div>Drop</div>
        </div>
        {CoursesToDropList}
      </div>
    </>
  );
};

export default CoursesToDropTable;
