import Grid from "@material-ui/core/Grid";
import HomeHeader from "../../components/HomeHeader/HomeHeader";
import classes from "./SubscribeSemester.module.css";
import {Container, makeStyles } from "@material-ui/core";
import BoxWithTitle from "../../components/BoxWithTitle/BoxWithTitle";
import Footer from "../../components/Footer/Footer";
import * as AcceptEnrolmentActionCreators from '../../store/actionsCreators/SubscribeSemesterAction';
import { connect } from "react-redux";
import React, { useEffect } from "react";
import * as Controls from '../../UI/Controls/Controls';
import Backdrop from '@material-ui/core/Backdrop';

const useStyles = makeStyles((theme) => ({
    backdrop: {
      zIndex: theme.zIndex.drawer + 1,
      color: '#fff',
    },
  }));

function SubscribeSemester({student,semester,semesters,year,getAllSemester,getInformationAcceptData,result,studentSubscribe}) {
    const classesMakeStyle = useStyles();

    const [openBackdrop, setOpenBackdrop] = React.useState(false);
  useEffect(()=>{
    window.scrollTo(0,0);
},[])


useEffect(() => {
    if(result !== ''){
    setOpenBackdrop(true);
    }
  }, [result]);

  const handleClose = () =>{
    setOpenBackdrop(false);
}

  useEffect(() => {
    getAllSemester();
 
  }, []);

 

const changeValue = (e) =>{
    getInformationAcceptData(e.target.name,e.target.value)
}

const subscribeSemester = ()=>{
    studentSubscribe(student,semester,year);
}

const yearData=[2020,2021,2022,2023];

  return (
    <>
    <Backdrop className={classesMakeStyle.backdrop} open={openBackdrop} onClick={handleClose}>
                <div style={{ color: '#fff' }}>
                    {
                        
                        <div className={classes.textBoxApplication}>
                            {
                             result
                            }
                         </div>

                    }
                </div>    
        </Backdrop>
      <HomeHeader/>
      {/* <LogedInHeader /> */}
      <div className={classes.divSpace}></div>
      <div className={classes.SubscribeSemesterContent}>
        <Container>
          <BoxWithTitle title="Suscribe Semester">
            <Grid container spacing={3}>
              
            <div className={classes.popupDisplay}>
            <div className={classes.selectYearSemester}>
                    <Controls.Input name='student' id='student' label='Student Id' value={ student} handleOnchange={ changeValue} required={true}  />
                    </div>
                    <div className={classes.selectYearSemester}>
                    <Controls.SelectInput name='year' id='year' label="Year" value={year} optionData={yearData} handleOnchange={changeValue} required={false}  ></Controls.SelectInput> 
                    </div>
                    <div className={classes.selectYearSemester}>
                    <Controls.SelectInput name='semester' id='semester' label="Semester" value={semester} optionData={semesters} handleOnchange={changeValue} required={false}  ></Controls.SelectInput>
                    </div>
                    <div className={classes.selectYearSemester}>
                    <button onClick={subscribeSemester} className={classes.acceptButton}>Subscribe</button>
                    </div>
                    </div> 

               
              <Grid item xs={12} md={3} sm={11}>
              </Grid>
              <Grid item xs={12} md={3} sm={1}>
              </Grid>
            </Grid>
          </BoxWithTitle>
        </Container>
      </div>
      <Footer />
    </>
  );
}

const mapStateToProps = (state) =>{
  return{
    semesters: state.SubscribeSemesterR.semesters,
    semester: state.SubscribeSemesterR.semester,
    student: state.SubscribeSemesterR.student,
    year: state.SubscribeSemesterR.year,
    result: state.SubscribeSemesterR.result,

    
    
  };
}

const mapDispatchToProps = (dispatch) =>{
  return{
    getInformationAcceptData : (name,value) => dispatch(AcceptEnrolmentActionCreators.getInformationAcceptData(name,value)), 
    getAllSemester: () => dispatch(AcceptEnrolmentActionCreators.getAllSemester()),
    studentSubscribe: (student,semester,year) => dispatch(AcceptEnrolmentActionCreators.studentSubscribe(student,semester,year)),
  
    };
}

export default connect(mapStateToProps, mapDispatchToProps)(SubscribeSemester);
