import Grid from "@material-ui/core/Grid";
import HomeHeader from "../../components/HomeHeader/HomeHeader";
import LogedInHeader from "../../components/LogedInHeader/LogedInHeader";
import classes from "./StudentMainPage.module.css";
import { Button, Container } from "@material-ui/core";
import DueAmount from "../../components/StudentPortalComponents/DueAmount/DueAmount";
import Divider from "../../components/Divider/Divider";
import img1 from "../../assets/images/HP_Banner.png";
import Footer from "../../components/Footer/Footer";
import * as Controls from "../../UI/Controls/Controls";
import BoxWithTitleAndWhiteBack from "../../components/BoxWithTitle2/BoxWithTitleAndWhiteBack";
import { Link } from 'react-router-dom'
import image from "../../assets/images/Probation.png";
import { useEffect } from "react";



function StudentMainPage() {
  useEffect(()=>{
    window.scrollTo(0,0);
},[])
  return (
    <>
      <HomeHeader LogedIn={true} />
      <LogedInHeader />
      
      <div className={classes.checkAccount}>
        <Container>
          <img src={img1} />
          <Divider padding="30px" />
          <BoxWithTitleAndWhiteBack title="">
          <Grid container spacing={1}>
            
            <Grid item xs={12} md={6} sm={6}>
            <Link to="/CalendarPage"><button className={classes.link}><span>Calendar</span></button></Link>
            </Grid>
            <Grid item xs={12} md={6} sm={6}>
            <Link to="/PlanOfStudy"><button className={classes.link}><span>Plan Of Study</span></button></Link>
            </Grid>
            <Grid item xs={12} md={6} sm={6}>
            <Link to="/CheckAccount"><button className={classes.link}><span>Check Account</span></button></Link>
            </Grid>
            <Grid item xs={12} md={6} sm={6}>
            <Link to="/CurrentSemester"><button className={classes.link}><span>Current Semester</span></button></Link>
            </Grid>
            <Grid item xs={12} md={6} sm={6}>
            <Link to="/History"><button className={classes.link}><span>History</span></button></Link>
            </Grid>
            <Grid item xs={12} md={6} sm={6}>
            <Link to="/Payment"><button className={classes.link}><span>Payment</span></button></Link>
            </Grid>
            <Grid item xs={12} md={6} sm={6}>
            <Link to="/ResetPassword"><button className={classes.link}><span>Reset Password</span></button></Link>
            </Grid>         
            <Grid item xs={12} md={6} sm={6}>
            <Link to="/UpdateNUid"><button className={classes.link}><span>Update personal Information</span></button></Link>
            </Grid>
            {/* <Grid item xs={12} md={6} sm={6}>
            <div className={classes.probation}><img src={image} />No Probation</div>
            </Grid> */}
          </Grid>
          </BoxWithTitleAndWhiteBack>
          <Divider padding="20px" />
        </Container>
      </div>
      <Footer />
    </>
  );
}

export default StudentMainPage;
