import React from 'react'
import CourseRow from './CourseRow/CourseRow'
import Backdrop from '@material-ui/core/Backdrop';
import classes from './CoursesTable.module.css'
import { makeStyles } from '@material-ui/core/styles';
import {useEffect} from 'react'
import { Paper, Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from '@material-ui/core'

const useStyles = makeStyles((theme) => ({
  backdrop: {
    zIndex: theme.zIndex.drawer + 1,
    color: '#fff',
  },
}));

const CoursesTable = ({courses}) => {
    const classesMakeStyle = useStyles();

    const [openBackdrop, setOpenBackdrop] = React.useState(false);
    const [selectedCourse, setSelectedCourse] = React.useState('');
    const [courseTitle , setCourseTitle] = React.useState('');
    const [courseCredit , setCourseCredit] = React.useState('');
    const [courseDescription , setCourseDescription] = React.useState('');


    // const Courses = [{name:'course - 1', credit: 3}, {name:'course - 1', credit: 3}, {name:'course - 1', credit: 3}, {name:'course - 1', credit: 3}, {name:'course - 1', credit: 3}, {name:'course - 1', credit: 3}, {name:'course - 1', credit: 3}, {name:'course - 1', credit: 3}]
    
    const handleShowInfoCourse = (courseId,courseTitle,courseCredit,courseDescription) =>{
        // console.log(courseName);
        setOpenBackdrop(true);
        setSelectedCourse(courseId);
        setCourseTitle(courseTitle);
        setCourseCredit(courseCredit)
        setCourseDescription(courseDescription);

    }

    const handleClose = () =>{
        setOpenBackdrop(false);
    }
    
    function createData(name, total) {
        return { name,  total };
    }
    
    const CoursesList = courses.map((course, index)=>{
        return (
            <CourseRow CourseName={course.iid} CreditNumber={course.credit} key={index} clicked={()=>handleShowInfoCourse(course.iid,course.title,course.credit,course.description)} />
        );
    })

    const rows = [
        // createData('General', 6, 6, 6, 18),
        // createData('Major', 12, 14, 14, 40),
        // createData('Core', 10, 12, 10, 32),
        // createData('Elective', 8, 4, 6, 18),
        // createData('Total', 36, 36, 36, 108),
        createData('Course Code',  selectedCourse),
        createData('Course Title',  courseTitle),
        createData('Course Credits',  courseCredit),
        createData('Course Description',  courseDescription)
        
    ]

    return (
        <>
            <Backdrop className={classesMakeStyle.backdrop} open={openBackdrop} onClick={handleClose}>
                <div style={{ color: '#fff' ,width: '550px' }}>
                    {
                    // <p>Course Code: {selectedCourse} <br/><br/>
                    // Course Title: {courseTitle}<br/><br/>
                    // Course Credits: {courseCredit}<br/><br/>
                    // Course Description: {courseDescription}</p>

                    }

                <TableContainer component={Paper}>
                        <Table className={classes.table} aria-label="customized table">
                            <TableHead>
                                <TableRow>
                                   
                                </TableRow>
                            </TableHead>
                            <TableBody>
                                {rows.map((row) => (
                                    <TableRow key={row.name} >
                                         <TableCell component="th" scope="row">
                                             {row.name}
                                         </TableCell>
                                         {/* <TableCell align="center" style={{ fontWeight: '800' }}>{row.name}</TableCell> */}
                                         {/* <TableCell align="center" style={{ fontWeight: '800' }}>{row.second}</TableCell>
                                         <TableCell align="center" style={{ fontWeight: '800' }}>{row.third}</TableCell> */}
                                         <TableCell align="left" style={{ fontWeight: '800',alignContent:'left' }}>{row.total}</TableCell>
                                     </TableRow>
                                ))}
                            </TableBody>
                        </Table>
                    </TableContainer>
                </div>    
            </Backdrop>
            <div className={classes.CoursesTable}>
                <div className={classes.Title}>
                    <div>Name</div>
                    <div>Credits</div>
                </div>
                {
                    CoursesList 
                }
            </div>
        </>
    )
}

export default CoursesTable
