import axios from 'axios';
import * as actionTypes from '../actions/actions';
import {digid , lms} from './Constants'
export const getSPInfo = (name, value) => {
    return{
        type: actionTypes.SetPassword.GET_USER_INFO,
        name: name,
        value: value
    }
}

export const onLoadSetPassword = (token) => {
    
    return{
        type: actionTypes.SetPassword.GET_ONLOADSP_INFO,
        data: token
    }
}

export const getUserByToken = (token) => {
    return dispatch =>{
        
        
        var link = digid+'/nuversity/api/digid/userByToken/'+token;
       axios.get(link)
            .then(res=>{
                
                dispatch(getUserData(res.data));
                
                        
            }).catch(err => {
                console.log(err)
            })
            
}
}

export const setPassword = (userId,password,confirmPassword,token) => {
    return dispatch =>{
        dispatch(setPasswordStart());
        const params = {"userId":userId,
                        "password":password,
                        "confirmPassword":confirmPassword,
                        "token":token}
        
            axios.post(digid+"/nuversity/api/digid/setPassword",params,{ headers: { "Content-Type": "application/json", }, })

                .then(res=>{
                    
                    dispatch(setPasswordEnd(res.data));
                         
                         
                        }).catch(err => {
                            dispatch(setPasswordFail(err));
                        })                                                                                                                                        
                }
}

export const setPasswordStart = () => {

    return{
        type: actionTypes.SetPassword.SETPASSWORD_START
    }

}

export const setPasswordEnd = (data) => {

    return{
        type: actionTypes.SetPassword.SETPASSWORD_END,
        userData: data
    }
}

export const setPasswordFail = (err) => {

    return{
        type: actionTypes.SetPassword.SETPASSWORD_FAIL,
        error: err
    }
}

export const getUserData = (data) => {

    return{
        type: actionTypes.SetPassword.GET_USER_DATA,
        udata: data
    }
}