import classes from './RequiredCredits.module.css';

const RequiredCredits = (props) => {
    const requiredCredits = props.requiredCredits;
    const text = props.text;
    return ( 
        <div className={classes.container}>
            <span className={classes.cred}>{text}</span>
            <span className={classes.credVal}>{requiredCredits}</span>
        </div>
     );
}
 
export default RequiredCredits;