
import ContainerMain from './ContainerMain/ContainerMain';
import LogedInHeader from '../../components/LogedInHeader/LogedInHeader'
import HomeHeader from '../../components/HomeHeader/HomeHeader'
import { Container } from '@material-ui/core'
import BoxWithTitle from '../../components/BoxWithTitle/BoxWithTitle'
import classes from './payment.module.css'
import Footer from '../../components/Footer/Footer'
import { useEffect } from 'react';


const Payment = () => {

    useEffect(()=>{
        window.scrollTo(0,0);
    },[])
    return ( 
    <div>
        <HomeHeader LogedIn={true} />
        <LogedInHeader />
        <div className={classes.payment}>
         
         <Container>
            <BoxWithTitle title='PAYMENT'>
                <ContainerMain />   
            </BoxWithTitle>
        </Container>
        </div>
        <Footer/>    
    </div>
     );
}
 
export default Payment;