import axios from 'axios';
import * as actionTypes from '../actions/actions';
import {digid , lms} from './Constants'
export const getUserInfo = (name, value) => {
    return{
        type: actionTypes.CreateNUid.GET_USER_INFO,
        name: name,
        value: value
    }
}

export const getMaritalStatus = () => {
    return dispatch =>{
        
        
        var link = digid+'/nuversity/api/digid/getMaritalStatus';
       axios.get(link)
            .then(res=>{
                console.log(res.data)
                dispatch(createNUidMaritalStatusData(res.data));
                
                        
            }).catch(err => {
                console.log(err)
            })
       
    }
}

