export const submitTest = (firstname,middlename,lastname,dob,citizenship,phone,primarylanguage,landline,email,caddress,maritalstatus,gender,passportnumber,passportfile) => {
    
    return dispatch =>{
        dispatch(createNUidStart());
        var FD = new FormData();
        FD.append("firstname", firstname);
        FD.append("middlename", middlename);
        FD.append("lastname", lastname);
        FD.append("dob", dob);
        FD.append("citizenship", citizenship);
        FD.append("phone", phone);
        FD.append("primarylanguage", primarylanguage);
        FD.append("landline", landline);
        FD.append("email", email);
        FD.append("caddress", caddress);
        FD.append("maritalstatus", maritalstatus);
        FD.append("gender", gender);
        FD.append("passportnumber",passportnumber);
        FD.append("passportfile", passportfile);



            axios(
                {
                    method: "post",
                    url: digid+"/nuversity/api/digid/contact",
                    data: FD,
                    headers: { "Content-Type": "multipart/form-data" },
                }
                

            )

                .then(res=>{
                    console.log(res.data);
                    dispatch(createNUidEnd(res.data));
                        
                        
                        }).catch(err => {
                            dispatch(createNUidFail(err));
                        })
                }
}

export const createNUidStart = () => {

    return{
        type: actionTypes.CreateNUid.CREATENUID_START
    }

}

export const createNUidEnd = (data) => {

    return{
        type: actionTypes.CreateNUid.CREATENUID_END,
        userData: data
    }
}

export const createNUidFail = (err) => {

    return{
        type: actionTypes.CreateNUid.CREATENUID_FAIL,
        error: err
    }
}


export const createNUidCitizenshipData = (data) => {

    return{
        type: actionTypes.CreateNUid.CREATENUID_CITIZENSHIPDATA,
        ctshdata: data
    }
}

export const createNUidMaritalStatusData = (data) => {

    return{
        type: actionTypes.CreateNUid.CREATENUID_MARITALSTATUSDATA,
        msdata: data
    }
}