import classes from './PaymentForm.module.css';
import {InputAdornment, TextField ,Button, CircularProgress} from '@material-ui/core'
const PaymentForm = ({nameOnCard , cardNumber , expiryDate , cvv , handleOnchange , amount , submitForm , loading}) => {
    return ( 
        <div>
            
          <div className={classes.payInformation}>
             <div className={classes.payInformationLine}>
                <div className={classes.payInformationItemRight}>
                        <TextField 
                        label="YOUR NAME on Card" 
                        name="nameOnCard"
                        fullWidth 
                        variant='outlined'
                        value={nameOnCard} 
                        onChange={handleOnchange}
                        required={true}
                        />
                </div>
                <div className={classes.payInformationItemLeft}>
                        <TextField 
                        label="CARD NUMBER" 
                        name="cardNumber"
                        fullWidth
                        variant='outlined'
                        value={cardNumber} 
                        onChange={handleOnchange}
                        required={true}
                         
                        />
                </div>
            </div>

            <div className={classes.payInformationLine}>
                <div className={classes.payInformationItemRight}>
                        <TextField 
                        label="MONTH / YEAR"
                        name="expiryDate" 
                        fullWidth 
                        variant='outlined' 
                        value={expiryDate}
                        onChange={handleOnchange}
                        required={true}
                        />
                </div>
                <div className={classes.payInformationItemLeftCVV}>
                        <TextField 
                        label="CVV" 
                        fullWidth
                        variant='outlined' 
                        name="cvv"
                        value={cvv}
                        onChange={handleOnchange}
                        required={true}
                         type="password"
                        />
                </div>
            </div>
            <br/><br/><br/>
            <div className={classes.payInformationLine}>
               <div className={classes.payAmount}>
                   <p className={classes.payAmountText}>AMOUNT</p>
               </div>
               <div className={classes.payAmountLabel}>
                        <input type="text"  name="amount" value={amount} placeholder=" 200 " className={classes.payInput} onChange={handleOnchange} required></input>
               </div>
               <div className={classes.payButton}>
                   
               <Button
                style={{ padding: "10px" }}
                variant="contained"
                fullWidth
                color="primary"
                onClick={submitForm}
              >

                {
                  loading ? <CircularProgress style={{ color: "#fff" }} size={35} />: <h2>PAY</h2>
                }
                
              </Button>
                     
               </div>
                
            </div>
        
        </div>
        
    </div>   
       
    );
}
export default PaymentForm;