import Grid from "@material-ui/core/Grid";
import HomeHeader from "../../components/HomeHeader/HomeHeader";
import LogedInHeader from "../../components/LogedInHeader/LogedInHeader";
import classes from "./CheckAccount.module.css";
import { Button, CircularProgress, Container } from "@material-ui/core";
import BoxWithTitle from "../../components/BoxWithTitle/BoxWithTitle";
import BoxWithTitleAndWhiteBack from "../../components/BoxWithTitle2/BoxWithTitleAndWhiteBack";
import Infos from "../../components/PlanOfStudyComponents/Infos/Infos";
import Footer from "../../components/Footer/Footer";
import LabelWithValue from "../../components/CheckAccountComponents/LabelWithValue/LabelWithValue";
import Receipt from "../../components/CheckAccountComponents/Receipt/Receipt";
import * as Controls from '../../UI/Controls/Controls';
import * as CheckAccountActionCreators from '../../store/actionsCreators/CkeckAccountActions';
import { connect } from "react-redux";
import { useEffect } from "react";


function CheckAccount({from,to,asof,account,balance,getInfo,getInformation,getCheckInfos,checkTable,loading}) {

  useEffect(()=>{
    window.scrollTo(0,0);
},[])

  useEffect(() => {
    getInformation(localStorage.getItem('userId'),localStorage.getItem('major'));
 
  }, []);

  const handleOnchange = (e) =>{
       
         getInfo(e.target.name, e.target.value);
    
}

const getCheckInformation = (e) =>{
     getCheckInfos(from,to,localStorage.getItem('userId'),localStorage.getItem('major'));
}

  return (
    <>
      <HomeHeader LogedIn={true} />
      <LogedInHeader />
      <div className={classes.checkAccount}>
        <Container>
          <BoxWithTitle title="Check Account">
            <Grid container spacing={3}>
              <Grid item xs={12} md={6} sm={12}>
                <Infos
                  title="Info USER"
                  text="................................................................................................................................."
                />
              </Grid>
              <Grid item xs={12}>
                <LabelWithValue label="Account" value={account} />
              </Grid>
              {/* <Grid item xs={12} md={3} sm={6}></Grid> */}
              <Grid item xs={12} md={3} sm={6}>
                <LabelWithValue label="Balance" value={balance} />
              </Grid>
              <Grid item xs={12} md={3} sm={6}>
                <LabelWithValue label="As Of" value={asof} />
              </Grid>
              <Grid item xs={4} md={3} sm={6}>
                {/* <LabelWithValue label="From" value="01/01/2021" /> */}
                <Controls.DateInput name='from' id='from' label='From' value={ from} handleOnchange={ handleOnchange} required={true}  />
              </Grid>
              <Grid item xs={4} md={3} sm={6}>
                {/* <LabelWithValue label="To" value="31/08/2021" /> */}
                <Controls.DateInput name='to' id='to' label='To' value={to} handleOnchange={ handleOnchange} required={true}  />

              </Grid>
              <Grid item xs={12} md={6} sm={12}>
                <BoxWithTitleAndWhiteBack>
                 { 
                   checkTable ?  <Receipt tableInfo={checkTable}/> :<></>
                  }
                </BoxWithTitleAndWhiteBack>
              </Grid>
              <Grid item xs={12} md={3} sm={11}>
              </Grid>
              <Grid item xs={12} md={3} sm={1}>
                  <Button variant="contained" color="primary" onClick = {getCheckInformation}>
                  { 
                        loading ? <CircularProgress style={{color:'#fff'}} size={35} /> : <h2>Print</h2>
                  }
                    </Button>
              </Grid>
            </Grid>
          </BoxWithTitle>
        </Container>
      </div>
      <Footer />
    </>
  );
}

const mapStateToProps = (state) =>{
  return{
    to: state.CheckAccountR.to,
    from: state.CheckAccountR.from,
    asof: state.CheckAccountR.asof,
    balance: state.CheckAccountR.balance,
    account: state.CheckAccountR.account,
    checkTable: state.CheckAccountR.checkTable,
    loading: state.CheckAccountR.loading
  };
}

const mapDispatchToProps = (dispatch) =>{
  return{
      getInfo : (name, value) => dispatch(CheckAccountActionCreators.getUserInfo(name, value)), 
      getInformation : (user,major) => dispatch(CheckAccountActionCreators.getInformation(user,major)),
      getCheckInfos: (from,to,userid,major)=>dispatch(CheckAccountActionCreators.getCheckInfos(from,to,userid,major))
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(CheckAccount);
