import classes from "./Switch.module.css";

const Switch = ({probation}) => {
  var checkedyes = true;
  var checkedno = true;
  if(probation === 'YES'){
    checkedno = false;
    checkedyes = true;
  }
  else if(probation === 'NO'){
    checkedno = true;
    checkedyes = false;
  }
  return (
    <div className={classes.switch}>
      <div className={classes.quality}>
        <input checked={checkedyes} id="q1" name="q" type="radio" value="q1" disabled />
        <label for="q1">YES</label>
      </div>
      <div className={classes.quality}>
        <input checked={checkedno} id="q2" name="q" type="radio" value="q2" disabled />
        <label for="q2">NO</label>
      </div>
    </div>
  );
};

export default Switch;
