import { TextField, makeStyles } from '@material-ui/core'
import React from 'react'
import { useState } from 'react';
import CssClasses from './Controls.module.css';

const useStyle = makeStyles(theme =>({
    Container:{
        margin: '10px 8px',
        '& .MuiOutlinedInput-notchedOutline':{
            borderRadius: '0',
            border: '1px solid black'
        }
    }
}));

export const Input = ({variant='outlined', name, id, label, value='', size='small', type='text', handleOnchange, required, disabled}) => {
    const classes=useStyle();
    const inputProps = {
        type: type
    }

    return (    
        <div className={classes.Container}>
            <TextField 
            fullWidth 
            required={required}
            disabled={disabled}
            color='secondary' 
            size={size} 
            onChange={handleOnchange}
            variant={variant} 
            name={name} 
            id={id} 
            label={label} 
            value={value} 
            inputProps={inputProps} />
        </div>
    )
}


export const DateInput = ({id, name, label, value, handleOnchange, required, disabled}) => {
    const classes=useStyle();

    const handleInputTypeChange = (e) =>{
        if(e.target.type === 'date'){
            // e.target.type = 'text';
        }else{
            e.target.type = 'date'
        }
    }

    return (    
        <div className={classes.Container}>
            <input 
            type="text" 
            onFocus={handleInputTypeChange}
            onBlur={handleInputTypeChange} 
            disabled={disabled}
            onChange={handleOnchange}
            className={CssClasses.InputDate} 
            placeholder={ label + `${required ? ' *' : ''}` }
            name={name} 
            id={id} 
            value={value} />
        </div>
    )
}

export const SelectInput = ({id, name, label, children, value, handleOnchange,options,optionData, required}) => {
    const classes=useStyle();
        const arrt = options;
        var Fields = <></>
        if(optionData.length>0){
         Fields = optionData.map(fieldName=>{
            return(
                <option value={fieldName} key={fieldName}>{fieldName}</option>
            )
        })
    }
        
    return (    
        <div className={classes.Container}>
            <select 
            className={CssClasses.Select} 
            name={name} 
            id={id} 
            defaultValue=''
            onChange={handleOnchange}
            required={required} >

                <option disabled={true} value="">{label} {required ? '*' : ''}</option>
                
                {Fields}
            
            </select>
        </div>
    )
        
}


export const CheckBoxInput = ({id, name, label, value, handleOnchange, required, disabled,checked}) => {

    return (    
        <div className={CssClasses.RadioInputContainer}>
            <input  
            type='radio' 
            id={id} 
            name={name} 
            className={CssClasses.RadioInput} 
            onChange={handleOnchange}
            value={value} 
            required={required}
            disabled={disabled}
            checked={checked} />
            <label htmlFor={id} className={CssClasses.LabelRadioInput}>{label}</label>
        </div>
    )
}

export const UploadFile = ({id, name, label, handleOnchange, required}) => {

    const [pickedFileName, setPickedFileName] = useState(label);

    const handleUploadPassport = () => {
        document.getElementById(id).click();
    }

    const handleUploadPassport1 = (e) => {
        let fileName = e.target.value.split(/(\\|\/)/g).pop();
        setPickedFileName(fileName);
        handleOnchange(e);
    }
    return (    
        <div className={CssClasses.Upload_File_Container} onClick={handleUploadPassport}>
            <input 
            type="file" 
            id={id} 
            name={name} 
            hidden 
            onChange={handleUploadPassport1}
            required={required}  />
            <div className={CssClasses.Upload_File_Text}>{pickedFileName}{required ? ' *' : ''}</div>
            <div className={CssClasses.Upload_File_Button}>Browse</div>
        </div>
    )
}