import React from 'react'
import { Redirect, Route, Switch } from 'react-router'
import Application from './pages/Application/Application'
import CreateNUid from './pages/CreateNUid/CreateNUid'
import UpdateNUid from './pages/UpdateNUid/UpdateNUid'
import Home from './pages/Home/Home'
import Login from './pages/Login/Login'
import Logout from './pages/Logout/Logout'
import Payment from  './pages/Payment/Payment'
import History from  './pages/History/History'
import PlanOfStudy from './pages/PlanOfStudy/PlanOfStudy'
import CheckAccount from './pages/CheckAccount/CheckAccount'
import CurrentSemester from './pages/CurrentSemester/CurrentSemester'
import StudentPortal from './pages/StudentPortal/StudentPortal'
import ResetPassword from './pages/ResetPassword/ResetPassword'
import SetPassword from './pages/SetPassword/SetPassword'
import ChangeForgottenPassword from './pages/ChangeForgottenPassword/ChangeForgottenPassword'
import SendForgettenPasswordMail from './pages/SendForgettenPasswordMail/SendForgettenPasswordMail'
import CalendarPage from './pages/CalendarPage/CalendarPage'
import ApplicationPending from  './pages/ApplicationPending/ApplicationPending'
import StudentMainPage from  './pages/StudentMainPage/StudentMainPage'
import AcceptStudent from  './pages/AdminAcceptStudent/AdminAcceptStudent'
import AcceptEnrolment from  './pages/AcceptEnrolment/AdminAcceptEnrolment'
import Administration from  './pages/Administration/Administration'
import SubscribeSemester from  './pages/SebscribeSemester/SubscribeSemester'

const StudentRoute = ({ component: Component, ...props }) => (
  <Route
    {...props}
    render={routeProps => {
      const major = localStorage.getItem("major");
      const userid = localStorage.getItem("userId");
      const group = localStorage.getItem("group");
      // Do all your conditional tests here
      return userid !== null && major !== null && group === 'STUDENT'? (
        <Component {...routeProps} />
      ) : (
        <Redirect to="/Login" />
      );
    }}
  />
);


const ApplicantRoute = ({ component: Component, ...props }) => (
  <Route
    {...props}
    render={routeProps => {
      const userid = localStorage.getItem("userId");
      const group = localStorage.getItem("group");
      // Do all your conditional tests here
      return userid !== null &&  group === 'APPLICANT'? (
        <Component {...routeProps} />
      ) : (
        <Redirect to="/Login" />
      );
    }}
  />
);

const App = () => {
  return (
    <div className='App'>
      <Switch>
      
      <Route path='/Administration' exact component={Administration} />
      
        <Route path='/AdminAcceptStudent' exact component={AcceptStudent} />
        <Route path='/AdminAcceptEnrolment' exact component={AcceptEnrolment} />
        <Route path='/SubscribeSemester' exact component={SubscribeSemester} />
        <Route path='/' exact component={Home} />
        <Route path='/Login' component={Login} />
        <Route path='/Logout' component={Logout} />
        <Route path='/CreateNUid' component={CreateNUid} />
        <StudentRoute path='/UpdateNUid' component={UpdateNUid} />
        <ApplicantRoute path='/Apply-Application' component={Application} />
        <StudentRoute  path='/Payment' component={Payment}/>
        <StudentRoute  path='/History' component={History}/>
        <ApplicantRoute  path='/ApplicationPending' component={ApplicationPending}/>
        <Route path='/SetPassword' component={SetPassword} />
        <StudentRoute path='/PlanOfStudy' component={PlanOfStudy} />
        <StudentRoute path='/CheckAccount' component={CheckAccount} />
        <StudentRoute path='/CurrentSemester' component={CurrentSemester} />
        <StudentRoute path='/StudentPortal' component={StudentPortal} />
        <StudentRoute path='/CalendarPage' component={CalendarPage} />
        <Route path='/ChangeForgottenPassword' component={ChangeForgottenPassword} />
        <Route path='/ForgetPassword' component={SendForgettenPasswordMail} />
	      <StudentRoute path='/ResetPassword' component={ResetPassword} />
        <StudentRoute path='/StudentMainPage' component={StudentMainPage} />
        <Redirect to='/' />
      </Switch>
    </div>
  )
}

export default App
