import * as actionTypes from '../actions/actions';

const initialState = {
    year: '',
    semestername: '',
    date: '',
    majorname: '',
    userId: '',
    error: '',
    loading: false,
    courses: '',
    dropList: [],
    result: ''
}

const CurrentSemesterR =  (state = initialState, action) => {
    switch (action.type) {

        case actionTypes.CreateNUid.GET_USER_INFO :
            return { ...state, [action.name] : action.value  }

        case actionTypes.CurrentSemester.CurrentSemester_START :
            return { ...state,loading: true}
    
        case actionTypes.CurrentSemester.CurrentSemester_END :
            return { ...state,loading: false,result: action.userData} 
                 
        case actionTypes.CurrentSemester.CurrentSemester_FAIL :
            return { ...state,loading: false,error: action.error}
            
        case actionTypes.CurrentSemester.GET_STUDENT_INFO :
            return { ...state,loading: false,majorname: action.stddata.major,userId: action.stddata.userid}  
            
        case actionTypes.CurrentSemester.GET_Current_Semester_INFO :
            return { ...state,loading: false,year: action.csidata.year,semestername: action.csidata.semester,date: action.csidata.date,courses: action.csidata.courses}   
        default:
            return state
    }
}

export default CurrentSemesterR;
