import { Button } from '@material-ui/core'
import React, { useState } from 'react'
import ButtonSection from '../ButtonSection/ButtonSection'
import InputDocument from './InputDocument/InputDocument'
import classes from './RequiredDocuments.module.css'
import SaveIcon from '@material-ui/icons/Save';
const RequiredDocuments = ({requiredDocument,docList,docListName}) => {

    const [showRequiredDocuments, setShowRequiredDocuments] = useState(false)

    const toggleRequiredDocuments = () =>{
        setShowRequiredDocuments(!showRequiredDocuments)
    }

    // const handleOnchange = (e) =>{
    //     docListName.push(e.target.name);
    //     docList.push(e.target.files[0]);
    // }


    const onClickButton = () =>{
        setShowRequiredDocuments(false);
    }

    const requiredDocuments = ['secondary certificate', 'toefl or ielts', 'other certificates']
    return (
        <div className={classes.RequiredDocuments}>
            <ButtonSection Text='required documents' clicked={toggleRequiredDocuments} toggleValue={showRequiredDocuments} />
            {
                showRequiredDocuments ?
                <form style={{ width: '100%' }}>
                    <div className={classes.Documents}>
                        {
                            requiredDocument.map(d=>{
                                return(
                                    <InputDocument name={d.doc} key={d.doc} docListName={docListName} docList={docList}/>
                                );
                            })
                        }
                        <Button
                            variant="contained"
                            color="primary"
                            size="large"
                            fullWidth
                            style={{ margin: '20px' }}
                            startIcon={<SaveIcon />}
                            onClick={onClickButton}
                        >
                            Save
                        </Button>
                    </div>
                </form> :
                    null
            }
            
        </div>
    )
}

export default RequiredDocuments
