import Grid from "@material-ui/core/Grid";
import classes from "./Announcements.module.css";
import img from "../../../assets/images/AnnoucementImga.png";
import img1 from "../../../assets/images/TypeArrow.png";

const Announcements = () => {
  return (
    <div className={classes.container1}>
      <Grid container spacing={3}>
        <Grid item xs={12} md={6} sm={5}>
          <div className={classes.img1}>
            <img src={img} />
          </div>
        </Grid>
        <Grid item xs={12} md={6} sm={7} justifyContent="center">
          <div className={classes.c2}>
            <h2>
              Announcement <br />
              and news
            </h2>
            <p>
              Spring 2022 Registrations starts on Monday 10 November 2021 at
              10:00 am!
            </p>
            <p>Check out the offerings for the new semester</p>
            <div className={classes.bt}>
              See courses offering <img src={img1} />
            </div>
          </div>
        </Grid>
      </Grid>
    </div>
  );
};

export default Announcements;
