import Divider from "../../Divider/Divider";
import Cost from './Cost/Cost';
import TotalSelection from './TotalSelection/TotalSelection';
import classes from './TotalCreditsAndCost.module.css';

const TotalCreditsAndCost = ({totalSelection,creditCost}) => {
    return ( 
        <div className={classes.container}>
            <div className={classes.subContainer}>
                <TotalSelection totalSelection={totalSelection}/>
                <Divider padding="20px" />
                <Cost totalSelection={totalSelection} creditCost={creditCost}/>
            </div>
        </div>
     );
}
 
export default TotalCreditsAndCost;
