import * as actionTypes from '../actions/actions';

const initialState = {
    students: '',
    programs: ''
}

const AcceptEnrolmentR =  (state = initialState, action) => {
    switch (action.type) {

        case actionTypes.AcceptEnrolment.GET_ENROLMENT_PENDING :
            return { ...state,students: action.cdata}

        // case actionTypes.ApplyApplication.GET_PROGRAMS :
        //     return { ...state,loading: false,programs: action.PGMData}

        default:
            return state
    }
}

export default AcceptEnrolmentR;