import axios from 'axios';
import * as actionTypes from '../actions/actions';
import {digid , lms} from './Constants'

export const getPendingEnrolment = () => {
    return dispatch =>{
       
        
        var link = lms+'/nuversity/api/lms/AllEnrolment';
       axios.get(link)
            .then(res=>{
                
                dispatch(getPendingEnrolmentData(res.data));
                
                        
            }).catch(err => {
                console.log(err)
            })
      
    }
}

export const getPendingEnrolmentData = (data) => {

    return{
        type: actionTypes.AcceptEnrolment.GET_ENROLMENT_PENDING,
        cdata: data
    }
}

export const acceptEnrolmentC = (studentId,majorId,course) => {
    
    return dispatch =>{
        // dispatch(planofstudyStart());
        var cs = [];
        cs.push(course);
        const params = {
            "studentId": studentId,
            "majorId": majorId,
            "courses":cs
        }
                                                                                      
              axios.post(lms+"/nuversity/api/lms/acceptRegisterCourses",params,{ headers: { "Content-Type": "application/json", }, })


                .then(res=>{
                    
                    dispatch(getPendingEnrolment());
                        
                        }).catch(err => {
                            dispatch(planofstudyFail(err));
                        })
                }
}

