import LogedInHeader from '../../components/LogedInHeader/LogedInHeader'
import HomeHeader from '../../components/HomeHeader/HomeHeader'
import { Container } from '@material-ui/core'
import BoxWithTitle from '../../components/BoxWithTitle/BoxWithTitle'
import Footer from '../../components/Footer/Footer'
import classes from './ApplicationPending.module.css'
// import GreyBoxithTitle from '../../components/GreyBoxWithTitle/GreyBoxWithTitle'


const ApplicationPending = () => {
    return ( 
    <div>
        <HomeHeader/>
        <LogedInHeader />

        <div className={classes.history}>
       
         <Container>
            <BoxWithTitle title='APPLICATION PENDING'>
                    <div className={classes.boxPosition}>
                    <div className={classes.textBoxApplication}>
                        <p>
                            Dear student, Your application has been successfuly registered.<br/>
                        </p>    
                         </div>
                    </div>
                                       
            </BoxWithTitle>
        </Container>
        </div>
        <Footer/>    
    </div>
     );
}
 
export default ApplicationPending;