import classes from "./Table.module.css";


const Table = ({courses}) => {

  const Fields = courses.map(fieldName=>{
   
    return(
        
        <tr key={fieldName.code}>
        <td className={classes.name}>{fieldName.code}</td>
        <td className={classes.teach}>{fieldName.name}</td>
        <td className={classes.credit}>{fieldName.status}</td>
        <td className={classes.avail}>{fieldName.result}</td>
        <td className={classes.camp}>{fieldName.credit}</td>
      </tr>
    )
})
  return (
    <div className={classes.containerTable}>
      <table className={classes.tab}>
        <thead className={classes.tabHead}>
          <tr>
            <th>CODE</th>
            <th>Name of Course</th>
            <th>Status</th>
            <th>Result</th>
            <th>Credit</th>
          </tr>
        </thead>
        <tbody className={classes.tabBody}>
         {Fields}
        </tbody>
      </table>
    </div>
  );
};

export default Table;
