import classes from "./Schedule.module.css";

const Schedule = ({courName,Monday,Tuesday, Wednesday, Thursday, Friday, Saturday, Sunday}) => {
  return (
    <div className={classes.container}>
      <table className={classes.tab}>
        <thead className={classes.tabHead}>
          <tr>
            <th className={classes.b}>COURSES / DAYS</th>
            <th className={classes.itemSchedule}>Monday                        </th>
            <th className={classes.itemSchedule}>TUESDAY  </th>
            <th className={classes.itemSchedule}>WEDNESDAY</th>
            <th className={classes.itemSchedule}>THURDAY  </th>
            <th className={classes.itemSchedule}>FRIDAY   </th>
            <th className={classes.itemSchedule}>SATURDAY </th>
            <th className={classes.itemSchedule}>SUNDAY   </th>
          </tr>
        </thead>
        <tbody className={classes.tabBody}>
          <tr>
            <td className={classes.b}>{courName}</td>
            <td className={classes.itemSchedule}>{Monday}</td>
            <td className={classes.itemSchedule}>{Tuesday}</td>
            <td className={classes.itemSchedule}>{Wednesday}</td>
            <td className={classes.itemSchedule}>{Thursday}</td>
            <td className={classes.itemSchedule}>{Friday}</td>
            <td className={classes.itemSchedule}>{Saturday}</td>
            <td className={classes.itemSchedule}>{Sunday}</td>
          </tr>
        </tbody>
      </table>
    </div>
  );
};

export default Schedule;
