import GreyBoxithTitle from '../../../components/GreyBoxWithTitle/GreyBoxWithTitle'
import classes from './HistoryForm.module.css'
import * as Controls from '../../../UI/Controls/Controls';
import Table from '../Table/Table';
import React, { useEffect } from 'react';

const HistoryForm = ({yearsData,semestersData,year,semester,courses,getInfoHistory,getSemesters}) => {
    const [chooseYear, setChooseYear] = React.useState(false);
    
    useEffect(()=>{
        if(year !== '' && chooseYear){
        getSemesters(localStorage.getItem('userId'),localStorage.getItem('major'),year);
        }

    },[year]);

    const onC = (e)=>{
        
        if(e.target.name === 'year'){
            setChooseYear(true);
        getInfoHistory(e.target.name,e.target.value);
        }else{
            getInfoHistory(e.target.name,e.target.value);  
        }
    }

    return ( 
        <div className={classes.historyFormRoot}>
             <GreyBoxithTitle Title='Student Info' /> 
               <div className={classes.historyForm}>
                   <div className={classes.historyFormLine}>
                       <div className={classes.historyFormLineLeft}>
                           
                           {
                            yearsData ? <Controls.SelectInput name='year' id='chooseyear' label='Choose the year' optionData={yearsData} value={year} handleOnchange={onC} required={false}   />: <></>
                           }
                            </div>
                       <div className={classes.historyFormLineRight}>
                           {
                           semestersData && chooseYear ? <Controls.SelectInput name='semester' id='choosesemester' label='Choose the Semester' value={semester} handleOnchange={onC} required={false} optionData={semestersData} />:<></>
                           }
                            </div>
                    </div>
                    <div className={classes.historyFormLine}>  
                        <div className={classes.historyDarkGreyBox}>
                            <h3 className={classes.historyDarkGreyBoxText}>SHOW NOW</h3>    
                        </div>
                    </div>
                    {year && semester ?
                    <div className={classes.historyFormLine}>  
                        <h1 className={classes.greyText}>
                            YOUR RESULT
                        </h1>
                        <h1 className={classes.rubyText}>
                            Year: {year}-Semester: {semester}
                        </h1>
                    </div>
                    :<></>}
                    {
                    courses ? <Table courses={courses}/> :<></>
                    } 
               </div>
              
        </div>

    );}

export default HistoryForm