import React from 'react'
import classes from './CourseRow.module.css'
const CourseRow = ({CourseName, CreditNumber, clicked}) => {
    return (
        <div className={classes.CourseRow} onClick={clicked}>
            <div>
                {CourseName}
            </div>
            <div>
                {CreditNumber}
            </div>
        </div>
    )
}

export default CourseRow
