import { Grid } from '@material-ui/core'
import { render } from '@testing-library/react';
import React, { Component } from 'react'
import * as Controls from '../../UI/Controls/Controls';
const OtherPersonalInformation   = ({maritalstatus, gender, passportnumber, passportfile, handleOnchange,maritalStatusData}) => {
  
    return (
        <>
            <Grid item container xs={12}>
                <Grid item xs={12} sm={6}>
                    {
                    maritalStatusData ? <Controls.SelectInput name='maritalstatus' id='MartialStatus' label='Martial Status' value={ maritalstatus} optionData={maritalStatusData} handleOnchange={ handleOnchange} required={false}  /> :<></> 
                    }
                </Grid>
                <Grid item xs={12} sm={6} container alignItems='center'>
                    <Grid item xs={5} >
                        <Controls.CheckBoxInput name='gender' id='Male' label='Male' value='Male' handleOnchange={ handleOnchange} required={true}  />
                    </Grid>
                    <Grid item xs={5}>
                        <Controls.CheckBoxInput name='gender' id='Female' label='Female' value='Female' handleOnchange={ handleOnchange} required={true}  />
                    </Grid>
                </Grid>
                <Grid item xs={12} container>
                    <Grid item xs={12} sm={6}>
                        <Controls.Input name='passportnumber' id='PassportNumber' label="Passport Number" value={ passportnumber} handleOnchange={ handleOnchange} required={true} ></Controls.Input>  
                    </Grid>
                    <Grid item xs={12} sm={6}>
                        <Controls.UploadFile name='passportfile' id='PassportFile' label="Upload your passport" value={ passportfile} handleOnchange={ handleOnchange} required={true} ></Controls.UploadFile>  
                    </Grid>
                </Grid>
            </Grid>
                
        </>
    )
}


export default OtherPersonalInformation
