import * as actionTypes from '../actions/actions';

const initialState = {
    semesters: '',
    semester: '',
    year: '',
    student: '',
    result: ''
}

const AcceptEnrolmentR =  (state = initialState, action) => {
    switch (action.type) {


        case actionTypes.SubscribeSemester.GET_INFORMATION_ACCEPT :
            return { ...state, [action.name] : action.value  }

        case actionTypes.SubscribeSemester.GET_ALL_ACCEPT_SEMESTER:
            return { ...state,semesters: action.cdata}

        case actionTypes.SubscribeSemester.SUB_END:
            return { ...state,result: action.userData}

        default:
            return state
    }
}

export default AcceptEnrolmentR;