import axios from 'axios';
import * as actionTypes from '../actions/actions';
import {digid , lms} from './Constants'


export const getData = (studentId,major) => {
//    return dispatch =>{
//         var res = {name: 'ahmad wwalid', major: 'computer science', accountBalance: 7896};
//         dispatch(fillStudentInfo(res));
//    }

return dispatch =>{
        
        
    var link = lms+'/nuversity/api/lms/getStudentPaymentInfo/'+studentId+'/'+major;
   axios.get(link)
        .then(res=>{
            console.log(res.data)
            dispatch(fillStudentInfo(res.data));
            
                    
        }).catch(err => {
            console.log(err)
        })
   
}
    
}

export const fillStudentInfo = (res) =>{
    return{
        type: actionTypes.Payment.PAYMENT_FILL_INFO,
        studentInfo: res
    }
}

export const getPaymentInfo = (name, value) => {
    return{
        type: actionTypes.Payment.GET_PAYMENT_INFO,
        name: name,
        value: value
    }
}


export const submitPayment = (nameOnCard,userId,major,cardNumber,expiryDate,cvv,amount) => {
    
    return dispatch =>{
        dispatch(paymentStart());
        const params = {
            "nameOncard":nameOnCard,
            "userId":userId,
            "major":major,
            "cardNumber":cardNumber,
            "expiryDate":expiryDate,
            "cvv":cvv,
            "amount":amount
        }
        
            axios.post(lms+"/nuversity/api/lms/pay",params,{ headers: { "Content-Type": "application/json", }, })

                .then(res=>{
                    console.log(res.data);
                    dispatch(paymentEnd(res.data));
                         
                         
                        }).catch(err => {
                            dispatch(paymentFail(err));
                        })                                                                                                                                        
                }
}

export const paymentStart = () => {

    return{
        type: actionTypes.Payment.PAYMENT_START
    }

}

export const paymentEnd = (data) => {

    return{
        type: actionTypes.Payment.PAYMENT_END,
        payData: data
    }
}

export const paymentFail = (err) => {

    return{
        type: actionTypes.Payment.PAYMENT_FAIL,
        error: err
    }
}