import React, { useState } from 'react'
import BoxToSelect from '../BoxToSelect/BoxToSelect'
import ButtonSection from '../ButtonSection/ButtonSection'
import InputField from '../InputField/InputField'
import classes from './Courses.module.css'
import CoursesTable from './CoursesTable/CoursesTable'
const StudyYear = ({coursesTypeS , coursesList,getInfoApp}) => {

    const [showCourses, setShowCourses] = useState(false)

    const toggleCourses = () =>{
        setShowCourses(!showCourses)
    }

    const handleChooseField = (e) =>{
        console.log(e.target.value);
        getInfoApp('coursesTypeS',e.target.value);
    }

    const CoursesType = ['GENERAL', 'MAJOR', 'CORE', 'ELECTIVE']
    const GMCE = CoursesType.map(CourseType=>{
        return(
            <div key={CourseType} style={{ width: '100%' }}>
                <InputField clicked={handleChooseField} fieldName={CourseType} value={CourseType} name={'courseType'}/>
            </div>
        )
    })
    
    return (
        <div className={classes.Courses}>
            <ButtonSection Text='courses' clicked={toggleCourses} toggleValue={showCourses} />
            {
                showCourses ?
                <div className={classes.Body}>
                    <div className={classes.CoursesBox}>
                        <BoxToSelect toShow = {GMCE} />
                    </div>
                    <div className={classes.Space}></div>
                    <div className={classes.CoursesName}>
                        {
                      coursesList ?  <CoursesTable courses={coursesList}/> : <></>
                        }
                    </div>
                </div>:
                null
            }
        </div>
    )
}

export default StudyYear
