import axios from 'axios';
import * as actionTypes from '../actions/actions';
import {digid , lms} from './Constants'
export const getAppointments = (student,major) => {
    return dispatch =>{
       
      
        var link = lms+'/nuversity/api/lms/getStudentCalendar/'+student+'/'+major;
        axios.get(link)
            .then(res=>{
                var dataA = [];
                res.data.map(fi=>{
                    dataA.push({
                        title: fi.title,
                        startDate: new Date(fi.startYear, fi.startMonth, fi.startDay, fi.startHour, fi.startMin),
                        endDate: new Date(fi.endYear, fi.endMonth, fi.endDay, fi.endHour, fi.endMin),
                      })
                  })
                dispatch(getAppointmentsData(dataA));
                
                        
            }).catch(err => {
                console.log(err)
            })
            
      
    }
}

export const getAppointmentsData = (data) => {

    return{
        type: actionTypes.Calendar.GET_APPOINTMENTS,
        adata: data
    }
}