import React, { useState } from 'react'
import classes from './AcceptStdTableRow.module.css'



const AcceptStdTableRow = ({student, major, campus, accept,acceptStudent}) => {


    const [acceptButton,setAcceptButton] = React.useState("Accept");
    const acceptStd =()=>{
        acceptStudent(student,accept.majorId,accept.campusId);
        // setAcceptButton("Loading...");
    }

    
    return (

        <div className={classes.ReceiptRow}>
            <div>
                {student}
            </div>
            <div>
                {major}
            </div>
            <div>
                {campus}
            </div>
            <div>
          
            <button onClick={acceptStd} className={classes.acceptButton}>{acceptButton}</button>
                
               
            </div>
        </div>
    
    )
}

export default AcceptStdTableRow
