import axios from 'axios';
import * as actionTypes from '../actions/actions';
import {digid , lms} from './Constants'



export const getPendingStudents = () => {
    return dispatch =>{
       
        
        var link = lms+'/nuversity/api/lms/allPendingStudent';
       axios.get(link)
            .then(res=>{
                
                dispatch(getPendingStudentsData(res.data));
                
                        
            }).catch(err => {
                console.log(err)
            })
      
    }
}

export const getPendingStudentsData = (data) => {

    return{
        type: actionTypes.AccpetStudent.GET_STUDENTS_PENDING,
        cdata: data
    }
}






export const acceptStudentReg = (studentId,majorId,campusId) => {
    
    return dispatch =>{
        // dispatch(planofstudyStart());
        var link = digid+'/nuversity/api/digid/makeStudent?userid='+studentId;
       axios.post(link)
           
                .then(res=>{
                    
                    dispatch(acceptStudentscd(studentId,majorId,campusId));
                        
                        }).catch(err => {
                            dispatch(planofstudyFail(err));
                        })
                }
}



export const acceptStudentscd = (studentId,majorId,campusId) => {
    
    return dispatch =>{
        // dispatch(planofstudyStart());
        const params = {
            "userid": studentId,
            "university": "NUVERSITY",
            "majorid": majorId,
            "campus":campusId
        }
                                                                                      
              axios.post(lms+"/nuversity/api/lms/student",params,{ headers: { "Content-Type": "application/json", }, })


                .then(res=>{
                    
                    dispatch(getPendingStudents());
                        
                        }).catch(err => {
                            dispatch(planofstudyFail(err));
                        })
                }
}

