import React from "react";
import ReceiptRow from "./AcceptStdTableRow/AccpetStdTableRow";
import classes from "./AcceptTableContent.module.css";

const AcceptTableContent = ({table,acceptStudent}) => {

  // if(tableInfo !== ''){
  // const Receipts = tableInfo;
  // }else{
  //   const Receipts =[];
  // }

  const ReceiptsList = table.map((receipt, index) => {
    return (
      <ReceiptRow
      acceptStudent={acceptStudent}
        student={receipt.studentid}
        major={receipt.majorName}
        campus={receipt.campusName}
        accept={{"majorId":receipt.majorId,
                "campusId":receipt.campusId}}       


        key={index}
      />
    );
  });

  return (
    <>
      <div className={classes.ReceiptsTable}>
        <div className={classes.Title}>
          <div>Student Id</div>
          <div>Major</div>
          <div>Campus</div>
          <div></div>
        </div>
        {ReceiptsList}
      </div>
    </>
  );
};

export default AcceptTableContent;
