import React from 'react'
import { Grid, makeStyles,CircularProgress } from '@material-ui/core';
import * as Controls from '../../UI/Controls/Controls';
import OtherPersonalInformation from './OtherPersonalInformation';
import { connect } from 'react-redux';
import * as updateNUidActionCreators from '../../store/actionsCreators/UpdateNUidActions';
import { useEffect } from "react";
import Backdrop from '@material-ui/core/Backdrop';
import classesSty from "./UpdateNUid.module.css";

const  useStyle = makeStyles(theme => ({
    Form: {
        width: '100%'
    },
    SubTitle: {
        textTransform: 'uppercase',
        letterSpacing: '.5px',
        fontWeight: '500',
        margin: '10px 0px 0px 8px'
    },
    FaceIDLink: {
        margin: '10px 0px 0px 8px',
        textDecoration: 'underline',
        cursor: 'not-allowed'
    },
    backdrop: {
        zIndex: theme.zIndex.drawer + 1,
        color: '#fff',
      },
    SubmitCreatNUIdForm: {
        margin: '20px 0px 0px 8px',
        border: 'none',
        outline: 'none',
        background: '#900603',
        padding: '8px 40px',
        color: 'white',
        cursor: 'pointer'
    }

}));

const PersonalInformationForm  = ({ updateContact, handleSubmission, getUserInfo,
     getUserById, firstname, lastname, middlename, dob, citizenship, phone, primarylanguage,
      landline, email, caddress, maritalstatus, gender, passportnumber, passportfile,loading,error,maritalStatusData,citizenshipData,primaryLanguagesData,getMaritalStatus,getPrimaryLanguages,getCitizenShip,userId,status,result }) => {
    
   
    const classes = useStyle();
    const [openBackdrop, setOpenBackdrop] = React.useState(false);
    
        
    useEffect(() => {
        if(citizenshipData){
            getUserById(localStorage.getItem('userId'));
            
        }
      }, [citizenshipData]);

    useEffect(() => {
      if(result !== ''){
      setOpenBackdrop(true);
      }
    }, [result]);

    const handleClose = () =>{
        setOpenBackdrop(false);
    }


    const handleOnchange = (e) =>{
       
        if(e.target.type === 'file'){
            getUserInfo(e.target.name, e.target.files[0])
        }else{
            getUserInfo(e.target.name, e.target.value);
        }
    }
    useEffect(() => {
        getMaritalStatus();
        getPrimaryLanguages();
        getCitizenShip();
        
      }, []);

      const updateNuidInfo = (e) =>{
        updateContact(localStorage.getItem('userId'),citizenship,phone,landline,caddress,maritalstatus,passportnumber,passportfile);
      }
    return (
        <Grid container item xs={12}>
            <Backdrop className={classes.backdrop} open={openBackdrop} onClick={handleClose}>
                <div style={{ color: '#fff' }}>
                    {
                        
                        <div className={classesSty.textBoxApplication}>
                            {
                             result === 'Success' ? <p>Your information has been updated successfully</p> : <p>Somthing Wrong. Please try again later.</p>
                            }
                         </div>

                    }
                </div>    
        </Backdrop>
            <Grid item xs={12}>
                <p className={ classes.SubTitle}>personal information</p>
            </Grid>
            <Grid item container xs={12}>
               
                    <Grid item container xs={12}>
                       
                        <Grid item xs={12} sm={4}>
                            <Controls.Input name='firstname' id='firstName' label='First Name' value={firstname} handleOnchange={ handleOnchange} disabled={true} required={true}/>
                        </Grid>
                        <Grid item xs={12} sm={4}>
                            <Controls.Input name='middlename' id='middleName' label='Middle Name' value={middlename} handleOnchange={ handleOnchange} disabled={true} required={true}  />
                        </Grid>
                        <Grid item xs={12} sm={4}>
                            <Controls.Input name='lastname' id='lastName' label='Last Name' value={lastname} handleOnchange={ handleOnchange} disabled={true} required={true}   />
                        </Grid>
                        
                    </Grid>
                    <Grid item container xs={12}>
                        <Grid item xs={12} sm={4}>
                            <Controls.DateInput name='dob' id='DoB' label='Date of Birthday' value={dob} handleOnchange={ handleOnchange} disabled={true} required={true}  />
                        </Grid>
                        <Grid item xs={12} sm={4}>
                            {
                            citizenshipData ? <Controls.SelectInput name='citizenship' id='citizenship' label="Citizenship" value={ citizenship} optionData={citizenshipData} handleOnchange={ handleOnchange} required={false}  ></Controls.SelectInput> : <></>
                            }                                                                          
                                                      </Grid>
                        <Grid item xs={12} sm={4}>
                            <Controls.Input name='phone' id='phone' label='Phone Number' value={ phone} handleOnchange={ handleOnchange} required={true}    />
                        </Grid>
                    </Grid>
                    <Grid item container xs={12}>
                        <Grid item xs={12} sm={4}>
                            {
                            primaryLanguagesData ? <Controls.SelectInput name='primarylanguage' id='PrimaryLanguage' label='Primary Language' value={ primarylanguage} optionData={primaryLanguagesData} handleOnchange={ handleOnchange} required={true}/>:<></>
                            }                        </Grid>
                        <Grid item xs={12} sm={4}>
                            <Controls.Input name='landline' id='LandLine' label="Landline" value={landline} handleOnchange={ handleOnchange} required={false}   />
                        </Grid>
                        
                        <Grid item xs={12} sm={4}>
                            <Controls.Input name='email' id='Email' label="Email" value={email} handleOnchange={ handleOnchange} disabled={true} required={true} type='email' />
                        </Grid>
                    </Grid>
                    <Grid item container xs={12}>
                        <Grid item xs={12}>
                            <Controls.Input name='caddress' id='Address' label='Address' value={caddress} handleOnchange={ handleOnchange} required={true}   />
                        </Grid>     
                    </Grid>
                    <hr style={{ margin: '30px 8px' }} />
                    <Grid item xs={12}>
                        <p className={ classes.SubTitle}>other personal information</p>
                    </Grid>
                    <OtherPersonalInformation maritalstatus={maritalstatus}  gender={gender} passportnumber={ passportnumber} passportfile={ passportfile} handleOnchange={ handleOnchange} maritalStatusData={maritalStatusData}/>
                    <Grid item xs={12}>
                        <p className={ classes.FaceIDLink}>Open camera for Face ID to unlock accessibility features
                        </p>
                    </Grid>
                    
                    <Grid item xs={12}>
                    <button className={ classes.SubmitCreatNUIdForm} onClick={updateNuidInfo}>
                    { 
                         loading ? <CircularProgress style={{color:'#fff'}} size={35} /> : <h2>Submit</h2>
                    }
                    </button>
                    </Grid>
               
            </Grid>
           
        </Grid>
    )
}


const mapStateToProps = (state) =>{
    return{
        firstname: state.UpdateNUidR.firstname,
        middlename: state.UpdateNUidR.middlename,
        lastname: state.UpdateNUidR.lastname,
        gender: state.UpdateNUidR.gender,
        citizenship: state.UpdateNUidR.citizenship,
        phone: state.UpdateNUidR.phone,
        dob: state.UpdateNUidR.dob,
        primarylanguage: state.UpdateNUidR.primarylanguage,
        email: state.UpdateNUidR.email,
        landline: state.UpdateNUidR.landline,
        caddress: state.UpdateNUidR.caddress,
        passportfile: state.UpdateNUidR.passportfile,
        passportnumber: state.UpdateNUidR.passportnumber,
        maritalstatus: state.UpdateNUidR.maritalstatus,
        loading: state.UpdateNUidR.loading,
        maritalStatusData: state.UpdateNUidR.maritalStatusData,
        citizenshipData: state.UpdateNUidR.citizenshipData,
        primaryLanguagesData: state.UpdateNUidR.primaryLanguagesData,
        error: state.UpdateNUidR.error,
        userId: state.UpdateNUidR.userId,
        status: state.UpdateNUidR.status,
        result: state.UpdateNUidR.result
    };
}


const mapDispatchToProps = (dispatch) =>{
    return{
        getUserInfo : (name, value) => dispatch(updateNUidActionCreators.getUserInfo(name, value)),
        getUserById: (id) => dispatch(updateNUidActionCreators.getUserById(id)), 
        getMaritalStatus : () => dispatch(updateNUidActionCreators.getMaritalStatus()),
        getCitizenShip : () => dispatch(updateNUidActionCreators.getCitizenShip()),
        getPrimaryLanguages : () => dispatch(updateNUidActionCreators.getPrimaryLanguages()),
        updateContact: (userId,citizenship,phone,landline,caddress,maritalstatus,passportnumber,passportfile)=> dispatch(updateNUidActionCreators.updateContact(userId,citizenship,phone,landline,caddress,maritalstatus,passportnumber,passportfile))
    };
}
//*{ maritalStatusData.map(fieldName=>{ return(<option  value={fieldName}>{fieldName}</option>)})  }*/}
// { citizenShipData.map(fieldName=>{ return(<option  value={fieldName}>{fieldName}</option>)})  }
export default connect(mapStateToProps, mapDispatchToProps)(PersonalInformationForm)
