import React, { useEffect } from 'react'
import { Grid, makeStyles } from '@material-ui/core';
import GreyBox from '../../UI/GreyBox/GreyBox';
import PersonalInformationForm from './PersonalInformationForm';
import { useState } from 'react';
import Backdrop from '../../UI/Backdrop/Backdrop';
import SmileImg from '../../assets/images/Smile.png';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import HomeHeader from '../../components/HomeHeader/HomeHeader';
import Footer from '../../components/Footer/Footer';

const useStyle = makeStyles(theme =>({

    Container0:{
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        width: '100vw',
        height: '100%',
        padding : '14vh 0'
    },
    Container: {
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'center',
        width: '100%'
    },
    Title:{
        marginTop: '2vw',
        fontWeight: '500',
        letterSpacing: '1px',
        fontSize: '25px',
        color: '#A91B0D'
    },
    GreyBoxContainer:{
        marginTop: '2vw'
    }
   
}));

const UpdateNUid = () => {
    useEffect(()=>{
        window.scrollTo(0,0);
    },[])
    const classes = useStyle();
    const [showBackdrop, setShowBackdrop] = useState(false);
    const matches = useMediaQuery('(max-width: 700px)');

    const handleCreatNUIdFormSubmission = (e) =>{
        e.preventDefault();
        setShowBackdrop(true);
    }

    const hideBackdrop = ()=>{
        setShowBackdrop(false);
    }
    return (
        <>
            <HomeHeader LogedIn={true}/>
            <div className={classes.Container0}>
                <div className={classes.Container}>
                    <Backdrop show = {showBackdrop} clicked={hideBackdrop}>
                        <div style={{ background: '#900603', display: 'flex', flexDirection: 'column', color:'white', borderRadius: '30px', alignItems:'center', padding:'20px 50px' }}>
                            <img src={SmileImg} alt="successful_creation" width='40px' />
                            <p style={{ lineHeight: '1.5', marginTop: '10px', textAlign: 'center' }}>
                                Dear user, your NUId have been <br /> successfully registered, you can <br /> use it across all NU platforms 
                            </p>
                        </div>
                    </Backdrop>
                    <Grid container  direction='column' alignItems='center' justifyContent='center'>
                        <Grid xs={12} item>
                            <h1 className={classes.Title}>UPDATE YOUR NUId</h1>
                        </Grid>
                        <Grid item container xs={12} className={classes.GreyBoxContainer}>
                            <Grid item xs={1} md={2}></Grid>
                            <Grid item container xs={10} md={8} >
                                <GreyBox width={matches ? '100%' : '90%'}>
                                    <PersonalInformationForm  handleSubmission = {handleCreatNUIdFormSubmission}/>
                                    
                                </GreyBox>
                            </Grid>
                            <Grid item xs={1} md={8}></Grid>
                        </Grid>
                    </Grid>
                </div>
            </div>
            <Footer />
        </>
    )
}
  
export default UpdateNUid;