import axios from 'axios';
import * as actionTypes from '../actions/actions';
import {digid , lms} from './Constants'
export const getPOSInfo = (name, value) => {
    
    return{
        type: actionTypes.PlanOfStudy.GET_USER_INFO,
        name: name,
        value: value
    }
}

export const getGpa = (studentId,majorId) => {
    return dispatch =>{
        dispatch(planofstudyStart());
        
        var link = lms+'/nuversity/api/lms/studentGlobalGPA/'+studentId+'/'+majorId;
       axios.get(link)
            .then(res=>{
                
                dispatch(getgpa(res.data));
                
                        
            }).catch(err => {
                console.log(err)
            })
       
    }
}

