export const getCreditsAndCost = (coursesOList,courses) => {
    return dispatch =>{ 
        var credits = 0;
        for(let i = 0;i<courses.length;i++){
            for(let j = 0;j<coursesOList.length;j++){
                if(courses[i] == coursesOList[j].course.iid){
                    credits = credits + coursesOList[j].course.courseBean.credit;
                }
            }
        }
        dispatch(getCreditsAndCostData(credits));
    }}

    export const getCreditsAndCostData = (data) => {

        return{
            type: actionTypes.PlanOfStudy.GET_CREDITS_AND_COST,
            cdata: data
        }
    }

export const getCourseByCourseOffering = (coursesOList,courses) => {
    return dispatch =>{ 
        var response = [];
        for(let i = 0;i<courses.length;i++){
            for(let j = 0;j<coursesOList.length;j++){
                if(courses[i] == coursesOList[j].course.iid){
                    response.push(coursesOList[j])
                }
            }
        }
        dispatch(getcourseofferbyoffering(response));
        }
    } 

