export const registerCourses = (courses,studentId,majorId) => {
    
    return dispatch =>{
        dispatch(planofstudyStart());
        const params = {
            "courses": courses,
            "studentId": studentId,
            "majorId": majorId
        }
                                                                                      
              axios.post(lms+"/nuversity/api/lms/registerCourses",params,{ headers: { "Content-Type": "application/json", }, })


                .then(res=>{
                    
                    dispatch(planofstudyEnd(res.data));
                        
                        }).catch(err => {
                            dispatch(planofstudyFail(err));
                        })
                }
}

