import React, { useEffect } from 'react'
import HomeHeader from '../../components/HomeHeader/HomeHeader'
import ImageBackground from '../../components/ImageBackground/ImageBackground'
import Bg1 from '../../assets/images/MoreMountainsMorePeaks.jpeg'
import { useMediaQuery } from '@material-ui/core'
import Footer from '../../components/Footer/Footer'
import Announcement from '../../components/StudentPortalComponents/Announcements/Announcements'

const Home = () => {
    window.localStorage.clear();
    const ToolBarHeight= useMediaQuery('(min-width: 600px)')

    useEffect(()=>{
        window.scrollTo(0,0);
    },[])
    return (
        <>
            <HomeHeader />
            <div style={{ paddingTop: ToolBarHeight ? '64px' : '48px' }}>
                <ImageBackground bg={Bg1} />
                <Announcement />
            </div>
            <Footer />
        </>
    )
}

export default Home
