import axios from 'axios';
import * as actionTypes from '../actions/actions';
import {digid , lms} from './Constants'
export const getDueAmount = (stdId,major) => {
    return dispatch =>{
        
        
        var link = lms+'/nuversity/api/lms/getStudentAmount/'+stdId+'/'+major;
       axios.get(link)
            .then(res=>{
                
                dispatch(getDueAmountData(res.data));
                
                        
            }).catch(err => {
                console.log(err)
            })
       
    }
}

export const getDueAmountData = (data) => {

    return{
        type: actionTypes.StudentPortal.GET_DUE_AMOUNT,
        adata: data
    }
}


