export const submitApplication = (userId,major) => {
    
    return dispatch =>{
        dispatch(submitApplicationStart());
        
        const params = {
            "userid": userId,
            "major": major, //studentId,
            
        }
        
            axios.post(lms+"/nuversity/api/lms/applicatntwithapplication",params,{ headers: { "Content-Type": "application/json", }, })

                .then(res=>{
                    console.log(res.data);
                    dispatch(submitApplicationEnd(res.data));
                         
                         
                        }).catch(err => {
                            dispatch(submitApplicationFail(err));
                        })                                                                                                                                        
                }
}

export const submitApplicationStart = () => {

    return{
        type: actionTypes.ApplyApplication.SUBMIT_APPLICATION_START
        
    }
}

export const submitApplicationEnd = (data) => {

    return{
        type: actionTypes.ApplyApplication.SUBMIT_APPLICATION_END,
        cdata: data
    }
}

export const submitApplicationFail = (err) => {

    return{
        type: actionTypes.ApplyApplication.SUBMIT_APPLICATION_FAIL,
        error: err
    }
}


export const submitApplicationDoc = (docs,docName,application) => {
    
    return dispatch =>{
        
        var FD = new FormData();
        for(let i=0 ; i< docs.length;i++){
            FD.append("docs",docs[i]);
        }
        // FD.append("docs", docs);
        FD.append("docName", docName);
        FD.append("application", application);
        
        

      
            // console.log(FD)
            axios(
                {
                    method: "post",
                    url: lms+"/nuversity/api/lms/addApplicationDocument",
                    data: FD,
                    headers: { "Content-Type": "multipart/form-data" },
                }
                

            )

                .then(res=>{
                    
                    dispatch(submitDocEnd(res.data));
                        
                        
                        }).catch(err => {
                            dispatch(submitDocFail(err));
                        })
                }
}


export const submitDocFail = (err) => {

    return{
        type: actionTypes.ApplyApplication.SUBMIT_APPLICATION_DOC_FAIL,
        error: err
    }
}

export const submitDocEnd = (data) => {

    return{
        type: actionTypes.ApplyApplication.SUBMIT_APPLICATION_DOC_END,
        cdata: data
    }
}

