import Grid from "@material-ui/core/Grid";
import HomeHeader from "../../components/HomeHeader/HomeHeader";
import LogedInHeader from "../../components/LogedInHeader/LogedInHeader";
import classes from "./AdminAcceptEnrolment.module.css";
import { Button, CircularProgress, Container } from "@material-ui/core";
import BoxWithTitle from "../../components/BoxWithTitle/BoxWithTitle";
import Footer from "../../components/Footer/Footer";
import Receipt from "../../components/AcceptEnrolmentComponents/AcceptEnrolmentTable";
import * as AcceptEnrolmentActionCreators from '../../store/actionsCreators/AcceptEnrolmentAction';
import { connect } from "react-redux";
import { useEffect } from "react";


function AdminAcceptEnrolment({getPendingEnrolment,students,acceptEnrolmentC}) {

  useEffect(()=>{
    window.scrollTo(0,0);
},[])

  useEffect(() => {
    getPendingEnrolment();
 
  }, []);

 
const acceptStudent=(student,major,course)=>{
  acceptEnrolmentC(student,major,course)
}



  return (
    <>
      <HomeHeader/>
      {/* <LogedInHeader /> */}
      <div className={classes.divSpace}></div>
      <div className={classes.checkAccount}>
        <Container>
          <BoxWithTitle title="Accept Enrolment">
            <Grid container spacing={3}>
              
                 { 
                   students ?  <Receipt tableInfo={students} acceptStudent={acceptStudent}/> :<></>
                  }
               
              <Grid item xs={12} md={3} sm={11}>
              </Grid>
              <Grid item xs={12} md={3} sm={1}>
              </Grid>
            </Grid>
          </BoxWithTitle>
        </Container>
      </div>
      <Footer />
    </>
  );
}

const mapStateToProps = (state) =>{
  return{
    students: state.AcceptEnrolmentR.students,
    
  };
}

const mapDispatchToProps = (dispatch) =>{
  return{
    getPendingEnrolment : () => dispatch(AcceptEnrolmentActionCreators.getPendingEnrolment()), 
    acceptEnrolmentC: (student,major,course) => dispatch(AcceptEnrolmentActionCreators.acceptEnrolmentC(student,major,course)),
  
    };
}

export default connect(mapStateToProps, mapDispatchToProps)(AdminAcceptEnrolment);
