import classes from './Contains.module.css';
import img from "../../../assets/images/TypeArrow.png";
import Grid from "@material-ui/core/Grid";

const Contains = (props) => {
    const title = props.title;
    const text = props.text;
    const creditnumber = props.creditnumber;
    const Type = props.Type;
    const prob = props.prob;
    const image = props.image;
    return ( 
        <div className={classes.container}>
            <Grid container spacing={3}>
        <Grid item xs={12} md={6} sm={6}>
            <div className={classes.cont1}>
                <h2>{title}</h2>
                <p className={classes.text}>{text}</p>
                <div className={classes.im}><img src={img} /></div>
                <div className={classes.probation}><img src={image} />{prob}</div>
            </div></Grid>
            <Grid item xs={12} md={6} sm={6}>
            <div className={classes.cont2}>
                <p><span className={classes.number}>{creditnumber}</span><span className={classes.type}>{Type}</span></p>
            </div>
            </Grid>
            </Grid>
        </div>
     );
}
 
export default Contains;