import React, { useState } from "react";
import classes from "./AcceptEnrolmentTable.module.css";
import ReceiptTable from "./AcceptEnrolmentTable/AcceptEnrTable";
import Grid from "@material-ui/core/Grid";

const AcceptStudentTable = ({tableInfo,acceptStudent}) => {

  return (
    <div className={classes.Receipts}>
      <div className={classes.Body}>
        <Grid container spacing={3}>
          <Grid item xs={12} md={6} sm={12}>
            <div className={classes.CoursesName}>
              
              <ReceiptTable table={tableInfo} acceptStudent={acceptStudent}/>
              
            </div>
          </Grid>
        </Grid>
      </div>
    </div>
  );
};

export default AcceptStudentTable;
