import React, { useState } from 'react'
import classes from './AcceptEnrolmentRow.module.css'
const AcceptStdTableRow = ({student, major, course, accept,acceptStudent}) => {
    const [acceptEnrolmentButton,setScceptEnrolmentButton] = useState("Accept");
   
    const acceptStd =()=>{
        acceptStudent(student,accept.majorId,accept.enrolmentId);
        // setScceptEnrolmentButton("Loading...");
    }
    return (
        <div className={classes.ReceiptRow}>
            <div>
                {student}
            </div>
            <div>
                {major}
            </div>
            <div>
                {course}
            </div>
            <div>
           
               <button onClick={acceptStd} className={classes.acceptButton}>{acceptEnrolmentButton}</button>
                
               
            </div>
        </div>
    )
}

export default AcceptStdTableRow
