import Grid from "@material-ui/core/Grid";
import HomeHeader from "../../components/HomeHeader/HomeHeader";
import LogedInHeader from "../../components/LogedInHeader/LogedInHeader";
import classes from "./AdminAcceptStudent.module.css";
import { Button, CircularProgress, Container } from "@material-ui/core";
import BoxWithTitle from "../../components/BoxWithTitle/BoxWithTitle";
import Footer from "../../components/Footer/Footer";
import Receipt from "../../components/AcceptStudentComponents/AcceptStudentTable";
import * as AcceptStudentActionCreators from '../../store/actionsCreators/AcceptStudentAction';
import { connect } from "react-redux";
import { useEffect } from "react";


function AdminAcceptStudent({getPendingStudents,students,acceptStudentReg,year,semester,allSemester}) {

  useEffect(()=>{
    window.scrollTo(0,0);
},[])

  useEffect(() => {
    getPendingStudents();
 
  }, []);


 
const acceptStudent=(student,major,campus)=>{
    acceptStudentReg(student,major,campus)
}



  return (
    <>
      <HomeHeader/>
      {/* <LogedInHeader /> */}
      <div className={classes.divSpace}></div>
      <div className={classes.checkAccount}>
        <Container>
          <BoxWithTitle title="Accept Student">
            <Grid container spacing={3}>
              
                 { 
                   students ?  <Receipt tableInfo={students} acceptStudent={acceptStudent} /> :<></>
                  }
               
              <Grid item xs={12} md={3} sm={11}>
              </Grid>
              <Grid item xs={12} md={3} sm={1}>
                  {/* <Button variant="contained" color="primary" onClick = {getCheckInformation}> */}
                  {/* { 
                        loading ? <CircularProgress style={{color:'#fff'}} size={35} /> : <h2>Print</h2>
                  } */}
                    {/* </Button> */}
              </Grid>
            </Grid>
          </BoxWithTitle>
        </Container>
      </div>
      <Footer />
    </>
  );
}

const mapStateToProps = (state) =>{
  return{
    students: state.AcceptStudentR.students,
    year: state.AcceptStudentR.year,
    semester: state.AcceptStudentR.semester,
    allSemester: state.AcceptStudentR.allSemester
    
  };
}

const mapDispatchToProps = (dispatch) =>{
  return{
    getPendingStudents : () => dispatch(AcceptStudentActionCreators.getPendingStudents()), 
    acceptStudentReg: (student,major,campus) => dispatch(AcceptStudentActionCreators.acceptStudentReg(student,major,campus)),
    };
}

export default connect(mapStateToProps, mapDispatchToProps)(AdminAcceptStudent);
