import React from 'react';
import SwipeableViews from 'react-swipeable-views';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Typography from '@material-ui/core/Typography';
import Box from '@material-ui/core/Box';
import BsC from './BsC/BsC';
import Master from './Master/Master';
import Phd from './Phd/Phd';

function TabPanel(props) {
    const { children, value, index, ...other } = props;

    return (
        <div
        role="tabpanel"
        hidden={value !== index}
        id={`full-width-tabpanel-${index}`}
        aria-labelledby={`full-width-tab-${index}`}
        {...other}
        >
        {value === index && (
            <Box p={3}>
                <Typography component='div'>{children}</Typography>
            </Box>
        )}
        </div>
    );

}

function a11yProps(index) {
    return {
    id: `full-width-tab-${index}`,
    'aria-controls': `full-width-tabpanel-${index}`,
    };
}

const useStyles = makeStyles((theme) => ({
    root: {
    backgroundColor: theme.palette.background.paper,
    width: '100%',
    },
    ActiveTab: {
        background: 'linear-gradient(to right, #900603, #E3242B, #BC544B, #A91B0D)',
        color: '#fff'
    }
}));

export default function Major({majorType,majors,getInfoSP,majorsMaster,majorsPHD,selectedMajor,prerequisites}) {
    const classes = useStyles();
    const theme = useTheme();
    const [value, setValue] = React.useState(0);

    const handleChange = (event, newValue) => {
    setValue(newValue);
    };

    const handleChangeIndex = (index) => {
    setValue(index);
    };

    const setMajorType = (type,value)=>{
        getInfoSP(type,value);
    }

    return (
    <div className={classes.root}>
        <AppBar position="static" color="default">
        <Tabs
            value={value}
            onChange={handleChange}
            variant="fullWidth"
            aria-label="tabs"
           indicatorColor='primary'
           style={{ 
               color: '#900603',
               fontWeight: '900'
            }}
        >
            <Tab  
            classes={{ 
                selected: classes.ActiveTab
            }}
            label="BsC"
            {...a11yProps(0)}
             />

            <Tab 
            classes={{ 
                selected: classes.ActiveTab
            }}
            label="master" 
            {...a11yProps(1)} />

            <Tab 
            classes={{ 
                selected: classes.ActiveTab
            }}
            label="phd" 
            {...a11yProps(2)} />

        </Tabs>
        </AppBar>
        <SwipeableViews
            axis={theme.direction === 'rtl' ? 'x-reverse' : 'x'}
            index={value}
            onChangeIndex={handleChangeIndex}
        >
        <TabPanel value={value} index={0} dir={theme.direction} >
             {
                 setMajorType('majorType','BSC')
             }   
            {
              majors ?  <BsC majors={majors} setMajorType={setMajorType} selectedMajor={selectedMajor} prerequisites={prerequisites}/> : <></>
            }            
            
        </TabPanel>
        <TabPanel value={value} index={1} dir={theme.direction}>
            
             
               
               {
              majorsMaster ?  <Master majors={majorsMaster} setMajorType={setMajorType} selectedMajor={selectedMajor} prerequisites={prerequisites}/>  : <></>
            }
            

        </TabPanel>
        <TabPanel value={value} index={2} dir={theme.direction}>
       
        {
              majorsPHD ?  <Phd majors={majorsPHD} setMajorType={setMajorType} selectedMajor={selectedMajor} prerequisites={prerequisites}/>  : <></>
            }
             
                 
            
            
        </TabPanel>
        </SwipeableViews>
    </div>
    );
}
