import axios from 'axios';
import * as actionTypes from '../actions/actions';
import {digid , lms} from './Constants'


export const getLoginInfo = (name, value) => {
    return{
        type: actionTypes.Login.LOGIN,
        name: name,
        value: value
    }
}


export const submitLogin = (email,password) => {
    
    return dispatch =>{
        dispatch(loginStart());        
        var link = digid+'/nuversity/api/digid/login?email='+email+'&pwd='+password;
       axios.get(link)
            .then(res=>{
                if(res.data.status === 'UnregisteredEmail'){
                    dispatch(loginFail("Unregistered email"));
                    // throw new Error('Unregistered Email');
                }else if(res.data.status === 'BlockedAccount'){
                    // throw new Error('Account Blocked');
                    dispatch(loginFail("Account Blocked"));
                }else if(res.data.status === 'WrongPassword'){
                    // throw new Error('Wrong Password');
                    dispatch(loginFail("Wrong Password"));
                }else if(res.data.status === 'ExpiredPassword'){
                    // throw new Error('Password Expired');
                    dispatch(loginFail("Password Expired"));
                }else{
                
                    dispatch(callApp(res.data));
                    dispatch(loginEnd(res.data));
                }
                        
            }).catch(err => {
                dispatch(loginFail(err));
            })
        
    }
}

 
export const callApp = (dataI) =>{

    return dispatch =>{
        // dispatch(loginStart());
        // console.log(email  ,password)
        
        var link = lms+'/nuversity/api/lms/applicationAndApplicantByUserId/'+dataI.userId;
       axios.get(link)
            .then(res=>{
                   
                dispatch(loginGetAppData(res.data));

                        
            }).catch(err => {
                dispatch(loginDataApp(err));
            })
        }
    }

export const loginDataApp = (err) =>{
    
    return{
        type: actionTypes.Login.LOGIN_GET_APP_DATA_ERROR,
        error: err
    }
}

export const loginStart = () => {

    return{
        type: actionTypes.Login.LOGIN_START
    }
}

export const loginEnd = (data) => {
       
    return{
        type: actionTypes.Login.LOGIN_END,
        userData: data
    }
}

export const loginFail = (err) => {

    return{
        type: actionTypes.Login.LOGIN_FAIL,
        error: err
    }
}

export const loginGetAppData = (data) => {
    return{
        type: actionTypes.Login.LOGIN_GET_APP_DATA,
        dataApp: data
    }
}
