import React from 'react'
import classes from './LogedInHeader.module.css'
import WelcomeImg from '../../assets/images/Welcome.png'
import LogoutImg from '../../assets/images/Logout.png'
import { Toolbar } from '@material-ui/core'
import { Redirect, useHistory } from 'react-router'
const LogedInHeader = ({firstName=localStorage.getItem('name'), lastName='', id=localStorage.getItem('userId'), appId=localStorage.getItem('application')}) => {
    
    const history = useHistory();
    const logout = () =>{
        let path ="/Login";
        history.push(path)
        console.log("clicked");
        // <Redirect to='/Login'/>
    }
    return (
        <div className={classes.LogedInHeader}>
             <Toolbar className={classes.Toolbar}>
                <div className={classes.Grow}>
                    <div> 
                        <span>
                            <img src={WelcomeImg} alt="" />
                        </span>
                        <span>    
                            <span>{firstName}</span><span>{lastName}</span>
                        </span>
                    </div>
                   <div>
                        <span>ID: {id}</span><span> | </span> <span>Application ID: {appId}</span>  
                   </div>
                </div>
                <div className={classes.rightSection}>
                    <div>
                        <span>ID: {id}</span><span> | </span> <span>Application ID: {appId}</span>
                    </div>
                    {/* <div>
                        <img src={LogoutImg} alt="" onClick={()=>logout()}/>
                    </div> */}
                </div>
             </Toolbar>
        </div>
    )
}

export default LogedInHeader
