import React, { useState } from 'react'
import ButtonSection from '../ButtonSection/ButtonSection'
import BoxInfo from './BoxInfo/BoxInfo'
import classes from './StudyYear.module.css'
const StudyYear = ({StudyYears,totalCrd,crdPerYear}) => {

    const [showStudyYear, setShowStudyYear] = useState(false)

    const toggleStudyYear = () =>{
        setShowStudyYear(!showStudyYear)
    }

    return (
        <div className={classes.StudyYear}>
            <ButtonSection Text='study years' clicked={toggleStudyYear} toggleValue={showStudyYear} />
            {
                showStudyYear ? 
                <div className={classes.Body}>
                    <BoxInfo title='study duration' number={StudyYears} text='years minimum' />
                    <BoxInfo title='number of credits per year' number={crdPerYear} text='credits' />
                    <BoxInfo title='total credits' number={totalCrd} text='credits' />
                </div> :
                null
            }
        </div>
    )
}

export default StudyYear
