import * as actionTypes from '../actions/actions';

const initialState = {
    loading: false,
    programs: '',
    selectedProgram: '',
    majors: '',
    majorsMaster: '',
    majorsPHD: '',
    selectedMajor: '',
    prerequisites: '',
    studyYears: '',
    totalCrd: '',
    crdPerYear: '',
    coreCrd: '',
    generalCrd: '',
    electiveCrd: '',
    majorCrd: '',
    totalCrdSheet: '',
    coursesList: '',
    coursesTypeS: '',
    requiredDocument: '',
    docList: [],
    docListName: [],
    application: '',
    applicant: '',
    userId: '',
    error: '',
    docsResponse: ''
    
}

const ApplyApplicationR =  (state = initialState, action) => {
    switch (action.type) {

        case actionTypes.ApplyApplication.GET_INFO :
            return { ...state, [action.name] : action.value  }

        case actionTypes.ApplyApplication.GET_PROGRAMS :
            return { ...state,loading: false,programs: action.PGMData}
        
        case actionTypes.ApplyApplication.GET_MAJORS :
            return { ...state,loading: false,majors: action.MajorData}

        case actionTypes.ApplyApplication.GET_MAJORS_MASTER :
            return { ...state,loading: false,majorsMaster: action.MajorData}

        case actionTypes.ApplyApplication.GET_MAJORS_PHD :
            return { ...state,loading: false,majorsPHD: action.MajorData}

        case actionTypes.ApplyApplication.GET_MAJORS_PRERE :
            return { ...state,loading: false,prerequisites: action.Prere.prerequisites}

        case actionTypes.ApplyApplication.GET_MAJORS_YEARS:
            return { ...state,loading: false,studyYears: action.Syears.myears,totalCrd: action.Syears.creditNumber,crdPerYear: action.Syears.creditNumber/3}
            
        case actionTypes.ApplyApplication.GET_MAJOR_CONTRACT_SHEET :
            return { ...state,loading: false,coreCrd: action.cdata.core , majorCrd:action.cdata.major , generalCrd: action.cdata.general, electiveCrd: action.cdata.elective , totalCrdSheet: action.cdata.core + action.cdata.general + action.cdata.major + action.cdata.elective  }
         
        case actionTypes.ApplyApplication.GET_COURSES:
            return { ...state,loading: false,coursesList: action.cdata}
        
        case actionTypes.ApplyApplication.GET_REQUIRED_DOC:
            return { ...state,loading: false,requiredDocument: action.cdata}

        case actionTypes.ApplyApplication.SUBMIT_APPLICATION_START:
            return { ...state,loading: true}

        case actionTypes.ApplyApplication.SUBMIT_APPLICATION_END:
            return { ...state,applicant: action.cdata.applicant, application: action.cdata.application, userId: action.cdata.userid}

        case actionTypes.ApplyApplication.SUBMIT_APPLICATION_FAIL:
            return { ...state,loading: false, error: action.error}

        case actionTypes.ApplyApplication.SUBMIT_APPLICATION_DOC_FAIL:
            return { ...state,loading: false, error: action.error}

        case actionTypes.ApplyApplication.SUBMIT_APPLICATION_DOC_END:
            return { ...state,loading: false, docsResponse: action.cdata}
        default:
            return state
    }
}

export default ApplyApplicationR;
