import axios from 'axios';
import * as actionTypes from '../actions/actions';
import {digid , lms} from './Constants'

export const getFPInfo = (name, value) => {
    return{
        type: actionTypes.ChangeForgottenPassword.GET_FP_INFO,
        name: name,
        value: value
    }
}

export const onLoadForgetPassword = (token) => {
    
    return{
        type: actionTypes.ChangeForgottenPassword.GET_ONLOADFP_INFO,
        data: token
    }
}

export const getUserByToken = (token) => {
    return dispatch =>{
       
      
        var link = digid+'/nuversity/api/digid/userByToken/'+token;
       axios.get(link)
            .then(res=>{
                
                dispatch(getUBTInfo(res.data));
                
                        
            }).catch(err => {
                console.log(err)
            })
      
    }
}

export const ChangeForgottenPassword = (email,token,npassword,conewpassword) => {
    
    return dispatch =>{
        dispatch(ChangeForgottenPasswordStart());
        const params = {"email":email,
                        "password":npassword,
                        "confirmPassword":conewpassword,
                        "token":token}
        
            axios.post(digid+"/nuversity/api/digid/changeforgottenPassword",params,{ headers: { "Content-Type": "application/json", }, })

                .then(res=>{
                    
                    dispatch(ChangeForgottenPasswordEnd(res.data));
                         
                         
                        }).catch(err => {
                            dispatch(ChangeForgottenPasswordFail(err));
                        })                                                                                                                                        
                }
}


export const ChangeForgottenPasswordStart = () => {

    return{
        type: actionTypes.ChangeForgottenPassword.CHANGE_FORGOTTEN_PASSWORD_START
    }

}



export const getUBTInfo = (data) => {

    return{
        type: actionTypes.ChangeForgottenPassword.GET_USER_BY_TOKEN,
        cdata: data
    }
}


export const ChangeForgottenPasswordEnd = (data) => {

    return{
        type: actionTypes.ChangeForgottenPassword.CHANGE_FORGOTTEN_PASSWORD_END,
        cdata: data
    }
}

export const ChangeForgottenPasswordFail = (err) => {

    return{
        type: actionTypes.ChangeForgottenPassword.CHANGE_FORGOTTEN_PASSWORD_FAIL,
        error: err
    }
}
