import * as actionTypes from '../actions/actions';

const initialState = {
    from: '',
    to: '',
    asof: '',
    account: '',
    balance: '',
    loading: false,
    checkTable: '',
    error: ''
}

const CheckAccountR = (state = initialState, action) =>{
    switch (action.type) {

        case actionTypes.CheckAccount.GET_USER_INFO :
            return { ...state, [action.name] : action.value}
        
        case actionTypes.CheckAccount.GET_INFORMATION :
            return { ...state,asof: action.userData.asof,account: action.userData.account,balance: action.userData.amount}
            
        case actionTypes.CheckAccount.GET_CHECK_INFOS_START :
            return { ...state,loading: true} 

        case actionTypes.CheckAccount.GET_CHECK_INFOS_END :
            return { ...state,loading: false,checkTable: action.userData.operations} 
        case actionTypes.CheckAccount.GET_CHECK_INFOS_FAIL :
            return { ...state,loading: false,error: action.userData}     
        default:
            return state            
}
}

export default CheckAccountR;