import { useEffect } from 'react';
import Schedule from '../Schedule/Schedule';
import classes from './ScheduleBox.module.css';

const ScheduleBox = ({Name,Monday,Tuesday, Wednesday, Thursday, Friday, Saturday, Sunday,getCourseOfferSchedulePopup,selectedPop}) => {
    useEffect(()=>{
        getCourseOfferSchedulePopup(selectedPop);
    })
    return ( 
        <div className={classes.container}>
            <h3 className={classes.title}>Schedule</h3>
            <div className={classes.subContainer}>
               {

               Monday !== '' || Tuesday !=='' || Wednesday!== '' || Thursday !== '' || Friday !== '' || Saturday !== ''? <Schedule courName={Name} Monday={Monday} Tuesday={Tuesday} Wednesday={Wednesday} Thursday={Thursday} Friday={Friday} Saturday={Saturday}/>:<></>
               }
                </div>
        </div>
     );
}
 
export default ScheduleBox;
