import React from "react";
import classes from "./ResetPassword.module.css";
import {
  Button,
  CircularProgress,
  InputAdornment,
  TextField,
} from "@material-ui/core";
import { Link } from "react-router-dom";
import { AccountCircle, Lock } from "@material-ui/icons";
import HomeHeader from "../../components/HomeHeader/HomeHeader";
import Footer from "../../components/Footer/Footer";
import { useEffect,useState } from "react";
import Backdrop from '@material-ui/core/Backdrop';
import { makeStyles } from '@material-ui/core/styles';
import * as ResetPasswordCreators from "../../store/actionsCreators/ResetPasswordAction";
import { connect } from "react-redux";



const useStyles = makeStyles((theme) => ({
    backdrop: {
      zIndex: theme.zIndex.drawer + 1,
      color: '#fff',
    },
  }));

const ResetPassword = ({loading,email,oldPassword,newPassword,onLoadResetPassword,retryNewPassword,result,error,resetPassword,getRPInfo}) => {

  useEffect(()=>{
    window.scrollTo(0,0);
},[])
  const classesMakeStyle = useStyles();

    const [openBackdrop, setOpenBackdrop] = React.useState(false);
   


    useEffect(() => {
      
      onLoadResetPassword(localStorage.getItem('userId'));
      // getRPInfo("email",localStorage.getItem('userId'));
    }, []);

    useEffect(() => {
      if(result !== ''){
      setOpenBackdrop(true);
      }
    }, [result]);

    const handleClose = () =>{
      setOpenBackdrop(false);
  }

  const handleOnChange = (e)=>{
    getRPInfo(e.target.name,e.target.value);
  }


  const submitForm =(e) =>{
    resetPassword(email,oldPassword,newPassword,retryNewPassword);
  }
  return (
    <>
    <Backdrop className={classesMakeStyle.backdrop} open={openBackdrop} onClick={handleClose}>
                <div style={{ color: '#fff' }}>
                    {
                        
                        <div className={classes.textBoxApplication}>
                            {
                             result
                            }
                         </div>

                    }
                </div>    
        </Backdrop>
        <HomeHeader LogedIn={true} />
      <div className={classes.Login}>
        <div className={classes.LoginContainer}>
          <div className={classes.LoginContainerForm}>
            
              <h1>Reset Password</h1>
              <br />

              <TextField
                label="NUid/NUmail"
                name="email"
                value={email}
                fullWidth
                onChange={handleOnChange}
                variant="outlined"
                style={{ margin: "20px 0px" }}
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <AccountCircle
                        style={{ color: "#900603" }}
                        fontSize="large"
                      />
                    </InputAdornment>
                  ),
                }}
              />

              <TextField
                label="Old Password"
                name="oldPassword"
                fullWidth
                value={oldPassword}
                onChange={handleOnChange}
                style={{ margin: "0px 0px 20px 0px" }}
                variant="outlined"
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <Lock style={{ color: "#900603" }} fontSize="large" />
                    </InputAdornment>
                  ),
                  type: "password",
                }}
              />

              <TextField
                label="New Password"
                name="newPassword"
                fullWidth
                value={newPassword}
                onChange={handleOnChange}
                style={{ margin: "0px 0px 20px 0px" }}
                variant="outlined"
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <Lock style={{ color: "#900603" }} fontSize="large" />
                    </InputAdornment>
                  ),
                  type: "password",
                }}
              />
              <TextField
                label="Confirm Password"
                name="retryNewPassword"
                fullWidth
                value={retryNewPassword}
                onChange={handleOnChange}
                style={{ margin: "0px 0px 20px 0px" }}
                variant="outlined"
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <Lock style={{ color: "#900603" }} fontSize="large" />
                    </InputAdornment>
                  ),
                  type: "password",
                }}
              />

              {/* <input type="submit" value={<CircularProgress color="secondary" />} className={classes.SubmitBtn} /> */}
              <Button
                style={{ padding: "10px" }}
                variant="contained"
                fullWidth
                color="primary"
                onClick={submitForm}
              >

                {
                  loading ? <CircularProgress style={{ color: "#fff" }} size={35} />: <h2>Reset Password</h2>
                }
                
              </Button>
            
          </div>
        </div>
      </div>
      <Footer />
    </>
  );
};

const mapStateToProps = (state) => {
  return {
     email: state.ResetPasswordR.email,
     loading: state.ResetPasswordR.loading,
     error: state.ResetPasswordR.error,
     result: state.ResetPasswordR.result,
     oldPassword: state.ResetPasswordR.oldPassword,
     newPassword: state.ResetPasswordR.newPassword,
     retryNewPassword: state.ResetPasswordR.retryNewPassword,
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
      getRPInfo: (name,value) => dispatch(ResetPasswordCreators.getRPInfo(name,value)),
      onLoadResetPassword: (token) => dispatch(ResetPasswordCreators.onLoadResetPassword(token)),
      resetPassword: (email,opass,npass,rnpass) => dispatch(ResetPasswordCreators.resetPassword(email,opass,npass,rnpass)),
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(ResetPassword);
