import React from 'react'

const ButtonSection = ({Text, clicked, toggleValue}) => {
    
    return (
        <div 
        style={{ 
            width: '100%',
            padding: '10px',
            textAlign: 'center',
            background: toggleValue ? 'linear-gradient(to right, #900603, #E3242B, #BC544B, #A91B0D)' : '#ccc',
            color: toggleValue ? '#fff' : '#58595B',
            letterSpacing: '1.5px',
            textTransform: 'uppercase',
            cursor: 'pointer'
         }}
        onClick={clicked}>
            {Text}
        </div>
    )

}

export default ButtonSection
