import RequiredCredits from './RequiredCredits/RequiredCredits';
import classes from './MaxAndMinCredits.module.css';

const MaxAndMinCredits = () => {
    return ( 
        <div className={classes.container}>
                <RequiredCredits requiredCredits="12" text="Min Required Credit" />
                <RequiredCredits requiredCredits="22" text="Max Required Credit" />
        </div>
     );
}
 
export default MaxAndMinCredits;
