import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import { BrowserRouter } from 'react-router-dom';
import { createTheme, ThemeProvider } from '@material-ui/core';
import { createStore, applyMiddleware, compose, combineReducers } from 'redux';
import { Provider } from 'react-redux';
import CreateNUidR from './store/reducers/CreateNUidReducer';
import thunk from 'redux-thunk';
import LoginR from './store/reducers/LoginReducer';
import PaymentR from './store/reducers/PaymentReducer';
import PlanOfStudyR from './store/reducers/PlanOfStudyReducer'
import CheckAccountR from './store/reducers/CheckAccountReducer'
import CurrentSemesterR from './store/reducers/CurrentSemesterReducer';
import UpdateNUidR from './store/reducers/UpdateNUidReducer';
import SetPasswordR from './store/reducers/SetPasswordReducer';
import ApplyApplicationR from './store/reducers/ApplyApplicationReducer';
import ForgetPasswordR from './store/reducers/ForgetPasswordReducer';
import ResetPasswordR from './store/reducers/ResetPasswordReducer';
import ChangeForgottenPasswordR from './store/reducers/ChangeForgottenPasswordReducer';
import HistoryR from './store/reducers/HistoryReducer';
import StudentPortalR from './store/reducers/StudentPortalReducer';
import CalendarR from './store/reducers/CalendarReducer';
import AcceptStudentR from './store/reducers/AcceptStudentReducer';
import AcceptEnrolmentR from './store/reducers/AcceptEnrolmentReducer';
import SubscribeSemesterR from './store/reducers/SubscribeSemesterReducer';
const theme = createTheme({
  breakpoints:{
      values:{
          sm: 880
      }
  },
  palette:{
    primary:{
        main: '#900603',
        light: '#A91B0D',
    },
    secondary:{
        main: '#3f51b5'
    },
    divider: '#ddd',
    background:{
        default: '#F8F8F8'
    } 
}
});
const composeEnhancers = (process.env.NODE_ENV === 'development' ? window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ : null) || compose;

const reducer = combineReducers(
  {
    CreateNUidR: CreateNUidR,
    LoginR: LoginR,
    PaymentR: PaymentR,
    PlanOfStudyR: PlanOfStudyR,
    CheckAccountR: CheckAccountR,
    CurrentSemesterR: CurrentSemesterR,
    UpdateNUidR: UpdateNUidR,
    SetPasswordR: SetPasswordR,
    ApplyApplicationR: ApplyApplicationR,
    ForgetPasswordR: ForgetPasswordR,
    ResetPasswordR: ResetPasswordR,
    ChangeForgottenPasswordR: ChangeForgottenPasswordR,
    HistoryR: HistoryR,
    StudentPortalR: StudentPortalR,
    CalendarR: CalendarR,
    AcceptStudentR: AcceptStudentR,
    AcceptEnrolmentR: AcceptEnrolmentR,
    SubscribeSemesterR: SubscribeSemesterR
  }
)

const store = createStore(reducer, composeEnhancers(
  applyMiddleware(thunk)
));

ReactDOM.render(
  <BrowserRouter>
    <ThemeProvider theme={theme}>
      <Provider store={store}>
        <App />
      </Provider>
    </ThemeProvider>
  </BrowserRouter>,
  document.getElementById('root')
);
