import React from 'react'
import classes from './CoursesSelectedRow.module.css'
const CoursesSelectedRow = ({CourseName, Category,courses, Teacher, CreditNumber, Campus,selectList,getPOSInfo,courseOffer,getCreditsAndCost,getCourseByCourseOffering,allCourses}) => {


      const removeCourse = () =>{
        for(let i = 0;i<selectList.length;i++){
            if(selectList[i] === courseOffer){
                for(let j = 0;j<allCourses.length;j++){
                    if(allCourses[j].course.iid === courseOffer){
                        var compL = allCourses[j].compList;
                        if(compL.length == 0){
                            var index3 = selectList.indexOf(selectList[i]);
                                                    if(index3 > -1){
                                                      selectList.splice(index3, 1);
                                                        
                                                    }
                                                getPOSInfo('selectList',selectList);
                                                getCourseByCourseOffering(courses,selectList);
                                                getCreditsAndCost(courses,selectList);
                        }
                        for(let k = 0;k<compL.length;k++){
                            if(compL[k].compType === 'CO REQUISITES'){
                                var courseToRemove = compL[k].courseCmp.iid;
                                for(let n = 0;n<allCourses.length;n++){
                                        if(allCourses[n].course.courseBean.iid === courseToRemove){
                                            for(let m = 0;m<selectList.length;m++){
                                                if(selectList[m] === allCourses[n].course.iid){
                                                    var index1 = selectList.indexOf(selectList[m]);
                                                    if(index1 > -1){
                                                      selectList.splice(index1, 1);
                                                        
                                                    }
                                                    getPOSInfo('selectList',selectList);
                                                    getCourseByCourseOffering(courses,selectList);
                                                    getCreditsAndCost(courses,selectList);
                                                }
                                            }
                                        }
                                }
                            }
                        }

                        var index = selectList.indexOf(courseOffer);
                                                    if(index > -1){
                                                      selectList.splice(index, 1);
                                                        
                                                    }
        getPOSInfo('selectList',selectList);
          getCourseByCourseOffering(courses,selectList);
          getCreditsAndCost(courses,selectList);
                    }
                }
             
            }
          }
        //   getPOSInfo('selectList',selectList);
        //   getCourseByCourseOffering(courses,selectList);
        //   getCreditsAndCost(courses,selectList);
      }
    return (
        <div className={classes.CourseRow}>
            <div>
                {CourseName}
            </div>
            {/* <div>
                {Category}
            </div> */}
            <div>
                {Teacher}
            </div>
            <div>
                {CreditNumber}
            </div>
            <div>
                {Campus}
            </div>
            <div>
                <button onClick={removeCourse} className={classes.check}>X</button>
            </div>
        </div>
    )
}

export default CoursesSelectedRow
