import * as actionTypes from '../actions/actions';

const initialState = {
    gpa: '',
    probation: '',
    majorname: '',
    teacherfirstname: '',
    teacherlastname: '',
    contractsheetname: '',
    general: [],
    major: [],
    core: [],
    elective: [],
    coursename: '',
    credit: '',
    coursedescription: '',
    monday: '',
    tuesday: '',
	wednesday: '',
	thursday: '',
	friday: '',
	saturday:'',
	sunday: '',
    campus: '',
    capacity: '',
    userId: '',
    error: '',
    loading: false,
    courses: '',
    courseOfferings: '',
    selectList: [],
    courseType: '',
    coursesS: '',
    credits: '',
    creditCost: 0,
    result: ''
}

const PlanOfStudyR =  (state = initialState, action) => {
    switch (action.type) {

        case actionTypes.PlanOfStudy.GET_USER_INFO :
            return { ...state, [action.name] : action.value  }

        case actionTypes.PlanOfStudy.PLANOFSTUDY_START :
            return { ...state,loading: true}
    
        case actionTypes.PlanOfStudy.PLANOFSTUDY_END :
            return { ...state,loading: false,result: action.userData} 
                 
        case actionTypes.PlanOfStudy.PLANOFSTUDY_FAIL :
            return { ...state,loading: false,error: action.error}
            
        case actionTypes.PlanOfStudy.GET_GPA :
            return { ...state,loading: false,gpa: action.gpadata.gpa}  
            
        case actionTypes.PlanOfStudy.GET_PROBATION_STUDENT :
            return { ...state,loading: false,probation: action.psdata}   

        case actionTypes.PlanOfStudy.GET_MAJOR :
            return { ...state,loading: false,majorname: action.majordata} 

        case actionTypes.PlanOfStudy.GET_TEACHER_INFO :
            return { ...state,loading: false,teacherfirstname: action.teacherdata.contact.firstname,teacherlastname: action.teacherdata.contact.lastname}

        case actionTypes.PlanOfStudy.GET_CONTRACT_SHEET :
            return { ...state,loading: false,contractsheetname: [action.csdata[0].name,action.csdata[1].name,action.csdata[2].name], major: [action.csdata[0].major,action.csdata[1].major,action.csdata[2].major],core: [action.csdata[0].core,action.csdata[1].core,action.csdata[2].core],elective: [action.csdata[0].elective,action.csdata[1].elective,action.csdata[2].elective],general: [action.csdata[0].general,action.csdata[1].general,action.csdata[2].general]}

        case actionTypes.PlanOfStudy.GET_COURSE_OFFER_SCHEDULE_POPUP :
            return { ...state,loading: false,monday: action.cospdata.monday,tuesday: action.cospdata.tuesday,wednesday: action.cospdata.wednesday,thursday: action.cospdata.thursday, /*friday: action.cospdata.friday,*/saturday: action.cospdata.saturday,sunday: action.cospdata.sunday}

        case actionTypes.PlanOfStudy.GET_COURSE_INFO_POPUP :
            return { ...state,loading: false,coursename: action.coipdata.course,credit: action.coipdata.credit,coursedescription: action.coipdata.description}
            
        case actionTypes.PlanOfStudy.GET_COURSE_OFFER :
            return { ...state,loading: false,courses: action.codata}
            
        case actionTypes.PlanOfStudy.GET_COURSE_OFFERING :
            return { ...state,loading: false,coursesS: action.cobodata}

        case actionTypes.PlanOfStudy.GET_CREDITS_AND_COST:
            return { ...state,loading: false,credits: action.cdata}

        case actionTypes.PlanOfStudy.GET_MAJOR_CRD_COST:
            return { ...state,loading: false,creditCost: action.cdata.creditCost}
        default:
            return state
    }
}

export default PlanOfStudyR;
