import * as actionTypes from '../actions/actions';

const initialState = {
    citizenshipData: '',
    maritalStatusData: '',
    primaryLanguagesData: '',
    firstname: '',
    middlename: '',
    lastname: '',
    citizenship: '',
    phone: '',
    dob: '',
    primarylanguage: '',
    landline: '',
    email: '',
    caddress: '',
    maritalstatus: '',
    gender: '',
    passportnumber: '',
    passportfile: '',
    userId: '',
    status:'',
    error: '',
    loading: false,
    result: ''
    
}

const UpdateNUidR =  (state = initialState, action) => {
    switch (action.type) {

        case actionTypes.UpdateNUid.GET_USER_INFO :
            return { ...state, [action.name] : action.value  }

        case actionTypes.UpdateNUid.UPDATENUID_START :
            return { ...state,loading: true}
    
        case actionTypes.UpdateNUid.UPDATENUID_END :
            return { ...state,loading: false,result: action.userData} 
                
        case actionTypes.UpdateNUid.UPDATENUID_FAIL :
            return { ...state,loading: false,error: action.error}
            
        case actionTypes.UpdateNUid.UPDATENUID_CITIZENSHIPDATA :
            return { ...state,loading: false,citizenshipData: action.ctshdata}  
            
        case actionTypes.UpdateNUid.UPDATENUID_MARITALSTATUSDATA :
            return { ...state,loading: false,maritalStatusData: action.msdata}   

        case actionTypes.UpdateNUid.UPDATENUID_PRIMARYLANGUAGEDATA :
            return { ...state,loading: false,primaryLanguagesData: action.ctshdata}   

        case actionTypes.UpdateNUid.GET_USER_DATA :
            return { ...state,loading: false,firstname: action.udata.contact.firstname,lastname: action.udata.contact.lastname,middlename: action.udata.contact.middlename,gender: action.udata.contact.gender,dob: action.udata.contact.dob,citizenship: action.udata.contact.citizenship,primarylanguage: action.udata.contact.primarylanguage,phone: action.udata.contact.phone,landline: action.udata.contact.landline,email: action.udata.contact.email,caddress: action.udata.contact.caddress,maritalstatus: action.udata.contact.maritalstatus}  
        default:
            return state
    }
}

export default UpdateNUidR;
