import React from 'react'
import classes from './InputDocument.module.css'

const InputDocument = ({name,docListName,docList}) => {
    const handleOnChange = (e) => {
        docListName.push(e.target.name);
        docList.push(e.target.files[0]);
    }
    return (
        <div className={classes.InputDocument}>
            <div>
                {name}
            </div>
            <div>
                <input type="file" name={name} onChange={handleOnChange} />
            </div>
        </div>
    )
}

export default InputDocument
