import React, { useState } from "react";
import classes from "./CoursesToDrop.module.css";
import CoursesToDropTable from "./CoursesToDropTable/CoursesToDropTable";
import Grid from "@material-ui/core/Grid";

const CoursesToDrop = ({coursesArray,listToDrop}) => {

  return (
    <div className={classes.CoursesToDrop}>
      <div className={classes.Body}>
        <Grid container spacing={3}>
          <Grid item xs={12} md={6} sm={12}>
            <div className={classes.CoursesToDropName}>
              <CoursesToDropTable courses={coursesArray} dropList={listToDrop}/>
            </div>
          </Grid>
        </Grid>
      </div>
    </div>
  );
};

export default CoursesToDrop;
