import React from 'react'
import DehazeIcon from '@material-ui/icons/Dehaze'
import CloseIcon from '@material-ui/icons/Close'
import classes from './MobileLinks.module.css'
import HomeHeaderLinks from '../HomeHeaderLinks/HomeHeaderLinks'

const MobileLinks = ({navIcon, handleClickNavIcon, LogedIn}) => {
    return (
        <>
            <div onClick={handleClickNavIcon}>
                {navIcon ? <CloseIcon /> : <DehazeIcon />}
            </div>

            {
                navIcon ? 
                <div className={classes.MobileLinksContainer}>
                    {
                
                LogedIn ? <HomeHeaderLinks LinkName='Home' path='/StudentPortal' />:<HomeHeaderLinks LinkName='Home' path='/' />
            }
                    
                    <HomeHeaderLinks LinkName='Online Services' path='#' />
                    <HomeHeaderLinks LinkName='Admission' path='#' />
                    {
                        LogedIn ?
                        <>
                            <HomeHeaderLinks LinkName='Plan Of Study' path='/PlanOfStudy' />
                            <HomeHeaderLinks LinkName='Services' path='/StudentMainPage' />
                            <HomeHeaderLinks LinkName='Logout' path='/Logout' />
                        </> :
                        <>
                            <HomeHeaderLinks LinkName='Apply' path='/Login' />
                            <HomeHeaderLinks LinkName='Login' path='/Login' />
                            <HomeHeaderLinks LinkName='Administration' path='/Administration' />
                        </> 

                    }
                    
                </div> 
                : null
            }
            
        </>
    )
}

export default MobileLinks
