
import axios from 'axios';
import * as actionTypes from '../actions/actions';
import {digid , lms} from './Constants'


export const getInformationAcceptData = (name, value) => {
    return{
        type: actionTypes.SubscribeSemester.GET_INFORMATION_ACCEPT,
        name: name,
        value: value
    }
}

export const getAllSemester = () => {
    return dispatch =>{
       
        
        var link = lms+'/nuversity/api/lms/allSemester';
       axios.get(link)
            .then(res=>{
                
                dispatch(getAllSemesterData(res.data));
                
                        
            }).catch(err => {
                console.log(err)
            })
      
    }
}

export const getAllSemesterData = (data) => {

    return{
        type: actionTypes.SubscribeSemester.GET_ALL_ACCEPT_SEMESTER,
        cdata: data
    }
}

export const studentSubscribe = (studentId,semester,year) => {
    
    return dispatch =>{
        // dispatch(studentSubscribeStart());
        
        const params = {
            "student": studentId,
            "semester": semester,
            "oyear":year
        }
                                                                                      
              axios.post(lms+"/nuversity/api/lms/subscribeSemester",params,{ headers: { "Content-Type": "application/json", }, })


                .then(res=>{
                    
                    dispatch(studentSubscribeEnd(res.data));
                        
                        }).catch(err => {
                            dispatch(studentSubscribeFail(err));
                        })
                }
}

