import React, { useState } from "react";
import BoxToSelect from "../../ComponentsForApplication/BoxToSelect/BoxToSelect";
import ButtonSection from "../../ComponentsForApplication/ButtonSection/ButtonSection";
import InputField from "../../ComponentsForApplication/InputField/InputField";
import classes from "./Courses.module.css";
import CoursesTable from "./CoursesTable/CoursesTable";
import Grid from "@material-ui/core/Grid";

const CoursesToSelect = ({coursesArray,listToSelect,getPOSInfo,courseType,getCourseByCourseOffering,Monday,
  Tuesday,
  Wednesday,
  Thursday,
  Friday,
  Saturday,
  Sunday,
  teacherfirstname,
  teacherlastname,
  getCourseOfferSchedulePopup,
  getTeacher,
  getCreditsAndCost}) => {
  const handleChooseField = (e) => {
    getPOSInfo(e.target.name,e.target.value)
  };

  const CoursesType = ["GENERAL", "MAJOR", "CORE", "ELECTIVE"];
  const name="courseType";
  const GMCE = CoursesType.map((CourseT) => {
    return (
      <div key={CourseT} style={{ width: "100%" }}>
        <InputField clicked={handleChooseField} fieldName={CourseT} value={CourseT} name={name} />
      </div>
    );
  });

  return (
    <div className={classes.Courses}>
      <div className={classes.Body}>
        <Grid container spacing={3}>
          <Grid item xs={12} md={6} sm={2}>
            <div className={classes.CoursesBox}>
              <BoxToSelect toShow={GMCE} />
            </div>
          </Grid>
          <Grid item xs={12} md={6} sm={10}>
            <div className={classes.CoursesName}>
              {
             coursesArray !== ''? <CoursesTable courses={coursesArray} selectList={listToSelect} getPOSInfo={getPOSInfo} getCourseByCourseOffering={getCourseByCourseOffering} Monday={Monday}
             Tuesday={Tuesday}
             Wednesday={Wednesday}
             Thursday={Thursday}
             Friday={Friday}
             Saturday={Saturday}
             Sunday={Sunday}
             teacherfirstname={teacherfirstname}
             teacherlastname={teacherlastname}
             getCourseOfferSchedulePopup={getCourseOfferSchedulePopup}
            getTeacher={getTeacher}
            getCreditsAndCost={getCreditsAndCost} />:<></>
              }
            </div>
          </Grid>
        </Grid>
      </div>
    </div>
  );
};

export default CoursesToSelect;