import axios from 'axios';
import * as actionTypes from '../actions/actions';
import {digid , lms} from './Constants'

export const getFPInfo = (name, value) => {
    return{
        type: actionTypes.ForgetPassword.GET_FP_INFO,
        name: name,
        value: value
    }
}

export const forgetPassword = (email) => {
    
    return dispatch =>{
        dispatch(forgetPasswordStart());
        const params = {
            "email": email,
        }
        
            axios.post(digid+"/nuversity/api/digid/forgetPassword",params,{ headers: { "Content-Type": "application/json", }, })

                .then(res=>{
                    console.log(res.data);
                    dispatch(forgetPasswordEnd(res.data));
                         
                         
                        }).catch(err => {
                            dispatch(forgetPasswordFail(err));
                        })                                                                                                                                        
                }
}


export const forgetPasswordStart = () => {

    return{
        type: actionTypes.ForgetPassword.FORGET_PASSWORD_START
    }

}

export const forgetPasswordEnd = (data) => {

    return{
        type: actionTypes.ForgetPassword.FORGET_PASSWORD_END,
        cdata: data
    }
}

export const forgetPasswordFail = (err) => {

    return{
        type: actionTypes.ForgetPassword.FORGET_PASSWORD_FAIL,
        error: err
    }
}
