import axios from 'axios';
import * as actionTypes from '../actions/actions';
import {digid , lms} from './Constants'

export const getStudentInfo = (studentId,majorId) => {
    return dispatch =>{
        
        
        var link = lms+'/nuversity/api/lms/getStudentCurrentSemesterInfo/'+studentId+'/'+majorId;
       axios.get(link)
            .then(res=>{
                
                dispatch(getstudentinfo(res.data));
                
                        
            }).catch(err => {
                console.log(err)
            })
      
    }
}

export const getCurrentSemesterInfo = (studentId,majorId) => {
    return dispatch =>{
        
        var link = lms+'/nuversity/api/lms/getCurrentSemesterInfo/'+studentId+'/'+majorId;
       axios.get(link)
            .then(res=>{
               
                dispatch(getcurrentsemesterinfo(res.data));
                
                        
            }).catch(err => {
                console.log(err)
            })
       
    }
}

export const dropCourses = (courses,studentId,majorId) => {
    
    return dispatch =>{
        dispatch(currentsemesterStart());
        console.log(courses);
        const params = {
            "courses": courses,
            "studentId": studentId, //studentId,
            "majorId": majorId //majorId
        }
        
            axios.post(lms+"/nuversity/api/lms/dropCourses",params,{ headers: { "Content-Type": "application/json", }, })

                .then(res=>{
                    console.log(res.data);
                    dispatch(currentsemesterEnd(res.data));
                         
                         
                        }).catch(err => {
                            dispatch(currentsemesterFail(err));
                        })                                                                                                                                        
                }
}

export const currentsemesterStart = () => {

    return{
        type: actionTypes.CurrentSemester.CurrentSemester_START
    }

}

export const currentsemesterEnd = (data) => {

    return{
        type: actionTypes.CurrentSemester.CurrentSemester_END,
        userData: data
    }
}

export const currentsemesterFail = (err) => {

    return{
        type: actionTypes.CurrentSemester.CurrentSemester_FAIL,
        error: err
    }
}
export const getstudentinfo = (data) => {

    return{
        type: actionTypes.CurrentSemester.GET_STUDENT_INFO,
        stddata: data
    }  
}
export const getcurrentsemesterinfo = (data) => {

    return{
        type: actionTypes.CurrentSemester.GET_Current_Semester_INFO,
        csidata: data
    }
}