import Border from "../../../Border/Border";
import Divider from "../../../Divider/Divider";
import CourseInformation from "../CourseInformation/CourseInformation";
import classes from "./Informations.module.css";

const Informations = (props) => {
  const text = props.text;
  const courseName = props.courseName;
  const teacherName = props.teacherName;
  const credits = props.credits;
  return (
    <div className={classes.container}>
      <h3 className={classes.title}>Informations</h3>
      <div className={classes.subContainer}>
        <CourseInformation courses={courseName} teacher={teacherName} credits={credits} />
        <Border height="150px" />
        <Divider padding="10px" />
        <div className={classes.text}>
          <h3>Introduction</h3>
          {text}
        </div>
      </div>
    </div>
  );
};

export default Informations;
