import classes from "./CourseInfo.module.css";

const CourseInfo = ({generalrequired,generalacquired,generaltogo,majorrequired,majoracquired,majortogo,corerequired,coreacquired,coretogo,electiverequired,electiveacquired,electivetogo}) => {
  const totalrequired = generalrequired+majorrequired+corerequired+electiverequired;
  const totalacquired = generalacquired+majoracquired+coreacquired+electiveacquired;
  const totaltogo = generaltogo+majortogo+coretogo+electivetogo;
  return (
    <div className={classes.container}>
      <table className={classes.tab}>
        <thead className={classes.tabHead}>
          <tr>
            <th>Course</th>
            <th>Required</th>
            <th>Acquired</th>
            <th>To Go</th>
          </tr>
        </thead>
        <tbody className={classes.tabBody}>
          <tr>
            <td>General</td>
            <td className={classes.generalreq}>{generalrequired}</td>
            <td className={classes.generalacq}>{generalacquired}</td>
            <td className={classes.generatogo}>{generaltogo}</td>
          </tr>
          <tr>
            <td>Major</td>
            <td className={classes.majorreq}>{majorrequired}</td>
            <td className={classes.majoracq}>{majoracquired}</td>
            <td className={classes.majortogo}>{majortogo}</td>
          </tr>
          <tr>
            <td>Core</td>
            <td className={classes.corereq}>{corerequired}</td>
            <td className={classes.coreacq}>{coreacquired}</td>
            <td className={classes.coretogo}>{coretogo}</td>
          </tr>
          <tr>
            <td>Elective</td>
            <td className={classes.electivereq}>{electiverequired}</td>
            <td className={classes.electiveacq}>{electiveacquired}</td>
            <td className={classes.electivetogo}>{electivetogo}</td>
          </tr>
        </tbody>
        <tfoot className={classes.tabFoot}>
          <tr>
            <td>Total</td>
            <td className={classes.totalreq}>{totalrequired}</td>
            <td className={classes.totalacq}>{totalacquired}</td>
            <td className={classes.totaltogo}>{totaltogo}</td>
          </tr>
        </tfoot>
      </table>
    </div>
  );
};

export default CourseInfo;
