import Switch from './Switch/Switch';
import classes from './Probation.module.css';

const Probation = ({probation}) => {
    return ( 
        <div className={classes.container}>
            <span className={classes.prob}>Probation</span>
            <span className={classes.choice}><Switch probation={probation} /></span>
        </div>
     );
}
 
export default Probation;