import Divider from "../../../Divider/Divider";
import classes from "./CourseInformation.module.css";

const CourseInformation = (props) => {
  const courses = props.courses;
  const teacher = props.teacher;
  const credits = props.credits;
  return (
    <div className={classes.container}>
      <div className={classes.cont}>
        <span className={classes.attribute}>Course</span>
        <Divider padding="5px" />
        <span className={classes.value}>{courses}</span>
      </div>
      <div className={classes.cont}>
        <span className={classes.attribute}>Teacher</span>
        <Divider padding="5px" />
        <span className={classes.value}>{teacher}</span>
      </div>
      <div className={classes.cont}>
        <span className={classes.attribute}>Credits</span>
        <Divider padding="5px" />
        <span className={classes.value}>{credits}</span>
      </div>
    </div>
  );
};

export default CourseInformation;
