import React from 'react'
import { Link } from 'react-router-dom'
import './Footer.css'

const Footer = () => {
    return (
        <div className="footer-container">

             <div className="footer-inner-container">
                <div>
                    <span>Menu</span>
                    <div className='partLinks'>
                        <Link to=''>About Us</Link>
                        <Link to=''>Careers</Link>
                        <Link to=''>Events</Link>
                        <Link to=''>Admissions</Link>
                        <Link to=''>Blogs</Link>
                    </div>
                </div>
                <div>
                    <span>Services</span>
                    <div className='partLinks'>
                        <Link to=''>My Account</Link>
                        <Link to=''>Classes</Link>
                        <Link to=''>Payments</Link>
                    </div>
                </div>
                <div>
                    <span>Support</span>
                    <div className='partLinks'>
                        <Link to=''>Contact Us</Link>
                        <Link to=''>FAQs</Link>
                        <Link to=''>Research</Link>
                    </div>
                </div>
                <div className="call-us">                   
                    <span className='CallUsTitle'>CALL</span>
                    
                    <div>
                        <Link to="" className="fas fa-phone-volume footer-links"></Link>
                    </div>
                    <div>
                        <Link to="" className="far fa-envelope-open footer-links"></Link>
                    </div>
                    <div>
                        <Link to="" className="fas fa-map-marker-alt footer-links"></Link>
                    </div>
                    <div className="line"></div>
                    <div className="social-media-icons">
                        <div>
                            <Link to="" className="fa fa-whatsapp footer-links footer-rounded"></Link>
                        </div>
                        <div>
                            <Link to="" className="fa fa-twitter footer-links footer-rounded"></Link>
                        </div>
                        <div>
                            <Link to="" className="fa fa-instagram footer-links footer-rounded"></Link>
                        </div>
                        <div>
                            <Link to="" className="fa fa-facebook footer-links footer-rounded"></Link>
                        </div>
                        <div>
                            <Link to="" className="fa fa-linkedin footer-links footer-rounded"></Link>
                        </div>
                        <div>
                            <Link to="" className="fa fa-google-plus footer-links footer-rounded"></Link>
                        </div>
                    </div>
                </div>
            </div>
            
            <div className="footer-inner1-container">

                <div>
                    <p className="copy-right">Copyright, NUVERSITY / All rights reserved</p>
                </div>
                <div className="privacy-usage-copyright">
                    <div><Link to="" className="privacy-usage-copyright-links">Privacy Policy</Link></div>
                    <div><Link to="" className="privacy-usage-copyright-links">Usage Agreement</Link></div>
                    <div><Link to="" className="privacy-usage-copyright-links">Copyrights</Link></div>
                </div>

            </div>

        </div>
    )
}

export default Footer
