import React from "react";
import Grid from "@material-ui/core/Grid";
import HomeHeader from "../../components/HomeHeader/HomeHeader";
import LogedInHeader from "../../components/LogedInHeader/LogedInHeader";
import classes from "./CurrentSemester.module.css";
import { Button, CircularProgress, Container, makeStyles } from "@material-ui/core";
import BoxWithTitle from "../../components/BoxWithTitle/BoxWithTitle";
import BoxWithTitleAndWhiteBack from "../../components/BoxWithTitle2/BoxWithTitleAndWhiteBack";
import Infos from "../../components/CurrentSemestre/Infos/Infos";
import Footer from "../../components/Footer/Footer";
import LabelWithValue from "../../components/CheckAccountComponents/LabelWithValue/LabelWithValue";
import LabelWithValueC from "../../components/CurrentSemestre/LabelWithValueC/LabelWithValueC";
import CoursesToDrop from "../../components/CurrentSemestre/Receipt/CoursesToDrop";
import Dialog from "@material-ui/core/Dialog";
import DialogActions from "@material-ui/core/DialogActions";
import DialogContent from "@material-ui/core/DialogContent";
import DialogContentText from "@material-ui/core/DialogContentText";
import DialogTitle from "@material-ui/core/DialogTitle";
import Slide from "@material-ui/core/Slide";
import { connect } from "react-redux";
import Backdrop from '@material-ui/core/Backdrop';
import * as CurrentSemesterActionCreators from "../../store/actionsCreators/CurrentSemesterAction";
import { useEffect } from "react";

const Transition = React.forwardRef(function Transition(props, ref) {
  return <Slide direction="up" ref={ref} {...props} />;
});

const useStyles = makeStyles((theme) => ({
  backdrop: {
    zIndex: theme.zIndex.drawer + 1,
    color: '#fff',
  },
}));

const CurrentSemester = ({getStudentInfo,getCurrentSemesterInfo,dropCourses,loading,studentId,majorId,majorname,userId,year,semestername,date,courses,dropList,result}) => {
  
  const classesMakeStyle = useStyles();

    const [openBackdrop, setOpenBackdrop] = React.useState(false);
  
    useEffect(()=>{
      window.scrollTo(0,0);
  },[])
  
  useEffect(() => {
      getStudentInfo(localStorage.getItem('userId'),localStorage.getItem('major'));
      getCurrentSemesterInfo(localStorage.getItem('userId'),localStorage.getItem('major'));
      
    }, []);

    useEffect(() => {
      if(result !== ''){
      setOpenBackdrop(true);
      }
    }, [result]);

    const handleOnClose = () =>{
      setOpenBackdrop(false);
  }

  const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };
  const handleCloseSubmit = () => {
    setOpen(false);
    dropCourses(dropList,localStorage.getItem('userId'),localStorage.getItem('major'));
  };
  return (
    <>
    {result !== '' ?
    <Backdrop className={classesMakeStyle.backdrop} open={openBackdrop} onClick={handleOnClose}>
                <div style={{ color: '#fff' }}>
                    {
                        
                        <div className={classes.textBoxApplication}>
                            {
                             
                                  <p>Courses dropped successfully.<br/></p>
                             
                            }
                         </div>

                    }
                </div>    
        </Backdrop> : <></>
}
      <HomeHeader LogedIn={true} />
      <LogedInHeader />
      <div className={classes.checkAccount}>
        <Container>
          <BoxWithTitle title="Current Semester">
            <Grid container spacing={3}>
              <Grid item xs={12} md={6} sm={12}>
                <Infos title="Student Info">
                <LabelWithValue label="Student Id" value={userId} />
                <LabelWithValue label="Major" value={majorname} />
                </Infos>
              </Grid>
              <Grid item xs={12} md={3} sm={6}>
                <LabelWithValue label="Year" value={year} />
              </Grid>
              <Grid item xs={12} md={3} sm={6}>
                <LabelWithValue label="Semester" value={semestername} />
              </Grid>
              <Grid item xs={12} md={3} sm={6}>
                <LabelWithValue label="Date" value={date} />
              </Grid>
              <Grid item xs={12} md={3} sm={6}></Grid>
              <Grid item xs={12} md={6} sm={12}>
                <BoxWithTitleAndWhiteBack>
                 { 
                 
                  courses ? <CoursesToDrop coursesArray={courses} listToDrop={dropList}/>: <></>
                 
                 }
                </BoxWithTitleAndWhiteBack>
              </Grid>
              <Grid item xs={12} md={3} sm={11}></Grid>
              <Grid item xs={12} md={3} sm={20}>
                <div className={classes.but}>
                  <Button variant="contained" color="primary" onClick={handleClickOpen} style={{ padding: "10px" , width: "200px" }}>
                  {
                  loading ? <CircularProgress style={{ color: "#fff" }} size={35} />: <h2>Save</h2>
                }
                  </Button>
                </div>
                <Dialog
                  open={open}
                  TransitionComponent={Transition}
                  keepMounted
                  onClose={handleClose}
                  aria-labelledby="alert-dialog-slide-title"
                  aria-describedby="alert-dialog-slide-description"
                >
                  <DialogTitle id="alert-dialog-slide-title">
                    {"Are you sure ?"}
                  </DialogTitle>
                  <DialogContent>
                    <DialogContentText id="alert-dialog-slide-description">
                      If you agree the course(s) will be dropped.
                    </DialogContentText>
                  </DialogContent>
                  <DialogActions>
                    <Button onClick={handleClose} color="primary">
                      Disagree
                    </Button>
                    <Button onClick={handleCloseSubmit} color="primary">
                      Agree
                    </Button>
                  </DialogActions>
                </Dialog>
              </Grid>
            </Grid>
          </BoxWithTitle>
        </Container>
      </div>
      <Footer />
    </>
  );
};
const mapStateToProps = (state) => {
  return {
    majorname: state.CurrentSemesterR.majorname,
    userId: state.CurrentSemesterR.userId,
    year: state.CurrentSemesterR.year,
    semestername: state.CurrentSemesterR.semestername,
    date: state.CurrentSemesterR.date,
    loading: state.CurrentSemesterR.loading,
    error: state.CurrentSemesterR.error,
    courses: state.CurrentSemesterR.courses,
    dropList: state.CurrentSemesterR.dropList,
    result: state.CurrentSemesterR.result
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    getStudentInfo: (userId,major) => dispatch(CurrentSemesterActionCreators.getStudentInfo(userId,major)),
    getCurrentSemesterInfo: (userId,major) => dispatch(CurrentSemesterActionCreators.getCurrentSemesterInfo(userId,major)),
    dropCourses: (courses,studentId,majorId) => dispatch(CurrentSemesterActionCreators.dropCourses(courses,studentId,majorId))
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(CurrentSemester);
