import React from "react";
import classes from "./ChangeForgottenPassword.module.css";
import {
  Button,
  CircularProgress,
  InputAdornment,
  TextField,
} from "@material-ui/core";
import { Link } from "react-router-dom";
import { AccountCircle, Lock } from "@material-ui/icons";
import HomeHeader from "../../components/HomeHeader/HomeHeader";
import Footer from "../../components/Footer/Footer";
import Backdrop from '@material-ui/core/Backdrop';
import { makeStyles } from '@material-ui/core/styles';
import * as ForgetPasswordCreators from "../../store/actionsCreators/ChangeForgottenPasswordActions";
import { connect } from "react-redux";
import {useState ,useEffect} from "react";



const useStyles = makeStyles((theme) => ({
    backdrop: {
      zIndex: theme.zIndex.drawer + 1,
      color: '#fff',
    },
  }));

const ChangeForgottenPassword = ({loading,email,token,newPassword,retryNewPassword,result,error,getUserByToken,getFPInfo,onLoadForgetPassword,ChangeForgottenPassword}) => {

  const classesMakeStyle = useStyles();

    const [openBackdrop, setOpenBackdrop] = React.useState(false);
    const [backdropText, setBackdropText] = React.useState('');


    useEffect(() => {
      
      const tokenURL = new URLSearchParams(window.location.search);
      onLoadForgetPassword(tokenURL.get("token"));
      getUserByToken(tokenURL.get("token"));
      
    }, []);

    useEffect(() => {
      if(result !== ''){
      setOpenBackdrop(true);
      }
    }, [result]);

    const handleClose = () =>{
      setOpenBackdrop(false);
  }

  const handleOnChange = (e)=>{
    getFPInfo(e.target.name,e.target.value);
  }


  const submitForm =(e) =>{
    ChangeForgottenPassword(email,token,newPassword,retryNewPassword);
  }

  return (
    <>
    <Backdrop className={classesMakeStyle.backdrop} open={openBackdrop} onClick={handleClose}>
                <div style={{ color: '#fff' }}>
                    {
                        
                        <div className={classes.textBoxApplication}>
                            {
                             result
                            }
                         </div>

                    }
                </div>    
        </Backdrop>
      <HomeHeader />
      <div className={classes.Login}>
        <div className={classes.LoginContainer}>
          <div className={classes.LoginContainerForm}>
            <form autoComplete="off">
              <h1>Change Password</h1>
              <br />

              <TextField
                label="NUmail"
                name="email"
                value={email}
                onChange={handleOnChange}
                fullWidth
                variant="outlined"
                style={{ margin: "20px 0px" }}
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <AccountCircle
                        style={{ color: "#900603" }}
                        fontSize="large"
                      />
                    </InputAdornment>
                  ),
                }}
              />
              <TextField
                label="New Password"
                name="newPassword"
                fullWidth
                value={newPassword}
                onChange={handleOnChange}
                style={{ margin: "0px 0px 20px 0px" }}
                variant="outlined"
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <Lock style={{ color: "#900603" }} fontSize="large" />
                    </InputAdornment>
                  ),
                  type: "password",
                }}
              />
              <TextField
                label="Confirm Password"
                name="retryNewPassword"
                fullWidth
                value={retryNewPassword}
                onChange={handleOnChange}
                style={{ margin: "0px 0px 20px 0px" }}
                variant="outlined"
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <Lock style={{ color: "#900603" }} fontSize="large" />
                    </InputAdornment>
                  ),
                  type: "password",
                }}
              />

              {/* <input type="submit" value={<CircularProgress color="secondary" />} className={classes.SubmitBtn} /> */}
              <Button
                style={{ padding: "10px" }}
                variant="contained"
                fullWidth
                color="primary"
                onClick={submitForm}
              >

                {
                  loading ? <CircularProgress style={{ color: "#fff" }} size={35} />: <h2>Change Password</h2>
                }
              </Button>
            </form>
          </div>
        </div>
      </div>
      <Footer />
    </>
  );
};

const mapStateToProps = (state) => {
  return {
     email: state.ChangeForgottenPasswordR.email,
     loading: state.ChangeForgottenPasswordR.loading,
     error: state.ChangeForgottenPasswordR.error,
     result: state.ChangeForgottenPasswordR.result,
     token: state.ChangeForgottenPasswordR.token,
     newPassword: state.ChangeForgottenPasswordR.newPassword,
     retryNewPassword: state.ChangeForgottenPasswordR.retryNewPassword,
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
      getFPInfo: (name,value) => dispatch(ForgetPasswordCreators.getFPInfo(name,value)),
      onLoadForgetPassword: (token) => dispatch(ForgetPasswordCreators.onLoadForgetPassword(token)),
      ChangeForgottenPassword: (email,token,npass,rnpass) => dispatch(ForgetPasswordCreators.ChangeForgottenPassword(email,token,npass,rnpass)),
      getUserByToken: (token) => dispatch(ForgetPasswordCreators.getUserByToken(token)),
  };
};
export default connect(mapStateToProps, mapDispatchToProps)(ChangeForgottenPassword);
