import LogedInHeader from '../../components/LogedInHeader/LogedInHeader'
import HomeHeader from '../../components/HomeHeader/HomeHeader'
import { Container } from '@material-ui/core'
import BoxWithTitle from '../../components/BoxWithTitle/BoxWithTitle'
import Footer from '../../components/Footer/Footer'
import classes from './History.module.css'
// import GreyBoxithTitle from '../../components/GreyBoxWithTitle/GreyBoxWithTitle'
import WelcomeBox from './WelcomeBox/WelcomeBox';
import HistoryFrom from './HistoryForm/HistoryForm';
import { useEffect } from 'react';
import * as HistoryAction from "../../store/actionsCreators/HistoryAction";
import { connect } from "react-redux";
const History = ({yearsData,semestersData,year,semester,courses,userId,major,getInfoHistory,getYears,getSemesters,getStudentInfo,getCourses}) => {
    
    useEffect(()=>{
        window.scrollTo(0,0);
    },[])

    useEffect(()=>{
        getYears(localStorage.getItem('userId'),localStorage.getItem('major'));
        getStudentInfo(localStorage.getItem('userId'),localStorage.getItem('major'));

    },[]);

    
    

    useEffect(()=>{
        if(semester !== ''){
            getCourses(localStorage.getItem('userId'),localStorage.getItem('major'),semester);
           
        }

    },[semester]);

    return ( 
    <div>
        <HomeHeader LogedIn={true} />
        <LogedInHeader />

        <div className={classes.history}>
       
         <Container>
            <BoxWithTitle title='HISTORY'>
                <WelcomeBox WelcomeMessage='Dear Studen .........................................................................................................................................................
                                        ..............................................................................................................................................................................
                                        ..............................................................................................................................................................................
                                        ..............................................................................................................................................................................
                                        ..............................................................................................................................................................................
                                        ..............................................................................................................................................................................'
                                        />  
               {
               yearsData ? <HistoryFrom yearsData={yearsData} semestersData={semestersData} year={year} semester={semester} courses={courses} getInfoHistory={ getInfoHistory} getSemesters={getSemesters}/>   :<></>                     
               }
               </BoxWithTitle>
        </Container>
        </div>
        <Footer/>    
    </div>
     );
}

const mapStateToProps = (state) => {
    return {
        yearsData: state.HistoryR.yearsData,
        semestersData: state.HistoryR.semestersData,
        year: state.HistoryR.year,
        semester: state.HistoryR.semester,
        courses: state.HistoryR.courses,
        userId: state.HistoryR.userId,
        major: state.HistoryR.major
    }
  }
  
  const mapDispatchToProps = (dispatch) => {
    return {
        getInfoHistory: (name,value) => dispatch(HistoryAction.getInfoHistory(name,value)),
        getYears: (stdId,major) => dispatch(HistoryAction.getYears(stdId,major)),
        getSemesters: (stdId,major,year) => dispatch(HistoryAction.getSemesters(stdId,major,year)),
        getStudentInfo: (stdId,major) => dispatch(HistoryAction.getStudentInfo(stdId,major)),
        getCourses: (stdId,major,semester) => dispatch(HistoryAction.getCourses(stdId,major,semester)),


        };
  };
 
export default connect(mapStateToProps, mapDispatchToProps)(History);