import React from 'react'
import LogedInHeader from '../../components/LogedInHeader/LogedInHeader'
import HomeHeader from '../../components/HomeHeader/HomeHeader'
import classes from './CalendarPage.module.css'
import { Button, Container } from '@material-ui/core'
import Calendar from '../../components/Calendar/Calendar'
import BoxWithTitle from '../../components/BoxWithTitle/BoxWithTitle'
import Footer from '../../components/Footer/Footer'
import { useEffect } from 'react';
import * as CalendarAction from "../../store/actionsCreators/CalendarAction";
import { connect } from "react-redux";
const Application = ({appointments,getAppointments}) => {

    useEffect(()=>{
        window.scrollTo(0,0);
    },[])
    
    useEffect(()=>{
        getAppointments(localStorage.getItem('userId'),localStorage.getItem('major'));
    })
    return (
        <>
            <HomeHeader LogedIn={true} />
            <LogedInHeader />
            <div className={classes.Calendar}>
                <Container>
                    <BoxWithTitle title='Calendar'>
                        {
                        appointments ? <Calendar appointments={appointments}/>: <></>
                        }
                    </BoxWithTitle>
                </Container>
            </div>
            <Footer />    
        </>
    )
}

const mapStateToProps = (state) => {
    return {
        appointments: state.CalendarR.appointments
    }
}

const mapDispatchToProps = (dispatch) => {
    return {
        getAppointments: (stdId,major) => dispatch(CalendarAction.getAppointments(stdId,major)),
    }
}
export default connect(mapStateToProps, mapDispatchToProps)(Application)
