import React, { useState } from "react";
import BoxToSelect from "../../ComponentsForApplication/BoxToSelect/BoxToSelect";
import ButtonSection from "../../ComponentsForApplication/ButtonSection/ButtonSection";
import InputField from "../../ComponentsForApplication/InputField/InputField";
import classes from "./CoursesSelected.module.css";
import CoursesTable from "./CoursesSelectedTable/CoursesSelectedTable";
import Grid from "@material-ui/core/Grid";

const CoursesSelected = ({coursesArray,category,getTeacher ,teacherfirstname,
  teacherlastname,selectList,getPOSInfo,getCourseByCourseOffering , getCreditsAndCost,allCourses}) => {

  return (
    <div className={classes.Courses}>
      <div className={classes.Body}>
        <Grid container spacing={3}>
          <Grid item xs={12} md={6} sm={12}>
            <div className={classes.CoursesName}>
              <CoursesTable courses={coursesArray} category={category} getTeacher={getTeacher} teacherfirstname={teacherfirstname} getCreditsAndCost={getCreditsAndCost}
                          teacherlastname={teacherlastname} selectList={selectList} getPOSInfo={getPOSInfo} getCourseByCourseOffering={getCourseByCourseOffering} allCourses={allCourses}/>
            </div>
          </Grid>
        </Grid>
      </div>
    </div>
  );
};

export default CoursesSelected;
