import { AppBar, Toolbar, useMediaQuery, useScrollTrigger } from '@material-ui/core'
import React,{useState, useEffect} from 'react'
import { Link } from 'react-router-dom'
import Logo from '../Logo/Logo'
import DesktopLinks from './DesktopLinks/DesktopLinks'
import classes from './HomeHeader.module.css'
import MobileLinks from './MobileLinks/MobileLinks'


const HomeHeader = ({LogedIn=false}) => {

    const [navIcon, toggleNavIcon] = useState(false);
    const mobileSize = useMediaQuery('(max-width: 961px)');

    const trigger = useScrollTrigger();

    const handleClickNavIcon = () =>{
        toggleNavIcon(!navIcon);
    }

    useEffect(() => {
        if(!mobileSize && navIcon){
            toggleNavIcon(false)
        }
        
    }, [mobileSize, navIcon])
    
    return (
        <AppBar elevation={trigger ? 8 : 0} >
            <Toolbar className={classes.Toolbar}>
                <div className={classes.LogoContainer}>
                    <Link to='/'>
                        <Logo />
                    </Link>
                </div>
                <div className={classes.Grow}></div>
                {
                    mobileSize ? <MobileLinks LogedIn={LogedIn} navIcon={navIcon} handleClickNavIcon={handleClickNavIcon} /> : <DesktopLinks LogedIn={LogedIn}  />
                } 
            </Toolbar>
        </AppBar>
    )
}

export default HomeHeader
