import axios from 'axios';
import * as actionTypes from '../actions/actions';
import {digid , lms} from './Constants'

export const getRPInfo = (name, value) => {
    return{
        type: actionTypes.ResetPassword.GET_RP_INFO,
        name: name,
        value: value
    }
}

export const onLoadResetPassword = (email) => {
    
    return{
        type: actionTypes.ResetPassword.GET_ONLOADRP_INFO,
        cdata: email
    }
}

export const resetPassword = (email,opassword,npassword,conewpassword) => {
    
    return dispatch =>{
        dispatch(resetPasswordStart());
        const params = {"email":email,
                        "oldPassword":opassword,
                        "password":npassword,
                        "confirmPassword":conewpassword
        }
        
            axios.post(digid+"/nuversity/api/digid/resetPassword",params,{ headers: { "Content-Type": "application/json", }, })

                .then(res=>{
                    console.log(res.data);
                    dispatch(resetPasswordEnd(res.data));
                         
                         
                        }).catch(err => {
                            dispatch(resetPasswordFail(err));
                        })                                                                                                                                        
                }
}


export const resetPasswordStart = () => {

    return{
        type: actionTypes.ResetPassword.RESET_PASSWORD_START
    }

}

export const resetPasswordEnd = (data) => {

    return{
        type: actionTypes.ResetPassword.RESET_PASSWORD_END,
        cdata: data
    }
}

export const resetPasswordFail = (err) => {

    return{
        type: actionTypes.ResetPassword.RESET_PASSWORD_FAIL,
        error: err
    }
}
