import React, { useEffect } from 'react'
import classes from './CourseRow.module.css'
const CourseRow = ({CourseName,courses,getCourseByCourseOffering,getPOSInfo, getCreditsAndCost, Teacher, CreditNumber, Availability, Campus, clicked, selectList,checkValue}) => {
    
    useEffect(()=>{
        if(selectList.length > 0){
            
          getCourseByCourseOffering(courses,selectList)
        }
      },[selectList])
    
    const changeList = (data) =>{
        getPOSInfo("selectList",data);
        getCourseByCourseOffering(courses,selectList)
        getCreditsAndCost(courses,selectList)
        
    }
    const handelCheck = (e) =>{
        var test = 0;
            for(let i = 0;i<selectList.length;i++){
              if(selectList[i] === e.target.value){
                var index = selectList.indexOf(selectList[i]);
                if(index > -1){
                    // selectList.splice(index, 1);
                    test ++;
                   
                    // changeList(selectList);
                }
              }
            }
            if(test === 0){
                
                for(let j = 0;j<courses.length;j++){
                    
                    if(courses[j].course.iid === e.target.value){
                        
                        var slc = courses[j].compList;
                        var exist = 0;
                        if(slc.length === 0){
                            selectList.push(e.target.value);
                            changeList(selectList);
                        }
                        for(let k = 0;k<slc.length;k++){
                            
                            if(slc[k].compType === 'CO REQUISITES'){
                                
                                var cmcourse = slc[k].courseCmp.iid;
                               
                                for(let z = 0;z<courses.length;z++){
                                   
                                    if(courses[z].course.courseBean.iid === cmcourse){
                                        
                                        // selectList.push(e.target.value);
                                        selectList.push(courses[z].course.iid);
                                        changeList(selectList);
                                    }
                                }
                            }else if(slc[k].compType === 'INCOMPATIBLE'){
                                
                                var cmcourse = slc[k].courseCmp.iid;
                                for(let z = 0;z<courses.length;z++){
                                    if(courses[z].course.courseBean.iid === cmcourse){
                                        
                                        for(let n = 0;n<selectList.length;n++){
                                            
                                            if(selectList[n] === courses[z].course.iid){
                                                    exist ++;
                                            }

                                        }
                                        // if(exist === 0){
                                        //     selectList.push(e.target.value);
                                        //     changeList(selectList);
                                        // }
                                        
                                        
                                        
                                    }
                                }
                            }
                        }
                        var notRegister = 0;
                        for(let h=0;h<selectList.length;h++){
                            if(selectList[h] === e.target.value){
                                notRegister ++;
                            }
                        }
                        if(notRegister === 0 && exist === 0){
                        selectList.push(e.target.value);
                        changeList(selectList);
                        }
                    }
                }
               
            }
    }
    
    return (
        
        <div className={classes.CourseRow} >
            <div onClick={clicked}>
                {CourseName}
            </div>
            <div onClick={clicked}>
                {Teacher}
            </div>
            <div onClick={clicked}>
                {CreditNumber}
            </div>
            <div onClick={clicked}>
                {Availability}
            </div>
            <div onClick={clicked}>
                {Campus}
            </div>
            
            <div>
            <label className={classes.cont}>
                    <button value={checkValue} className={classes.buttonChoose} id={checkValue} onClick={handelCheck}>Choose</button>
                    {/* <input type="checkbox" value={checkValue} id={checkValue} onClick={handelCheck}/> */}
                    {/* <span className={classes.checkmark}></span> */}
                  </label>
            </div>
        
        </div>
    )
}

export default CourseRow
