import { Paper, Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from '@material-ui/core'
import React, { useState } from 'react'
import ButtonSection from '../ButtonSection/ButtonSection'
import classes from './ContractSheet.module.css'
function createData(name, total) {
    return { name,  total };
}

const ContractSheet = ({generalCrd,majorCrd,coreCrd,electiveCrd,totalCrd}) => {

    const [showContractSheet, setShowContractSheet] = useState(false)

    const toggleContractSheet = () =>{
        setShowContractSheet(!showContractSheet)
    }

    const years=['TYPE', 'Second', 'Third']
    const rows = [
        // createData('General', 6, 6, 6, 18),
        // createData('Major', 12, 14, 14, 40),
        // createData('Core', 10, 12, 10, 32),
        // createData('Elective', 8, 4, 6, 18),
        // createData('Total', 36, 36, 36, 108),

         createData('General',  generalCrd),
        createData('Major',  majorCrd),
        createData('Core',  coreCrd),
        createData('Elective',  electiveCrd),
        createData('Total', totalCrd),
      ];
      
    return (
        <div className={classes.ContractSheet}>
            <ButtonSection Text='contract sheet' clicked={toggleContractSheet} toggleValue={showContractSheet} />
            {
                showContractSheet ?
                <div className={classes.Table}>
                    <TableContainer component={Paper}>
                        <Table className={classes.table} aria-label="customized table">
                            <TableHead>
                                <TableRow>
                                    {/* <TableCell>Years</TableCell> */}
                                    <TableCell>Courses Category</TableCell> 
                                    {/* {
                                        years.map(y=><TableCell align='center' key={y} >{y}</TableCell>)
                                    } */}
                                    <TableCell align='right'>Total</TableCell>
                                </TableRow>
                            </TableHead>
                            <TableBody>
                               

                                <TableRow key={'General'} style={{backgroundColor: '#D3D3D3'}}>
                                         <TableCell component="th" scope="row">
                                             {'General'}
                                         </TableCell>
                                         {/* <TableCell align="center" style={{ fontWeight: '800' }}>{row.name}</TableCell> */}
                                         {/* <TableCell align="center" style={{ fontWeight: '800' }}>{row.second}</TableCell>
                                         <TableCell align="center" style={{ fontWeight: '800' }}>{row.third}</TableCell> */}
                                         <TableCell align="right" style={{ fontWeight: '800',alignContent:'center' }}>{generalCrd}</TableCell>
                                </TableRow>

                                <TableRow key={'Major'}>
                                         <TableCell component="th" scope="row">
                                             {'Major'}
                                         </TableCell>
                                         {/* <TableCell align="center" style={{ fontWeight: '800' }}>{row.name}</TableCell> */}
                                         {/* <TableCell align="center" style={{ fontWeight: '800' }}>{row.second}</TableCell>
                                         <TableCell align="center" style={{ fontWeight: '800' }}>{row.third}</TableCell> */}
                                         <TableCell align="right" style={{ fontWeight: '800',alignContent:'center' }}>{majorCrd}</TableCell>
                                </TableRow>

                                <TableRow key={'Core'} style={{backgroundColor: '#D3D3D3'}}>
                                         <TableCell component="th" scope="row">
                                             {'Core'}
                                         </TableCell>
                                         {/* <TableCell align="center" style={{ fontWeight: '800' }}>{row.name}</TableCell> */}
                                         {/* <TableCell align="center" style={{ fontWeight: '800' }}>{row.second}</TableCell>
                                         <TableCell align="center" style={{ fontWeight: '800' }}>{row.third}</TableCell> */}
                                         <TableCell align="right" style={{ fontWeight: '800',alignContent:'center' }}>{coreCrd}</TableCell>
                                </TableRow>

                                <TableRow key={'Elective'}>
                                         <TableCell component="th" scope="row">
                                             {'Elective'}
                                         </TableCell>
                                         {/* <TableCell align="center" style={{ fontWeight: '800' }}>{row.name}</TableCell> */}
                                         {/* <TableCell align="center" style={{ fontWeight: '800' }}>{row.second}</TableCell>
                                         <TableCell align="center" style={{ fontWeight: '800' }}>{row.third}</TableCell> */}
                                         <TableCell align="right" style={{ fontWeight: '800',alignContent:'center' }}>{electiveCrd}</TableCell>
                                </TableRow>

                                <TableRow key={'Total'} style={{backgroundColor: '#D3D3D3'}}>
                                         <TableCell component="th" scope="row">
                                             {'Total'}
                                         </TableCell>
                                         {/* <TableCell align="center" style={{ fontWeight: '800' }}>{row.name}</TableCell> */}
                                         {/* <TableCell align="center" style={{ fontWeight: '800' }}>{row.second}</TableCell>
                                         <TableCell align="center" style={{ fontWeight: '800' }}>{row.third}</TableCell> */}
                                         <TableCell align="right" style={{ fontWeight: '800',alignContent:'center' }}>{totalCrd}</TableCell>
                                </TableRow>
                            </TableBody>
                        </Table>
                    </TableContainer>
                </div> :
                null
            }
        </div>
    )
}

export default ContractSheet
