import { Grid } from '@material-ui/core'
import React from 'react'
import classes from './FieldOfStudy.module.css'
import { createTheme, ThemeProvider } from '@material-ui/core';
import InputField from '../InputField/InputField';
const theme = createTheme({
    breakpoints:{
        values:{
            xs: 0,
            sm: 600,
            md: 960,
            lg: 1280,
            xl: 1920
        }
    },
    
});

const FieldOfStudy = ({programsList,selectedProgram,getInfoSP }) => {

    const handleChooseField = (e) =>{
        getInfoSP('selectedProgram',e.target.value);
        getInfoSP('majorType','BSC');
    }

    
    const Fields = programsList.map(fieldName=>{
        return(
            <Grid item xs={12} sm={4} md={3} lg={2} key={fieldName.name} style={{ width: '100%' }}>
                <InputField clicked={handleChooseField} fieldName={fieldName.name} value={fieldName.pgmid} name={'PREOGRAMS'}/>
            </Grid>
        )
    })

    return (
        <ThemeProvider theme={theme}>
            <div className={classes.FieldOfStudy}>
                <Grid container>
                    {
                        Fields
                    }
                </Grid>
            </div>
        </ThemeProvider>
    )
}

export default FieldOfStudy
