import React from "react";
import classes from "./SetPassword.module.css";
import {
  Button,
  CircularProgress,
  InputAdornment,
  TextField,
} from "@material-ui/core";

import { AccountCircle, Lock } from "@material-ui/icons";
import HomeHeader from "../../components/HomeHeader/HomeHeader";
import Footer from "../../components/Footer/Footer";

import * as setPasswordActionCreators from '../../store/actionsCreators/SetPasswordActions';

import Backdrop from '@material-ui/core/Backdrop';
import { makeStyles } from '@material-ui/core/styles';

import { connect } from "react-redux";
import {useState ,useEffect} from "react";



const useStyles = makeStyles((theme) => ({
    backdrop: {
      zIndex: theme.zIndex.drawer + 1,
      color: '#fff',
    },
  }));

const SetPassword = ({getUserByToken,setPassword,email,password,confirmPassword,token,loading,result,onLoadSetPassword,getSPInfo}) => {
  const classesMakeStyle = useStyles();

    const [openBackdrop, setOpenBackdrop] = React.useState(false);
    const [backdropText, setBackdropText] = React.useState('');


    useEffect(() => {
      
      const tokenURL = new URLSearchParams(window.location.search);
      onLoadSetPassword(tokenURL.get("token"));
      getUserByToken(tokenURL.get("token"));
      
    }, []);

    useEffect(() => {
      if(result !== ''){
      setOpenBackdrop(true);
      }
    }, [result]);

    const handleClose = () =>{
      setOpenBackdrop(false);
  }

  const handleOnChange = (e)=>{
    getSPInfo(e.target.name,e.target.value);
  }


  const submitForm =(e) =>{
    setPassword(email,password,confirmPassword,token);
  }


  return (
    <>
    <Backdrop className={classesMakeStyle.backdrop} open={openBackdrop} onClick={handleClose}>
                <div style={{ color: '#fff' }}>
                    {
                        
                        <div className={classes.textBoxApplication}>
                            {
                             result
                            }
                         </div>

                    }
                </div>    
        </Backdrop>
      <HomeHeader />
      <div className={classes.Login}>
        <div className={classes.LoginContainer}>
          <div className={classes.LoginContainerForm}>
            
              <h1>Set Password</h1>
              <br />

              <TextField
                label="NUid/NUmail"
                name="email"
                value={email}
                fullWidth
                onChange={handleOnChange}
                variant="outlined"
                style={{ margin: "20px 0px" }}
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <AccountCircle
                        style={{ color: "#900603" }}
                        fontSize="large"
                      />
                    </InputAdornment>
                  ),
                }}
              />
              <TextField
                label="Password"
                name="password"
                value={password}
                fullWidth
                onChange={handleOnChange}
                style={{ margin: "0px 0px 20px 0px" }}
                variant="outlined"
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <Lock style={{ color: "#900603" }} fontSize="large" />
                    </InputAdornment>
                  ),
                  type: "password",
                }}
              />
              <TextField
                label="Confirm Password"
                name="confirmPassword"
                value={confirmPassword}
                onChange={handleOnChange}
                fullWidth
                style={{ margin: "0px 0px 20px 0px" }}
                variant="outlined"
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <Lock style={{ color: "#900603" }} fontSize="large" />
                    </InputAdornment>
                  ),
                  type: "password",
                }}
              />

              {/* <input type="submit" value={<CircularProgress color="secondary" />} className={classes.SubmitBtn} /> */}
              <Button
                style={{ padding: "10px" }}
                variant="contained"
                fullWidth
                color="primary"
                onClick={submitForm}
              >
                {
                  loading ? <CircularProgress style={{ color: "#fff" }} size={35} />: <h2>Set Password</h2>
                }
                
              </Button>
            
          </div>
        </div>
      </div>
      <Footer />
    </>
  );
};
const mapStateToProps = (state) =>{
  return{
      email: state.SetPasswordR.email,
      password: state.SetPasswordR.password,
      confirmPassword: state.SetPasswordR.confirmPassword,
      result: state.SetPasswordR.result,
      userId: state.SetPasswordR.userId,
      loading: state.SetPasswordR.loading,
      error: state.SetPasswordR.error,
      token: state.SetPasswordR.token,
  };
}


const mapDispatchToProps = (dispatch) =>{
  return{
      getSPInfo : (name, value) => dispatch(setPasswordActionCreators.getSPInfo(name, value)),
      getUserByToken: (token) => dispatch(setPasswordActionCreators.getUserByToken(token)), 
      setPassword: (userId,password,confirmPassword,token)=> dispatch(setPasswordActionCreators.setPassword(userId,password,confirmPassword,token)),
      onLoadSetPassword: (token) => dispatch(setPasswordActionCreators.onLoadSetPassword(token)),
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(SetPassword);
