import classes from './PaymentStudentInfo.module.css';

const PaymentStudentInfo = ({personalInfo}) => {
    return ( 
        <div>
          
            <div className={classes.container}>
                <h2 className={classes.StudentInfoTitle}>STUDENT INFO</h2>
                <div className={classes.studentInfoBox}>
                    <div className={classes.studentBoxItems}>
                        <p>Student ID: <label name='name'>{personalInfo.userid}</label></p>
                        <p>Major: <label name='major'>{personalInfo.major}</label></p>
                        <p>Account Balance: <label name='account_balance'>{personalInfo.amount}</label></p>                      
                    </div>    
                </div>
            </div>
            <div className={classes.cardBar}>
                <h3 className={classes.cardBarText}>
                    CARD
                </h3>
            </div>
            </div>
           
     );
}
 
export default PaymentStudentInfo;