import classes from './Gallery.module.css';
import img1 from "../../../assets/images/HP_Banner.png";
import img2 from "../../../assets/images/MoreMountainsMorePeaks.jpeg";

const Gallery = () => {
    return ( 
        <div className={classes.gallery}>
            <img src={img2} className={classes.montainImg}/>
            
            <img src={img1} className={classes.montainImg}/>
        </div>
     );
}
 
export default Gallery;

