import {Component, React,useState,useEffect} from 'react'
import classes from './Login.module.css'
import {Button, CircularProgress, InputAdornment, TextField } from '@material-ui/core'
import { Link } from 'react-router-dom'
import { AccountCircle, Lock } from '@material-ui/icons'
import HomeHeader from '../../components/HomeHeader/HomeHeader'
import Footer from '../../components/Footer/Footer'
import { connect } from 'react-redux';
import { Redirect } from 'react-router';
import Application from '../Application/Application'
import StudentPortal from '../StudentPortal/StudentPortal'

import * as loginActionCreators from '../../store/actionsCreators/LoginActions';

const Login =({email,password,submitLogin,name,getLoginInfo,loading,error,errorShow,group,userId,status,application,applicant,major})=>{

    // constructor(props){
    //     super(props);  
    // }
    

     const submitForm = (e) =>{
        submitLogin(email,password);
    }
    
     
  
    // const 
    const handleOnchange = (e) =>{
        getLoginInfo(e.target.name, e.target.value);
    }

    useEffect(()=>{
         
        const testUserId = localStorage.getItem('major');
        if(testUserId === major && localStorage.getItem('userId') === userId && localStorage.setItem('group',group) === group && localStorage.setItem('applicant',applicant)=== applicant && localStorage.setItem('application',application) === application ){
                
            
        }else{
            if(loading === false){
        localStorage.setItem('userId',userId);
        localStorage.setItem('group',group);
        localStorage.setItem('name',name);
        localStorage.setItem('applicant',applicant);
        localStorage.setItem('application',application);
        localStorage.setItem('major',major);
            }
        }      
    },[userId,group,applicant,application,major,name]);
   
   
    if(userId !== null && major !== null && group === 'STUDENT' && applicant !== null && application !== null && name !== null && status === 'Success'&& loading === false){
        
        return <Redirect to='/StudentPortal'/>
    }else if(userId !== null && group === 'APPLICANT' && name !== null && loading === false && applicant !=='' ){
       console.log(applicant)
        return <Redirect to='/Apply-Application'/>
    }else 
    return (
        <>
            <HomeHeader />
            <div className={classes.Login} >
                <div className={classes.LoginContainer}>
                    <div className={classes.LoginContainerForm}>
                        <form autoComplete='off'>
                            <h1>LOG IN</h1>
                            <br />
                            <div className={classes.showLoginError}>
                                
                                {
                                    errorShow ? <h2>{error}</h2> : <></>
                                }
                            </div>
                            <TextField 
                            label="NUid/NUmail"
                            name="email" 
                            fullWidth 
                            variant='outlined'
                            value={email}
                            onChange={handleOnchange} 
                            style={{ margin: '20px 0px' }}
                            InputProps={{
                                startAdornment: (
                                <InputAdornment position="start">
                                    <AccountCircle style={{ color: '#900603' }} fontSize='large' />
                                </InputAdornment>
                                ),
                            }} />

                            <TextField 
                            label="Password"
                            name="password" 
                            value={password}
                            fullWidth 
                            onChange={handleOnchange} 
                            style={{ margin: '0px 0px 20px 0px' }}
                            variant='outlined' 
                            InputProps={{
                                startAdornment: (
                                <InputAdornment position="start">
                                    <Lock style={{ color: '#900603' }} fontSize='large' />
                                </InputAdornment>
                                ),
                                type: 'password'
                            }} />

                            {/* <input type="submit" value={<CircularProgress color="secondary" />} className={classes.SubmitBtn} /> */}
                            <Button style={{padding:'10px'}} variant="contained" fullWidth color='primary' onClick={submitForm}>
                                {/* { {loading} &&<CircularProgress style={{color:'#fff'}} size={35} />}
                                {{loading} && <h2>LOGIN</h2>}     */}
                                { 
                                    loading ? <CircularProgress style={{color:'#fff'}} size={35} /> : <h2>LOGIN</h2>
                                }
                            </Button>
                                
                        </form>
                        <div className={classes.ForgetPassword}>
                            <Link to='/ForgetPassword'>Forget Password ?</Link>
                        </div>
                        <div className={classes.SignUp}>
                            <span>Don't Have an Account ? </span><Link to='/CreateNUid'>Sign up</Link>
                        </div>
                        
                    </div>
                </div>
            </div>
            <Footer />
        </>
    )
                            

}

const mapStateToProps = (state) =>{
    return{
        email: state.LoginR.email,
        password: state.LoginR.password,
        loading: state.LoginR.loading,
        error: state.LoginR.error,
        errorShow: state.LoginR.errorShow,
        group: state.LoginR.group,
        userId: state.LoginR.userId,
        status: state.LoginR.status,
        applicant: state.LoginR.applicant,
        application: state.LoginR.application,
        major: state.LoginR.major,
        name: state.LoginR.name
    };
}


const mapDispatchToProps = (dispatch) =>{
    return{
        getLoginInfo : (name, value) => dispatch(loginActionCreators.getLoginInfo(name, value)), 
        submitLogin: (email,password)=> dispatch(loginActionCreators.submitLogin(email,password))
        
    };
}



export default connect(mapStateToProps, mapDispatchToProps)(Login)
