import React, { Component } from 'react'
import { Grid, makeStyles ,CircularProgress} from '@material-ui/core';
import * as Controls from '../../UI/Controls/Controls';
import OtherPersonalInformation from './OtherPersonalInformation';
import { connect } from 'react-redux';
import * as createNUidActionCreators from '../../store/actionsCreators/CreateNUidActions';
import { useEffect,useState } from "react";
import Backdrop from '@material-ui/core/Backdrop';
import classesSty from "./CreateNUid.module.css";

const  useStyle = makeStyles(theme => ({
    Form: {
        width: '100%'
    },
    SubTitle: {
        textTransform: 'uppercase',
        letterSpacing: '.5px',
        fontWeight: '500',
        margin: '10px 0px 0px 8px'
    },
    backdrop: {
        zIndex: theme.zIndex.drawer + 1,
        color: '#fff',
      },
    FaceIDLink: {
        margin: '10px 0px 0px 8px',
        textDecoration: 'underline',
        cursor: 'not-allowed'
    },
    SubmitCreatNUIdForm: {
        margin: '20px 0px 0px 8px',
        border: 'none',
        outline: 'none',
        background: '#900603',
        padding: '8px 40px',
        color: 'white',
        height: '55px',
        cursor: 'pointer'
    }

}));


const PersonalInformationForm  = ({ submitForm, handleSubmission, getInfo, firstname, lastname, middlename, dob, citizenship, phone, primarylanguage, landline, email, caddress, maritalstatus, gender, passportnumber, passportfile,loading,error,maritalStatusData,citizenshipData,primaryLanguagesData,getMaritalStatus,getPrimaryLanguages,getCitizenShip,userId,status }) => {
    
   
    const classes = useStyle();
    const [openBackdrop, setOpenBackdrop] = React.useState(false);
    

    useEffect(() => {
      if(status !== ''){
      setOpenBackdrop(true);
      }
    }, [status,userId]);

    const handleClose = () =>{
        setOpenBackdrop(false);
    }


    useEffect(() => {
        
        getMaritalStatus();
        getPrimaryLanguages();
        getCitizenShip();
        
      }, []);

    const handleOnchange = (e) =>{
       
        if(e.target.type === 'file'){
             getInfo(e.target.name, e.target.files[0])
        }else{
             getInfo(e.target.name, e.target.value);
        }
    }

    const submitCreateNuid = () =>{
        submitForm(firstname,middlename,lastname,dob,citizenship,phone,primarylanguage,landline, email,caddress,maritalstatus,gender,passportnumber, passportfile) 
    }
    
    return (
        <Grid container item xs={12}>
            <Backdrop className={classes.backdrop} open={openBackdrop} onClick={handleClose}>
                <div style={{ color: '#fff' }}>
                    {
                        
                        <div className={classesSty.textBoxApplication}>
                            {
                             status === 'Success' ? <p>Dear user, your NUID have been successfully registered, you can use it across all NU platforms.<br/>Check your email to confirm registration <br/> Your NUID is {userId}</p> : <p>Error while registration: {status}</p>
                            }
                         </div>

                    }
                </div>    
        </Backdrop>
            <Grid item xs={12}>
                <p className={ classes.SubTitle}>personal information</p>
            </Grid>
            <Grid item container xs={12}>
                {/* <form className={ classes.Form}  autoComplete='off'> */}
                    <Grid item container xs={12}>
                       
                        <Grid item xs={12} sm={4}>
                            <Controls.Input name='firstname' id='firstName' label='First Name' value={ firstname} handleOnchange={ handleOnchange} required={true}  />
                        </Grid>
                        <Grid item xs={12} sm={4}>
                            <Controls.Input name='middlename' id='middleName' label='Middle Name' value={ middlename} handleOnchange={ handleOnchange} required={true}  />
                        </Grid>
                        <Grid item xs={12} sm={4}>
                            <Controls.Input name='lastname' id='lastName' label='Last Name' value={ lastname} handleOnchange={ handleOnchange} required={true}   />
                        </Grid>
                        
                    </Grid>
                    <Grid item container xs={12}>
                        <Grid item xs={12} sm={4}>
                            <Controls.DateInput name='dob' id='DoB' label='Date of Birthday' value={ dob} handleOnchange={ handleOnchange} required={true}  />
                        </Grid>
                        <Grid item xs={12} sm={4}>
                            {
                            citizenshipData ? <Controls.SelectInput name='citizenship' id='citizenship' label="Citizenship" value={ citizenship} optionData={citizenshipData} handleOnchange={ handleOnchange} required={false}  ></Controls.SelectInput> : <></>
                            }                                                                          
                                                                                                                                                                                        
                        </Grid>
                        <Grid item xs={12} sm={4}>
                            <Controls.Input name='phone' id='phone' label='Phone Number' value={ phone} handleOnchange={ handleOnchange} required={true}    />
                        </Grid>
                    </Grid>
                    <Grid item container xs={12}>
                        <Grid item xs={12} sm={4}>
                            {
                            primaryLanguagesData ? <Controls.SelectInput name='primarylanguage' id='PrimaryLanguage' label='Primary Language' value={ primarylanguage} optionData={primaryLanguagesData} handleOnchange={ handleOnchange} required={true}/>:<></>
                            }
                        </Grid>
                        <Grid item xs={12} sm={4}>
                            <Controls.Input name='landline' id='LandLine' label="Landline" value={ landline} handleOnchange={ handleOnchange} required={false}   />
                        </Grid>
                        
                        <Grid item xs={12} sm={4}>
                            <Controls.Input name='email' id='Email' label="Email" value={ email} handleOnchange={ handleOnchange} required={true} type='email' />
                        </Grid>
                    </Grid>
                    <Grid item container xs={12}>
                        <Grid item xs={12}>
                            <Controls.Input name='caddress' id='Address' label='Address' value={ caddress} handleOnchange={ handleOnchange} required={true}   />
                        </Grid>     
                    </Grid>
                    <hr style={{ margin: '30px 8px' }} />
                    <Grid item xs={12}>
                        <p className={ classes.SubTitle}>other personal information</p>
                    </Grid>
                    <OtherPersonalInformation maritalstatus={ maritalstatus}  gender={ gender} passportnumber={ passportnumber} passportfile={ passportfile} handleOnchange={ handleOnchange} maritalStatusData={maritalStatusData}/>
                    <Grid item xs={12}>
                        <p className={ classes.FaceIDLink}>Open camera for Face ID to unlock accessibility features
                        </p>
                    </Grid>
                    
                    <Grid item xs={12}>
                    <button className={ classes.SubmitCreatNUIdForm} onClick={submitCreateNuid}>
                    { 
                         loading ? <CircularProgress style={{color:'#fff'}} size={35} /> : <h2>Submit</h2>
                    }
                    </button>
                    </Grid>
                {/* </form> */}
            </Grid>
           
        </Grid>
    )
}


const mapStateToProps = (state) =>{
    return{
        firstname: state.CreateNUidR.firstname,
        middlename: state.CreateNUidR.middlename,
        lastname: state.CreateNUidR.lastname,
        dob: state.CreateNUidR.dob,
        citizenship: state.CreateNUidR.citizenship,
        phone: state.CreateNUidR.phone,
        primarylanguage: state.CreateNUidR.primarylanguage,
        landline: state.CreateNUidR.landline,
        email: state.CreateNUidR.email,
        caddress: state.CreateNUidR.caddress,
        maritalstatus: state.CreateNUidR.maritalstatus,
        gender: state.CreateNUidR.gender,
        passportnumber: state.CreateNUidR.passportnumber,
        passportfile: state.CreateNUidR.passportfile,
        loading: state.CreateNUidR.loading,
        maritalStatusData: state.CreateNUidR.maritalStatusData,
        citizenshipData: state.CreateNUidR.citizenshipData,
        primaryLanguagesData: state.CreateNUidR.primaryLanguagesData,
        error: state.CreateNUidR.error,
        userId: state.CreateNUidR.userId,
        status: state.CreateNUidR.status

    };
}


const mapDispatchToProps = (dispatch) =>{
    return{
        getInfo : (name, value) => dispatch(createNUidActionCreators.getUserInfo(name, value)), 
        submitForm: (firstname,middlename,lastname,dob,citizenship,phone,primarylanguage,landline,email,caddress,maritalstatus,gender,passportnumber,passportfile)=> dispatch(createNUidActionCreators.submitTest(firstname,middlename,lastname,dob,citizenship,phone,primarylanguage,landline,email,caddress,maritalstatus,gender,passportnumber,passportfile)),
        getMaritalStatus : () => dispatch(createNUidActionCreators.getMaritalStatus()),
        getCitizenShip : () => dispatch(createNUidActionCreators.getCitizenShip()),
        getPrimaryLanguages : () => dispatch(createNUidActionCreators.getPrimaryLanguages())
    };
}

export default connect(mapStateToProps, mapDispatchToProps)(PersonalInformationForm)
