import React, { useEffect } from "react";
import CourseRow from "./CourseRow/CourseRow";
import Backdrop from "@material-ui/core/Backdrop";
import classes from "./CoursesTable.module.css";
import { makeStyles } from "@material-ui/core/styles";
import Popup from "../../Popup/Popup";
import ScheduleBox from "../../Popup/ScheduleBox/ScheduleBox";
import Informations from "../../Popup/Informations/Informations";



const useStyles = makeStyles((theme) => ({
  backdrop: {
    zIndex: theme.zIndex.drawer + 1,
    color: "#fff",
  },
}));

const CoursesTable = ({courses,selectList,getCourseByCourseOffering,getPOSInfo,getCourseOfferSchedulePopup,getTeacher,teacherfirstname, getCreditsAndCost,teacherlastname,Monday,Tuesday,Wednesday,Thursday,Friday,Saturday,Sunday}) => {
  const classesMakeStyle = useStyles();
 
  const [openBackdrop, setOpenBackdrop] = React.useState(false);
  const [dataCome, setDataCome] = React.useState(false);
  const [selectedCourse, setSelectedCourse] = React.useState("");
  const [selectedTeacher, setSelectedTeacher] = React.useState("");
  const [selectedCredit, setSelectedCredit] = React.useState("");
  const [selectedDescription, setSelectedDescription] = React.useState("");
  
  const [selectedPop, setSelectedPop] = React.useState("");
  const handleShowInfoCourse = (id,courseName,teacherName,credit,description)=>{//,monday,tuesday,wednesday,thursday,friday,saturday,sunday) => {
    
    setSelectedPop(id);
    setSelectedCourse(courseName);
    setSelectedTeacher(teacherName);
    setSelectedCredit(credit);
    setSelectedDescription(description); 
    
    setOpenBackdrop(true);
    
  };

  const handleClose = () => {
    getPOSInfo("monday","");
    getPOSInfo("tuesday","");
    getPOSInfo("wednesday","");
    getPOSInfo("thursday","");
    getPOSInfo("friday","");
    getPOSInfo("saturday","");
    getPOSInfo("sunday","");
    setOpenBackdrop(false);
  };

  const CoursesList = courses.map((course, index) => {
    getTeacher(course.course.teacherBean.iid);
    
    return (
      <CourseRow
        getCourseByCourseOffering={getCourseByCourseOffering}
        getPOSInfo={getPOSInfo}
        CourseName={course.course.courseBean.title}
        getCreditsAndCost={getCreditsAndCost}
        Teacher= {teacherfirstname}
        CreditNumber={course.course.courseBean.credit}
        Availability={course.course.capacity}
        Campus={course.course.campusBean.name}
        checkValue={course.course.iid}
        selectList={selectList}
        key={index}
        courses={courses}
        clicked={() => handleShowInfoCourse(course.course.iid,course.course.courseBean.title,teacherfirstname,course.course.courseBean.credit,course.course.courseBean.description)}//,Monday,Tuesday,Wednesday,Thursday, Friday, Saturday, Sunday)}

        // clicked={() => handleShowInfoCourse(course.course.courseBean.title,teacherfirstname,course.course.courseBean.credit,course.course.courseBean.description,Monday,Tuesday,Wednesday,Thursday, Friday, Saturday, Sunday)}
      />
    );
  });

  return (
    <>
      <Backdrop
        className={classesMakeStyle.backdrop}
        open={openBackdrop}
        onClick={handleClose}
      >
        <div className={classes.t}>
          <Popup>
            <Informations text={selectedDescription} courseName={selectedCourse} teacherName={selectedTeacher} credits={selectedCredit}/>
            {
            selectedPop !== ''?<ScheduleBox Name={selectedCourse} Monday={Monday} Tuesday={Tuesday} Wednesday={Wednesday} Thursday={Thursday} Friday={Friday} Saturday={Saturday} Sunday={Sunday} getCourseOfferSchedulePopup={getCourseOfferSchedulePopup} selectedPop={selectedPop}/> :<></>
            }
         </Popup>
        </div>
      </Backdrop>
      <div className={classes.CoursesTable}>
        <div className={classes.Title}>
          <div>Name</div>
          <div>Teacher</div>
          <div>Credits</div>
          <div>Availability</div>
          <div>Campus</div>
          <div>Select</div>
        </div>
        {CoursesList}
      </div>
    </>
  );
};


export default CoursesTable;
