
import * as actionTypes from '../actions/actions';



const initialState = {
    email: '',
    loading: false,
    token: '',
    newPassword: '',
    retryNewPassword: '',
    result: '',
    error: ''
    
}




const ChangeForgottenPasswordR =  (state = initialState, action) => {

    
    
    switch (action.type) {
        case actionTypes.ChangeForgottenPassword.GET_FP_INFO :
            return { ...state, [action.name] : action.value  }
        
        case actionTypes.ChangeForgottenPassword.CHANGE_FORGOTTEN_PASSWORD_START :
            return { ...state,loading: true}

        case actionTypes.ChangeForgottenPassword.CHANGE_FORGOTTEN_PASSWORD_END :
            return { ...state,loading: false,result: action.cdata.status} 
            
        case actionTypes.ChangeForgottenPassword.CHANGE_FORGOTTEN_PASSWORD_FAIL :
            return { ...state,loading: false,error: action.error}    
            
        case actionTypes.ChangeForgottenPassword.GET_ONLOADFP_INFO :
            return { ...state,loading: false,token: action.data}     

        case actionTypes.ChangeForgottenPassword.GET_USER_BY_TOKEN :
            return { ...state,loading: false,email: action.cdata.userid}  
        default:
            return state
    }
}

export default ChangeForgottenPasswordR;
