import Grid from "@material-ui/core/Grid";
import HomeHeader from "../../components/HomeHeader/HomeHeader";
import LogedInHeader from "../../components/LogedInHeader/LogedInHeader";
import classes from "./StudentPortal.module.css";
import { Button, Container } from "@material-ui/core";
import DueAmount from "../../components/StudentPortalComponents/DueAmount/DueAmount";
import Gallery from "../../components/StudentPortalComponents/Gallery/Gallery";
import Infos from "../../components/StudentPortalComponents/Infos/Infos";
import Contains from "../../components/StudentPortalComponents/Contains/Contains";
import Announcements from "../../components/StudentPortalComponents/Announcements/Announcements";
import Border from "../../components/Border/Border";
import Divider from "../../components/Divider/Divider";
import img1 from "../../assets/images/Major.png";
import img2 from "../../assets/images/Campus.png";
import img3 from "../../assets/images/SemesterDate.png";
import img4 from "../../assets/images/Probation.png";
import img5 from "../../assets/images/NoProbation.png";
import Footer from "../../components/Footer/Footer";
import { useEffect } from 'react';
import * as StudentPortalAction from "../../store/actionsCreators/StudentPortalAction";
import { connect } from "react-redux";

function StudentPortal({dueAmount,majorName,campus,passedCrd,credits,gpa,probation,semester,getDueAmount,getMajor,getCampus,getCredits,getGpa,getProbation,getSemester}) {
  
  useEffect(()=>{
    window.scrollTo(0,0);
},[])
  useEffect(()=>{
    getDueAmount(localStorage.getItem('userId'),localStorage.getItem('major'));
    getMajor(localStorage.getItem('major'));
    getCampus(localStorage.getItem('userId'));
    getCredits(localStorage.getItem('userId'),localStorage.getItem('major'));
    getGpa(localStorage.getItem('userId'),localStorage.getItem('major'));
    getSemester(localStorage.getItem('userId'),localStorage.getItem('major'));
    getProbation(localStorage.getItem('userId'),localStorage.getItem('major'));

  },[]);
  
  return (
    <>
      <HomeHeader LogedIn={true} />
      <LogedInHeader />
      <DueAmount dueAmount={dueAmount}/>
      <div className={classes.checkAccount}>
        <Container>
          <Gallery />
          <Divider padding="30px" />
          <Grid container spacing={3}>
            <Grid item xs={12} md={6} sm={3}>
              <Infos info="Major" infovalue={majorName} image={img1} />
            </Grid>
            <Grid item xs={12} md={6} sm={1}>
              <Border height="50px" />
            </Grid>
            <Grid item xs={12} md={6} sm={4}>
              <Infos info="Campus" infovalue={campus} image={img2} />
            </Grid>
            <Grid item xs={12} md={6} sm={1}>
              <Border height="50px" />
            </Grid>
            <Grid item xs={12} md={6} sm={3}>
              <Infos info="Date" infovalue={semester} image={img3} />
            </Grid>
          </Grid>
          <Divider padding="20px" />
          { 
          <Contains
            title="Remaining Credits"
            text={"You already passed "+passedCrd+" credits of yours courses and still have..."}
            creditnumber={credits}
            Type="credits"
          />
          }
          <Divider padding="10px" />
          {probation === 'YES'?
          <Contains
            title="Student Probation"
            text="GPA Status according to finishing credits"
            creditnumber={gpa}
            Type="GPA"
            prob="Probation"
            image={img4}
          />
        : <Contains
          title="Student Probation"
          text="GPA Status according to finishing credits"
          creditnumber={gpa}
          Type="GPA"
          prob="No-Probation"
          image={img5}
      />}
          <Divider padding="20px" />
          <Announcements />
          <Divider padding="20px" />
        </Container>
      </div>
      <Footer />
    </>
  );
}

const mapStateToProps = (state) => {
  return {
    dueAmount: state.StudentPortalR.dueAmount,
    majorName: state.StudentPortalR.majorName,
    campus: state.StudentPortalR.campus,
    credits: state.StudentPortalR.credits,
    gpa: state.StudentPortalR.gpa,
    probation:state.StudentPortalR.probation,
    semester: state.StudentPortalR.semester,
    passedCrd: state.StudentPortalR.passedCrd
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
      getDueAmount: (stdId,major) => dispatch(StudentPortalAction.getDueAmount(stdId,major)),
      getMajor: (major) => dispatch(StudentPortalAction.getMajor(major)),
      getCampus: (stdId) => dispatch(StudentPortalAction.getCampus(stdId)),
      getSemester: (stdId,major) => dispatch(StudentPortalAction.getSemester(stdId,major)),
      getCredits: (stdId,major) => dispatch(StudentPortalAction.getCredits(stdId,major)),
      getGpa: (stdId,major) => dispatch(StudentPortalAction.getGpa(stdId,major)),
      getProbation: (stdId,major) => dispatch(StudentPortalAction.getProbation(stdId,major)),
      };
};
export default connect(mapStateToProps, mapDispatchToProps)(StudentPortal);
