import * as actionTypes from '../actions/actions';



const initialState = {
    yearsData: [],
    semestersData: [],
    year: '',
    semester: '',
    courses: '',
    userId:'',
    major: ''
    
}




const HistoryR =  (state = initialState, action) => {

    switch (action.type) {
        case actionTypes.History.GET_INFO_HISTORY :
            return { ...state, [action.name] : action.value  }
        
        case actionTypes.History.GET_SEMESTER_HISTORY :
           
            return { ...state,semestersData: action.sdata}

        case actionTypes.History.GET_YEARS :
            return { ...state,yearsData: action.ydata} 
            
        case actionTypes.History.GET_HISTORY_DATA :
            return { ...state,userId: action.hdata.userid , major: action.hdata.major}    
            
        case actionTypes.History.GET_HISTORY_COURSES :
            return { ...state,courses: action.cdata}     

        default:
            return state
    }
}

export default HistoryR;