import * as actionTypes from '../actions/actions';

const initialState = {
    email: '',
    password: '',
    confirmPassword: '',
    result: '',
    userId: '',
    loading: '',
    error: '',
    token: '',
    loading: false,
    
}

const SetPasswordR =  (state = initialState, action) => {
    switch (action.type) {

        case actionTypes.UpdateNUid.GET_USER_INFO :
            return { ...state, [action.name] : action.value  }

        case actionTypes.SetPassword.SETPASSWORD_START :
            return { ...state,loading: true}
    
        case actionTypes.SetPassword.SETPASSWORD_END :
            return { ...state,loading: false,userId: action.userData.userId,result: action.userData} 
                
        case actionTypes.SetPassword.SETPASSWORD_FAIL :
            return { ...state,loading: false,error: action.error}

        case actionTypes.SetPassword.GET_USER_DATA :
            return { ...state,loading: false,email: action.udata.userid}  

        case actionTypes.SetPassword.GET_ONLOADSP_INFO :
            return { ...state,loading: false,token: action.data}  
        default:
            return state
    }
}

export default SetPasswordR;
