import React from "react";
import classes from "./CoursesToDropRow.module.css";
const CoursesToDropRow = ({ CourseId, CourseTitle, Drop,checkValue ,listToDrop}) => {
  const handelCheck = (e) =>{
    var test = 0;
    for(let i = 0;i<listToDrop.length;i++){
      if(listToDrop[i] === e.target.value){
        var index = listToDrop.indexOf(listToDrop[i]);
        if(index > -1){
          listToDrop.splice(index, 1);
          test ++;
        }
      }
    }
    if(test === 0){
    listToDrop.push(e.target.value);
    }
    console.log(listToDrop)
  }
  return (
    <div className={classes.CoursesToDropRow}>
      <div>{CourseId}</div>
      <div>{CourseTitle}</div>
      <div>
        <label className={classes.cont}>
          <input type="checkbox" value={checkValue} onClick={handelCheck}/>
          <span className={classes.checkmark}></span>
        </label>
      </div>
    </div>
  );
};

export default CoursesToDropRow;
