import React from "react";
import classes from "./SendForgettenPasswordMail.module.css";
import {
  Button,
  CircularProgress,
  InputAdornment,
  TextField,
} from "@material-ui/core";
import { Link } from "react-router-dom";
import { AccountCircle, Lock } from "@material-ui/icons";
import HomeHeader from "../../components/HomeHeader/HomeHeader";
import Footer from "../../components/Footer/Footer";
import * as ForgetPasswordCreators from "../../store/actionsCreators/ForgetPasswordAction";
import { connect } from "react-redux";
import { useEffect,useState } from "react";
import Backdrop from '@material-ui/core/Backdrop';
import { makeStyles } from '@material-ui/core/styles';


const useStyles = makeStyles((theme) => ({
    backdrop: {
      zIndex: theme.zIndex.drawer + 1,
      color: '#fff',
    },
  }));


const SendForgettenPasswordMail = ({loading,email,error,result,forgetPassword,getFPInfo}) => {

  const classesMakeStyle = useStyles();

    const [openBackdrop, setOpenBackdrop] = React.useState(false);
    const [backdropText, setBackdropText] = React.useState('');

    useEffect(() => {
      if(result !== ''){
      setOpenBackdrop(true);
      }
    }, [result]);


    const handleClose = () =>{
      setOpenBackdrop(false);
  }

  const handleOnChange = (e)=>{
    getFPInfo(e.target.name,e.target.value);
  }


  const submitForm =(e) =>{
    forgetPassword(email);
  }
  return (
    <>
    <Backdrop className={classesMakeStyle.backdrop} open={openBackdrop} onClick={handleClose}>
                <div style={{ color: '#fff' }}>
                    {
                        
                        <div className={classes.textBoxApplication}>
                            {
                             result === 'Success' ? <p>Mail sent successfuly, check your email to reset your password</p> : <p>Unknown user, check the entered email</p>
                            }
                         </div>

                    }
                </div>    
        </Backdrop>
      <HomeHeader />
      <div className={classes.Login}>
        <div className={classes.LoginContainer}>
          <div className={classes.LoginContainerForm}>
            <form autoComplete="off">
              <h1>Forget Password</h1>
              <br />

              <TextField
                label="NUmail"
                name="email"
                value={email}
                onChange={handleOnChange}
                fullWidth
                variant="outlined"
                style={{ margin: "20px 0px" }}
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <AccountCircle
                        style={{ color: "#900603" }}
                        fontSize="large"
                      />
                    </InputAdornment>
                  ),
                }}
              />

              <Button
                style={{ padding: "10px" }}
                variant="contained"
                fullWidth
                onClick={submitForm}
                color="primary"
              >

                {
                  loading ? <CircularProgress style={{ color: "#fff" }} size={35} />: <h2>Recovery Password</h2>
                }
                
              </Button>
            </form>
          </div>
        </div>
      </div>
      <Footer />
    </>
  );
};

const mapStateToProps = (state) => {
  return {
     email: state.ForgetPasswordR.email,
     loading: state.ForgetPasswordR.loading,
     error: state.ForgetPasswordR.error,
     result: state.ForgetPasswordR.result,
  }
}

const mapDispatchToProps = (dispatch) => {
  return {
      getFPInfo: (name,value) => dispatch(ForgetPasswordCreators.getFPInfo(name,value)),
      forgetPassword: (email) => dispatch(ForgetPasswordCreators.forgetPassword(email)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(SendForgettenPasswordMail);
