import React from "react";
import ReceiptRow from "./AcceptEnrolmentRow/AcceptEnrolmentRow";
import classes from "./AcceptEnrTable.module.css";

const AcceptTableContent = ({table,acceptStudent}) => {

  // if(tableInfo !== ''){
  // const Receipts = tableInfo;
  // }else{
  //   const Receipts =[];
  // }

  const ReceiptsList = table.map((receipt, index) => {
    return (
      <ReceiptRow
      acceptStudent={acceptStudent}
        student={receipt.studentId}
        major={receipt.majorName}
        course={receipt.courseName}
        accept={{"enrolmentId":receipt.enrId,
                "majorId":receipt.majorId}}


        key={index}
      />
    );
  });

  return (
    <>
      <div className={classes.ReceiptsTable}>
        <div className={classes.Title}>
          <div>Student Id</div>
          <div>Major</div>
          <div>Course</div>
          <div></div>
        </div>
        {ReceiptsList}
      </div>
    </>
  );
};

export default AcceptTableContent;
