import * as actionTypes from '../actions/actions';

const initialState = {
    students: '',
    programs: '',
    year: '',
    semester: '',
    allSemester: ''
}

const AcceptStudentR =  (state = initialState, action) => {
    switch (action.type) {
        case actionTypes.AccpetStudent.GET_INFORMATION_ACCEPT :
            return { ...state, [action.name] : action.value  }

        case actionTypes.AccpetStudent.GET_STUDENTS_PENDING :
            return { ...state,students: action.cdata}
            
        case actionTypes.AccpetStudent.GET_ALL_ACCEPT_SEMESTER :
                return { ...state,allSemester: action.cdata}
        // case actionTypes.ApplyApplication.GET_PROGRAMS :
        //     return { ...state,loading: false,programs: action.PGMData}

        default:
            return state
    }
}

export default AcceptStudentR;