import React from "react";
import CourseRow from "./CoursesSelectedRow/CoursesSelectedRow";
import classes from "./CoursesSelectedTable.module.css";


const CoursesSelectedTable = ({courses,category,getTeacher,teacherfirstname,selectList,getPOSInfo,getCourseByCourseOffering,getCreditsAndCost,allCourses}) => {
 

  const CoursesList = courses.map((course, index) => {
    getTeacher(course.course.teacherBean.iid);
    return (
      <CourseRow
        getCourseByCourseOffering={getCourseByCourseOffering}
        courseOffer={course.course.iid}
        CourseName={course.course.courseBean.title}
        Category={category}
        Teacher= {teacherfirstname}
        getCreditsAndCost={getCreditsAndCost}
        selectList={selectList}
        CreditNumber={course.course.courseBean.credit}
        Campus={course.course.campusBean.name}
        getPOSInfo={getPOSInfo}
        courses={courses}
        allCourses={allCourses}
        key={index}
        />
    );
  });

  return (
    <>
      <div className={classes.CoursesTable}>
        <div className={classes.Title}>
          <div>Course Name</div>
          {/* <div>Category</div> */}
          <div>Teacher</div>
          <div>Credits</div>
          <div>Campus</div>
          <div>Delete</div>
        </div>
        {CoursesList}
      </div>
    </>
  );
};




export default CoursesSelectedTable;