import React from 'react'
import classes from './BoxWithTitle.module.css'

const BoxWithTitle = ({title, children}) => {
    return (
        <div className={classes.BoxWithTitle}>
            <div className={classes.TitleSection}>{title}</div>
            <div className={classes.BodySection}>
                {children}
            </div>
        </div>
    )
}

export default BoxWithTitle
