import React from "react";
import ReceiptRow from "./ReceiptRow/ReceiptRow";
import classes from "./ReceiptTable.module.css";

const ReceiptsTable = ({table}) => {

  // if(tableInfo !== ''){
  // const Receipts = tableInfo;
  // }else{
  //   const Receipts =[];
  // }

  const ReceiptsList = table.map((receipt, index) => {
    return (
      <ReceiptRow
        Date={receipt.date}
        Ref={receipt.ref}
        CrDr={receipt.crdr}
        Amount={receipt.amount}
        key={index}
      />
    );
  });

  return (
    <>
      <div className={classes.ReceiptsTable}>
        <div className={classes.Title}>
          <div>Date</div>
          <div>Ref</div>
          <div>Cr/Dr</div>
          <div>Amount</div>
        </div>
        {ReceiptsList}
      </div>
    </>
  );
};

export default ReceiptsTable;
