import classes from "./Cost.module.css";

const Cost = ({totalSelection,creditCost}) => {
  return (
    <div className={classes.container}>
      <span className={classes.cost}>Cost</span>
      <span className={classes.costValue}>
        {totalSelection * creditCost}
        {/* <select name="currencies" id="cars">
          <option value="dollar">$</option>
          <option value="euro"></option>
        </select> */}
      </span>
    </div>
  );
};

export default Cost;
