import React from 'react'
const ImageBackground = ({bg}) => {

    const ContainerStyle = {
        'width' : '100%',
        'margin' : '0px',
        'padding' : '0px',
    }

    const ImageStyle = {
        'width' : '100%',
        'height' : 'auto'
    }

    return (
        <div style={ContainerStyle}>
            <img src={bg} alt="" style={ImageStyle} />
        </div>
    )
}

export default ImageBackground
