import React from 'react'
import BoxToSelect from '../../BoxToSelect/BoxToSelect'
import InputField from '../../InputField/InputField'
import Prerequisites from '../../Prerequisites/Prerequisites'
import classes from './Master.module.css'
const Master = ({majors,setMajorType,selectedMajor,prerequisites}) => {
    const handleChooseField = (e) =>{
        
        setSelectedMajor(e.target.value);
    }

    const setSelectedMajor = (data) =>{
        setMajorType('selectedMajor',data)
    }

    const Majors = majors.map(MajorName=>{
        return(
            <div key={MajorName.name} style={{ width: '100%' }}>
                <InputField clicked={handleChooseField} fieldName={MajorName.name} value={MajorName.majorid} name={'MASTER'}/>
            </div>
        )
    })
    return (
        <div className={classes.Master}>
            <div className={classes.MajorsBox}>
                <BoxToSelect toShow = {Majors} />
            </div>
            <div className={classes.Space}></div>
            <div className={classes.Prerequisites}>
                <Prerequisites prerequisites={prerequisites} />
            </div>
        </div>
    )
}

export default Master
