import classes from './BoxWithTitleAndWhiteBack.module.css';
import Divider from '../Divider/Divider'

const BoxWithTitleAndWhiteBack = ({title, children}) => {
    return ( 
        <div className={classes.container}>
            <h3 className={classes.title}>{title}</h3>
            <Divider padding="5px" />
            <div className={classes.subContainer}>
                {children}
            </div>
        </div>
     );
}
 
export default BoxWithTitleAndWhiteBack;
