import axios from 'axios';
import * as actionTypes from '../actions/actions';
import {digid , lms} from './Constants'
export const getUserInfo = (name, value) => {
    return{
        type: actionTypes.CheckAccount.GET_USER_INFO,
        name: name,
        value: value
    }
}


export const getInformation = (userId,major) => {
    return dispatch =>{
        
        
        var link = lms+'/nuversity/api/lms/getStudentCheckAccountInfo/'+userId+'/'+major;//+localStorage.getItem("userId")+"/"+localStorage.getItem("major");
       axios.get(link)
            .then(res=>{
               
                dispatch(getInformationData(res.data));
                
                        
            }).catch(err => {
                console.log(err)
            })
       
    }
}

export const getInformationData = (data) => {

    return{
        type: actionTypes.CheckAccount.GET_INFORMATION,
        userData: data
    }
}

export const getCheckInfos = (from,to,userId,major) => {
    
    return dispatch =>{
        dispatch(getCheckInfosStart());
        var FD = new FormData();
        FD.append("from", from);
        FD.append("to", to);
        FD.append("userId", userId);
        FD.append("major", major);
           
            axios(
                {
                    method: "get",
                    url: lms+"/nuversity/api/lms/getCheckAccountInfo/"+from+"/"+to+"/"+userId+"/"+major,
                   
                    
                }
                

            )

                .then(res=>{
                    
                    dispatch(getCheckInfosEnd(res.data));
                        
                        
                        }).catch(err => {
                            dispatch(getCheckInfosFail(err));
                        })
                }
}

export const getCheckInfosStart = () => {

    return{
        type: actionTypes.CheckAccount.GET_CHECK_INFOS_START
    }
}

export const getCheckInfosEnd = (data) => {

    return{
        type: actionTypes.CheckAccount.GET_CHECK_INFOS_END,
        userData: data
    }
}

export const getCheckInfosFail = (err) => {

    return{
        type: actionTypes.CheckAccount.GET_CHECK_INFOS_FAIL,
        userData: err
    }
}