import axios from 'axios';
import * as actionTypes from '../actions/actions';
import {digid , lms} from './Constants'
export const getUserInfo = (name, value) => {
    return{
        type: actionTypes.UpdateNUid.GET_USER_INFO,
        name: name,
        value: value
    }
}

export const getUserById = (id) => {
    return dispatch =>{
        
        
        var link = digid+'/nuversity/api/digid/user/'+id;
        
       axios.get(link)
            .then(res=>{
                
                dispatch(getUserData(res.data));
                
                        
            }).catch(err => {
                console.log(err)
            })
            
    }
}

export const updateContact = (userId,citizenship,phone,landline,caddress,maritalstatus,passportnumber,passportfile) => {
    
    return dispatch =>{
        dispatch(updateNUidStart());
        var FD = new FormData();
        FD.append("userid", userId);
        FD.append("caddress", caddress);
        FD.append("citizenship", citizenship);
        FD.append("landline", landline);
        FD.append("maritalstatus", maritalstatus);
        FD.append("phone", phone);
        FD.append("passportnumber",passportnumber);
        FD.append("passportfile", passportfile);

            axios(
                {
                    method: "put",
                    url: digid+"/nuversity/api/digid/updateContact",
                    data: FD,
                    headers: { "Content-Type": "multipart/form-data" },
                }
                

            )

                .then(res=>{
                    
                    dispatch(updateNUidEnd(res.data));
                        
                        
                        }).catch(err => {
                            dispatch(updateNUidFail(err));
                        })
                }
}

export const updateNUidStart = () => {

    return{
        type: actionTypes.UpdateNUid.UPDATENUID_START
    }

}

export const updateNUidEnd = (data) => {

    return{
        type: actionTypes.UpdateNUid.UPDATENUID_END,
        userData: data
    }
}

export const updateNUidFail = (err) => {

    return{
        type: actionTypes.UpdateNUid.UPDATENUID_FAIL,
        error: err
    }
}

export const getUserData = (data) => {

    return{
        type: actionTypes.UpdateNUid.GET_USER_DATA,
        udata: data
    }
}


