import React, { useState } from "react";
import Divider from "../../components/Divider/Divider";
import Footer from "../../components/Footer/Footer";
import BoxWithTitle from "../../components/BoxWithTitle/BoxWithTitle";
import TotalCreditsAndCost from "../../components/PlanOfStudyComponents/TotalCreditsAndCost/TotalCreditsAndCost";
import BoxWithTitleAndWhiteBack from "../../components/BoxWithTitle2/BoxWithTitleAndWhiteBack";
import Grid from "@material-ui/core/Grid";
import Gpa from "../../components/PlanOfStudyComponents/Gpa/Gpa";
import Probation from "../../components/PlanOfStudyComponents/Probation/Probation";
import Infos from "../../components/PlanOfStudyComponents/Infos/Infos";
import MaxAndMinCredits from "../../components/PlanOfStudyComponents/MaxAndMinCredits/MaxAndMinCredits";
import CourseInfo from "../../components/PlanOfStudyComponents/CourseInfo/CourseInfo";import HomeHeader from "../../components/HomeHeader/HomeHeader";
import LogedInHeader from "../../components/LogedInHeader/LogedInHeader";
import classes from "./PlanOfStudy.module.css";
import { Button, Container, CircularProgress } from "@material-ui/core";
import { createTheme } from "@material-ui/core/styles"; 	
import Courses from "../../components/PlanOfStudyComponents/Courses/Courses";
import purple from "@material-ui/core/colors/purple";
import CoursesSelected from "../../components/PlanOfStudyComponents/CoursesSelected/CoursesSelected"
import { connect } from "react-redux";
import * as PlanOfStudyActionCreators from "../../store/actionsCreators/PlanOfStudyAction";
import { useEffect } from "react";
import { Backdrop, makeStyles } from '@material-ui/core';

const useStyles = makeStyles((theme) => ({
    backdrop: {
      zIndex: theme.zIndex.drawer + 1,
      color: '#fff',
    },
  }));

const theme = createTheme({
  palette: {
    primary: {
      main: purple[500],
    },
    secondary: {
      main: "#900603",
    },
  },
});

const PlanOfStudy = ({getGpa,gpa,credits,creditCost,getCreditsAndCost,getProbationStudent,probation,courseType,coursesS,registerCourses,getPOSInfo,getMajor,getCourseOffer,getCourseByCourseOffering,majorname,getContractSheet,general,major,core,elective,courses,selectList,
  Monday,
  Tuesday,
  Wednesday,
  Thursday,
  Friday,
  Saturday,
  Sunday,
  teacherfirstname,
  teacherlastname,
  getCourseOfferSchedulePopup,
  getTeacher,
  getMajorCrdCost,
  result,
  loading}) => {

    const classesMakeStyle = useStyles();

    const [openBackdrop, setOpenBackdrop] = React.useState(false);
    useEffect(()=>{
      window.scrollTo(0,0);
  },[])
  
    useEffect(() => {
        if(result !== ''){
        setOpenBackdrop(true);
        }
      }, [result]);
  
      const handleOnClose = () =>{
        setOpenBackdrop(false);
    }


  useEffect(() => {
    getMajorCrdCost(localStorage.getItem('major'));
    getGpa(localStorage.getItem('userId'),localStorage.getItem('major'));
    getMajor(localStorage.getItem('major'));
    getContractSheet(localStorage.getItem('userId'),localStorage.getItem('major'));
   
    getProbationStudent(localStorage.getItem('userId'),localStorage.getItem('major'));
  }, []);


  useEffect(()=>{
    if(courseType !== ''){
      getCourseOffer(localStorage.getItem('userId'),localStorage.getItem('major'),courseType)
    }
  },[courseType]);

  const submitRegistration = () =>{
    registerCourses(selectList,localStorage.getItem('userId'),localStorage.getItem('major'));
  }
  const [totalSelection,setTotalSelection] = useState(selectList.length);
  return (
    <>
    {result !== '' ?
    <Backdrop className={classesMakeStyle.backdrop} open={openBackdrop} onClick={handleOnClose}>
                <div style={{ color: '#fff' }}>
                    {
                        
                        <div className={classes.textBoxApplication}>
                            {
                             
                              
                                  <p>Courses Registered Successfully.<br/></p>
                             
                           
                            }
                         </div>

                    }
                </div>    
        </Backdrop> : <></>
}
      <HomeHeader LogedIn={true} />
      <LogedInHeader />
      <div className={classes.Plan}>
        <Container>
          <BoxWithTitle title="Plan Of Study">
            <Divider padding="5px" />
            <BoxWithTitleAndWhiteBack title="Your Current Situation">
              <Grid container spacing={3}>
                <Grid item xs={12} md={6} sm={3}>
                  <Gpa gpa={gpa} />
                </Grid>
                <Grid item xs={12} md={6} sm={3}>
                  <Probation probation={probation}/>
                </Grid>
                <Grid item xs={12} md={6} sm={3}>
                  <Infos title="your major" text={majorname} />
                </Grid>
                <Grid item xs={12} md={6} sm={3}>
                  <Infos
                    title="second quarter"
                    text="................................................................................................................................."
                  />
                </Grid>
              </Grid>
            </BoxWithTitleAndWhiteBack>
            <Divider padding="10px" />
            <BoxWithTitleAndWhiteBack title="Contract Sheet">
              <Grid container spacing={3}>
                <Grid item xs={12} md={6} sm={5}>
                  <MaxAndMinCredits />
                </Grid>
                <Grid item xs={12} md={6} sm={7}>
                  <CourseInfo generalrequired={general[0]} majorrequired={major[0]} corerequired={core[0]} electiverequired={elective[0]} generalacquired={general[1]} majoracquired={major[1]} coreacquired={core[1]} electiveacquired={elective[1]} generaltogo={general[2]} majortogo={major[2]} coretogo={core[2]} electivetogo={elective[2]} />
                </Grid>
              </Grid>
            </BoxWithTitleAndWhiteBack>
            <Divider padding="10px" />
            <BoxWithTitleAndWhiteBack title="Select Your Category">
            
                  <Courses coursesArray={courses} listToSelect={selectList} getPOSInfo={getPOSInfo} courseType={courseType} getCourseByCourseOffering={getCourseByCourseOffering}
                          Monday={Monday}
                          Tuesday={Tuesday}
                          Wednesday={Wednesday}
                          Thursday={Thursday}
                          Friday={Friday}
                          Saturday={Saturday}
                          Sunday={Sunday}
                          teacherfirstname={teacherfirstname}
                          teacherlastname={teacherlastname}
                          getCourseOfferSchedulePopup={getCourseOfferSchedulePopup}
                          getTeacher={getTeacher}
                          getCreditsAndCost={getCreditsAndCost} />
            
            </BoxWithTitleAndWhiteBack>
            <Divider padding="10px" />
            <BoxWithTitleAndWhiteBack title="All Courses Selected">
            {
                 coursesS !=='' ? <CoursesSelected allCourses={courses} coursesArray={coursesS} getTeacher={getTeacher} teacherfirstname={teacherfirstname}
                 teacherlastname={teacherlastname} selectList={selectList} getCreditsAndCost={getCreditsAndCost} getPOSInfo={getPOSInfo} getCourseByCourseOffering={getCourseByCourseOffering} category="Core"/>: <></>
            }
            </BoxWithTitleAndWhiteBack>
            <Divider padding="50px" />
            <TotalCreditsAndCost totalSelection={credits} creditCost={creditCost}/>
            <Divider padding="5px" />
          </BoxWithTitle>
          <Divider padding="5px" />
          <div className={classes.but}>
            <Button
              style={{ padding: "10px" , width: "200px" }}
              variant="contained"
              color="primary"
              onClick={submitRegistration}
              // fullWidth
            >
              {
                  loading ? <CircularProgress style={{ color: "#fff" }} size={35} />: <h2>Save</h2>
                }
              
            </Button>
          </div>
        </Container>
      </div>
      <Footer />
    </>
  );
};

const mapStateToProps = (state) => {
  return {
    gpa: state.PlanOfStudyR.gpa,
    probation: state.PlanOfStudyR.probation,
    majorname: state.PlanOfStudyR.majorname,
    teacherfirstname: state.PlanOfStudyR.teacherfirstname,
    teacherlastname: state.PlanOfStudyR.teacherlastname,
    contractsheetname: state.PlanOfStudyR.contractsheetname,
    general: state.PlanOfStudyR.general,
    major: state.PlanOfStudyR.major,
    core: state.PlanOfStudyR.core,
    elective: state.PlanOfStudyR.elective,
    coursename: state.PlanOfStudyR.coursename,
    credit: state.PlanOfStudyR.credit,
    coursedescription: state.PlanOfStudyR.coursedescription,
    campus: state.PlanOfStudyR.campus,
    courseType: state.PlanOfStudyR.courseType,
    capacity: state.PlanOfStudyR.capacity,
    courses: state.PlanOfStudyR.courses,
    loading: state.LoginR.loading,
    error: state.LoginR.error,
    selectList: state.PlanOfStudyR.selectList,
    coursesS: state.PlanOfStudyR.coursesS,
    Monday: state.PlanOfStudyR.monday,
    Tuesday: state.PlanOfStudyR.tuesday,
    Wednesday: state.PlanOfStudyR.wednesday,
    Thursday: state.PlanOfStudyR.thursday,
    Friday: state.PlanOfStudyR.friday,
    Saturday: state.PlanOfStudyR.saturday,
    Sunday: state.PlanOfStudyR.sunday,
    teacherfirstname: state.PlanOfStudyR.teacherfirstname,
    teacherlastname: state.PlanOfStudyR.teacherlastname,
    credits: state.PlanOfStudyR.credits,
    creditCost: state.PlanOfStudyR.creditCost,
    result: state.PlanOfStudyR.result
  };
};


const mapDispatchToProps = (dispatch) => {
  return {
    getGpa: (stdId,majorId) => dispatch(PlanOfStudyActionCreators.getGpa(stdId,majorId)),
    getMajor: (major) => dispatch(PlanOfStudyActionCreators.getMajor(major)),
    getContractSheet: (stdId,majorId) => dispatch(PlanOfStudyActionCreators.getContractSheet(stdId,majorId)),
    getCourseOffer: (Id,Major,CourseType) => dispatch(PlanOfStudyActionCreators.getCourseOffer(Id,Major,CourseType)),
    getProbationStudent: (stdId,majorId) => dispatch(PlanOfStudyActionCreators.getProbationStudent(stdId,majorId)),
    getCourseByCourseOffering: (col,courses) => dispatch(PlanOfStudyActionCreators.getCourseByCourseOffering(col,courses)),
    registerCourses: (selectList,studentId,majorId) => dispatch(PlanOfStudyActionCreators.registerCourses(selectList,studentId,majorId)),
    getPOSInfo: (name,value) => dispatch(PlanOfStudyActionCreators.getPOSInfo(name,value)),
    getCourseOfferSchedulePopup: (id) => dispatch(PlanOfStudyActionCreators.getCourseOfferSchedulePopup(id)),
    getTeacher: (id) => dispatch(PlanOfStudyActionCreators.getTeacher(id)),
    getCreditsAndCost: (courses,cl) => dispatch(PlanOfStudyActionCreators.getCreditsAndCost(courses,cl)),
    getMajorCrdCost: (major) => dispatch(PlanOfStudyActionCreators.getMajorCrdCost(major)),
    
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(PlanOfStudy);
