
import * as actionTypes from '../actions/actions';



const initialState = {
    email: '',
    loading: false,
    oldPassword: '',
    newPassword: '',
    retryNewPassword: '',
    result: '',
    error: ''
    
}




const ResetPasswordR =  (state = initialState, action) => {

    
    
    switch (action.type) {
        case actionTypes.ResetPassword.GET_RP_INFO :
            return { ...state, [action.name] : action.value  }
        
        case actionTypes.ResetPassword.RESET_PASSWORD_START :
            return { ...state,loading: true}

        case actionTypes.ResetPassword.RESET_PASSWORD_END :
            return { ...state,loading: false,result: action.cdata.status} 
            
        case actionTypes.ResetPassword.RESET_PASSWORD_FAIL :
            return { ...state,loading: false,error: action.error}    
            
        case actionTypes.ResetPassword.GET_ONLOADRP_INFO :
            return { ...state,loading: false,email: action.cdata}     

        default:
            return state
    }
}

export default ResetPasswordR;
