
import * as actionTypes from '../actions/actions';



const initialState = {
    email: '',
    loading: false,
    result: '',
    error: ''
    
}




const ForgetPasswordR =  (state = initialState, action) => {

    
    
    switch (action.type) {
        case actionTypes.ForgetPassword.GET_FP_INFO :
            return { ...state, [action.name] : action.value  }
        
        case actionTypes.ForgetPassword.FORGET_PASSWORD_START :
            return { ...state,loading: true}

        case actionTypes.ForgetPassword.FORGET_PASSWORD_END :
            return { ...state,loading: false,result: action.cdata} 
            
        case actionTypes.ForgetPassword.FORGET_PASSWORD_FAIL :
            return { ...state,loading: false,error: action.error}                    

        default:
            return state
    }
}

export default ForgetPasswordR;
