import React from 'react'
import LogedInHeader from '../../components/LogedInHeader/LogedInHeader'
import HomeHeader from '../../components/HomeHeader/HomeHeader'
import classes from './Application.module.css'
import { Button,CircularProgress, Container } from '@material-ui/core'
import BoxWithTitle from '../../components/BoxWithTitle/BoxWithTitle'
import FieldOfStudy from '../../components/ComponentsForApplication/FieldOfStudy/FieldOfStudy'
import Major from '../../components/ComponentsForApplication/Major/Major'
import StudyYear from '../../components/ComponentsForApplication/StudyYear/StudyYear'
import RequiredDocuments from '../../components/ComponentsForApplication/RequiredDocuments/RequiredDocuments'
import Courses from '../../components/ComponentsForApplication/Courses/Courses'
import ContractSheet from '../../components/ComponentsForApplication/ContractSheet/ContractSheet'
import Footer from '../../components/Footer/Footer'
import { connect } from "react-redux";
import * as ApplyApplicationActionCreators from "../../store/actionsCreators/ApplyApplicationAction";
import { Redirect } from 'react-router';
import { useEffect,useState } from "react";
import Backdrop from '@material-ui/core/Backdrop';
import { makeStyles } from '@material-ui/core/styles';


const useStyles = makeStyles((theme) => ({
    backdrop: {
      zIndex: theme.zIndex.drawer + 1,
      color: '#fff',
    },
  }));



const Application = ({getInfo,programs,getPrograms,selectedProgram,getCourses,getMajors,majorType,majors,majorsMaster,majorsPHD,selectedMajor,prerequisites,getPrerequisites,studyYears,totalCrd,crdPerYear,getStudyYears,majorCrd,totalCrdSheet,coreCrd,generalCrd,electiveCrd,getContractSheet,coursesTypeS,coursesList,requiredDocument,docList,docListName,getRequiredDoc,submitApplication,applicant,application,userId,docsResponse,error,submitApplicationDoc,loading}) => {
    const classesMakeStyle = useStyles();

    const [openBackdrop, setOpenBackdrop] = React.useState(false);


    if( localStorage.getItem("applicant") !== 'nan' && localStorage.getItem("application") !== null){
        return <Redirect to='/ApplicationPending'/>
    }

    useEffect(()=>{
        window.scrollTo(0,0);
    },[])
    
    useEffect(() => {
        if(docsResponse === 'Success'){
        setOpenBackdrop(true);
        }
        
      }, [docsResponse]);


    useEffect(() => {
        
        getPrograms();
        
      }, []);
    
      useEffect(()=>{
        
          if(selectedProgram !== ''){
          getMajors(selectedProgram,'BSC');
          }
      },[selectedProgram]);

      useEffect(()=>{
        
          if(selectedMajor !== ''){
            getPrerequisites(selectedMajor);
            getStudyYears(selectedMajor);
            getContractSheet(selectedMajor);
            getRequiredDoc(selectedMajor);
            
          }
      },[selectedMajor]);

      useEffect(()=>{
        
        if(coursesTypeS !== ''){
        getCourses(selectedMajor,coursesTypeS);
        }
    },[coursesTypeS]);

    useEffect(()=>{
        
        if(application !== '' && applicant !== '' && userId !== ''){
            
            submitApplicationDoc(docList,docListName,application);
        }
    },[application,applicant,userId]);

      const getInfoApp = (name,value)=>{
          getInfo(name,value);
      }

      const submitAppApplication = (userId,majorId) =>{
          submitApplication(localStorage.getItem('userId'),selectedMajor);
      }


      const handleShowInfoCourse = (courseId,courseTitle,courseCredit,courseDescription) =>{
        setOpenBackdrop(true);
        

    }

    const handleClose = () =>{
        setOpenBackdrop(false);
        return <Redirect to='/Login'/>
    }

    return (
        <>

        <Backdrop className={classesMakeStyle.backdrop} open={openBackdrop} onClick={handleClose}>
                <div style={{ color: '#fff' }}>
                    {
                        
                        <div className={classes.textBoxApplication}>
                            Dear student, Your application has been successfuly registered
                         </div>

                    }
                </div>    
        </Backdrop>

            <HomeHeader />
            <LogedInHeader />
            <div className={classes.Application}>
                <Container>
                    <BoxWithTitle title='welcome to nuversity'>
                            <div className={classes.FieldStudy}>
                                <h1>choose your field of study</h1>
                                {
                                    programs ? <FieldOfStudy programsList={programs} selectedProgram={selectedProgram} getInfoSP={getInfoApp} /> : <></>
                                }
                            </div>
                            
                            
                            
                            <div className={classes.Major}>
                                <h1>choose your Major</h1>
                                {
                                  <Major majorType={majorType} majors={majors} majorsMaster={majorsMaster} majorsPHD={majorsPHD} getInfoSP={getInfoApp} selectedMajor={selectedMajor} prerequisites={prerequisites}/> 
                                }
                            </div>
                            <div className={classes.StudyYears}>

                                <StudyYear StudyYears={studyYears} totalCrd={totalCrd} crdPerYear={crdPerYear}/>

                            </div>
                            <div className={classes.ContractSheet}>
                                {
                                totalCrdSheet ?  <ContractSheet majorCrd={majorCrd} coreCrd={coreCrd} electiveCrd={electiveCrd} generalCrd={generalCrd} totalCrd={totalCrdSheet}/> : <></>
                                }
                                </div>
                            <div className={classes.Courses}>
                                <Courses coursesTypeS={coursesTypeS} coursesList={coursesList} getInfoApp={getInfoApp}/>
                            </div>
                            <div className={classes.RequiredDocuments}>
                               {
                                requiredDocument ? <RequiredDocuments requiredDocument={requiredDocument} docList={docList} docListName={docListName}/> : <></>
                               }
                            </div>
                            <div className={classes.SubmitApp}>

                            <Button
                                variant="contained"
                                color="primary"
                                size="large"
                                fullWidth
                                onClick={submitAppApplication}
                            >
                                 { 
                                    loading ? <CircularProgress style={{color:'#fff'}} size={35} /> : <h2>Submit you application</h2>
                            }
                                
                            </Button>
                            </div>
                    </BoxWithTitle>
                </Container>
            </div>
            <Footer />
        </>
    )
}

const mapStateToProps = (state) => {
    return {
     programs: state.ApplyApplicationR.programs,
     selectedProgram: state.ApplyApplicationR.selectedProgram,
     majorType: state.ApplyApplicationR.majorType,
     majors: state.ApplyApplicationR.majors,
     majorsMaster: state.ApplyApplicationR.majorsMaster,
     majorsPHD: state.ApplyApplicationR.majorsPHD,
     prerequisites: state.ApplyApplicationR.prerequisites,
     selectedMajor: state.ApplyApplicationR.selectedMajor,
     studyYears: state.ApplyApplicationR.studyYears,
     totalCrd: state.ApplyApplicationR.totalCrd,
     crdPerYear: state.ApplyApplicationR.crdPerYear,
     totalCrdSheet: state.ApplyApplicationR.totalCrdSheet,
     majorCrd: state.ApplyApplicationR.majorCrd,
     generalCrd: state.ApplyApplicationR.generalCrd,
     coreCrd: state.ApplyApplicationR.coreCrd,
     electiveCrd: state.ApplyApplicationR.electiveCrd,
     coursesTypeS: state.ApplyApplicationR.coursesTypeS,
     coursesList: state.ApplyApplicationR.coursesList,
     requiredDocument: state.ApplyApplicationR.requiredDocument,
     docList: state.ApplyApplicationR.docList,
     docListName: state.ApplyApplicationR.docListName,
     application: state.ApplyApplicationR.application,
     applicant: state.ApplyApplicationR.applicant,
     userId: state.ApplyApplicationR.userId,
     error: state.ApplyApplicationR.error,
     docsResponse: state.ApplyApplicationR.docsResponse,
     loading: state.ApplyApplicationR.loading
    };
  };
  

  const mapDispatchToProps = (dispatch) => {
    return {
        getPrograms: () => dispatch(ApplyApplicationActionCreators.getPrograms()),
        getMajors: (pgm,type) => dispatch(ApplyApplicationActionCreators.getMajors(pgm,type)),
        getInfo: (name,value) => dispatch(ApplyApplicationActionCreators.getInfo(name,value)),
        getPrerequisites: (majorId) => dispatch(ApplyApplicationActionCreators.getPrerequisites(majorId)),
        getStudyYears: (majorId) => dispatch(ApplyApplicationActionCreators.getStudyYears(majorId)),
        getContractSheet: (majorId) => dispatch(ApplyApplicationActionCreators.getContractSheet(majorId)),
        getCourses: (majorId,ctype) => dispatch(ApplyApplicationActionCreators.getCourses(majorId,ctype)),
        getRequiredDoc: (majorId) => dispatch(ApplyApplicationActionCreators.getRequiredDoc(majorId)),
        submitApplication: (userId , majorId) => dispatch(ApplyApplicationActionCreators.submitApplication(userId , majorId)),
        submitApplicationDoc :(docs,docsName,application) => dispatch(ApplyApplicationActionCreators.submitApplicationDoc(docs,docsName,application)),
    };
}

    //   dropCourses: (courses,studentId,majorId) => dispatch(CurrentSemesterActionCreators.dropCourses(courses,studentId,majorId))
    
  
export default connect(mapStateToProps, mapDispatchToProps)(Application)
