import * as actionTypes from '../actions/actions';

const initialState = {
    studentInfo: '',
    nameOnCard: '',
    cardNumber: '',
    expiryDate: '',
    cvv: '',
    error: '',
    amount: '',
    loading: false,
    result: ''
}




const PaymentR =  (state = initialState, action) => {

    

    switch (action.type) {
        case actionTypes.Payment.GET_PAYMENT_INFO :
            return { ...state, [action.name] : action.value  }
        
        case actionTypes.Payment.PAYMENT_START :
            return { ...state,loading: true}

        case actionTypes.Payment.PAYMENT_END :
            return { ...state,loading: false,result: action.payData} 
            
        case actionTypes.Payment.PAYMENT_FAIL :
            return { ...state,loading: false,error: action.error}              
        
        case actionTypes.Payment.PAYMENT_FILL_INFO :
            return { ...state,studentInfo: action.studentInfo}    

        default:
            return state
    }
}

export default PaymentR;
